.live-schedule-table {
  .filter-panel {
    margin-bottom: 0;
  }

  .live {
    &-wrapper {
      padding-left: 0;
    }

    &-content-wrap {
      max-height: none;
    }

    &-content-item {
      align-items: center;
      flex-wrap: wrap;
      cursor: unset;

      @include media-breakpoint-up(lg) {
        &:hover:not(.avoid-click) {
          cursor: pointer;

          .live-content-item__content {
            color: $color-pink-dark;
          }
        }
      }

      &__info {
        max-width: 300px;
        width: 100%;

        @include media-breakpoint-down(md) {
          max-width: 225px;
        }

        @include media-breakpoint-down(sm) {
          max-width: unset;
          width: 75%;
        }
      }

      &__description {
        margin-left: 25px;

        @include media-breakpoint-down(md) {
          margin-left: 10px;

          .context-1 {
            span {
              display: inline-block;
            }
          }
        }
      }

      &__content {
        position: relative;
        width: calc(50% - 80px);
        margin-top: 5px;
        transition: all 0.5s ease-in-out;

        @include media-breakpoint-down(lg) {
          padding: 0 3%;
        }

        @include media-breakpoint-down(sm) {
          display: none;
        }

        .description-paragraph {
          line-height: 130%;
          font-size: 16px;

          @include media-breakpoint-up(xl) {
            span:last-child {
              white-space: normal !important;
            }
          }
        }

        .view-more {
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          background: rgba(255,255,255,.85);
          border: none;

          @include media-breakpoint-up(xl) {
            display: none;
          }
        }

        .inner {
          span {
            display: block;
            height: 1px;
            width: 32px;
            background: #000;
            margin-top: 3px;
          }
        }
      }

      .opened-description {
        span:last-child {
          white-space: normal !important;
        }
      }

      &__description-watch {
        display: flex;
        align-items: center;
        justify-content: center;
        @include initBubbleSize(80px, 80px);
        padding: 10px;
        background: $bg-pink;
        border-radius: 50%;
        overflow: hidden;
        text-align: center;

        @include media-breakpoint-down(sm) {
          @include initSize(auto, auto);
          padding: 0;
          background: transparent;
          border-radius: 0;
          width: 100%;
          height: 40px;
          background: $bg-pink;
          border-radius: 100px;
          margin-top: 12px;
        }

        &:hover {
          animation-play-state: running;
        }

        &.no-cta {
          animation: none;
          background: none;
        }

        &.coming-soon {
          animation: none;
          background-color: $bg-grey-4;

          &:hover {
            transform: none;
          }

          a {
            cursor: unset;
          }
        }

        a {
          text-transform: capitalize;
          color: $white;
          font-size: 15px;
          line-height: 110%;

        }
      }

      &__schedule {
        @include media-breakpoint-up(md) {
          width: 100px;
          margin-bottom: 15px;
        }

        &-time {
          font-family: $font-header-regular;
        }

        .btn-transparent {
          text-align: left;
        }

        .added {
          .atcb-link {
            color: $color-green-2;
          }
        }

        .addtocalendar.active {
          .atcb-list {
            display: block;
            visibility: visible;
          }
        }

        .atcb {
          &-link {
            text-align: left;
            font-size: 10px;
            font-family: $font-context-regular;
            font-style: normal;
            font-weight: 400;
            text-transform: uppercase;

            @include media-breakpoint-down(sm) {
              font-size: 8px;
            }
          }

          &-list {
            display: none;
            width: 150px;
            padding: 8px;
            border: 1px solid $border-grey-1;

            @include media-breakpoint-down(sm) {
              right: 0;
              left: auto;
            }
          }

          &-item {
            position: relative;
            margin-bottom: 5px;

            &:last-child {
              margin-bottom: 0;
            }

            &-link {
              display: inline-block;
              color: $color-black-1;
              text-transform: uppercase;
              font-size: 12px;

              &:hover {
                color: $color-pink-dark;
              }
            }
          }

          &-checkmark {
            display: inline-block;
            margin-left: 3px;
            margin-right: 5px;

            @include media-breakpoint-down(sm) {
              margin-right: 3px;
            }

            &:after {
              content: '';
              display: block;
              width: 5px;
              height: 10px;
              border: solid $color-green-2;
              border-width: 0 2px 2px 0;
              transform: rotate(45deg);

              @include media-breakpoint-down(sm) {
                width: 4px;
                height: 7px;
              }
            }

            & + .atcb-item-link {
              color: $color-green-2;
            }
          }
        }
      }
    }
  }

  .additional-tags-box .tag {
    color: #fff;
    padding: 5px 10px 5px 10px;
    background-color: #df6b3c;
    border-radius: 60px;
    font-size: 20px;
    margin-right: 5px;
    font-weight: bold;
  }

  .additional-tags-box .tag a{
    margin-left: 13px;
    position: relative;
    top: 1px;
  }
}

.live-error-msg {
  color: $color-pink-dark;
}
