.dailyco-player-page {
  .c-container {
    /*width: 50%;*/
    margin-top: 150px;
  }

  .confirm-wrapper {
    color: #22444e;
    letter-spacing: 1px;

  }

  .row-c-title {
    margin-bottom: 45px;
  }

  .c-title {
    letter-spacing: 2px;
    font-size: 30px;
  }

  .c-bold {
    font-weight: bold;
    font-size: 25px;
  }

  .c-text {
    font-size: 20px;
  }

  .row-c-text {
    margin-bottom: 150px;
  }

  .confirm-wrapper .btns {
    border-radius: 0px;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: bold;
    width: 170px;
    height: 55px;
    margin: 10px 20px 10px 20px;
  }

  .btn-pole {
    color: #487480;
    border: #487480 solid 1px;
    width: 200px;
    height: 60px;
  }

  .btn-pole:hover {
    color: #487480cf;
    background-color: #e2e6ea47;
    border-color: #487480cf;
  }

  .btn-filled {
    color: white !important;
    border: #487480 solid 1px;
    background-color: #487480;
  }

  .btn-filled:hover {
    color: #e2e6ea47;
    background-color: #487480c7;
    border-color: #487480c7;
  }

  .btn-none {
    color: #487480;
    height: 60px;
    width: 200px;
    padding-left: 0;
    text-align: left;
    margin-left: 2px;
  }

  .btn-none:hover {
    color: #487480cf;
  }
}
