$min-size: 768px;
$max-size: 1058px;

header {
  max-width: $max-width;
  margin: 0 auto;
  height: auto;
  transition: height ease-in-out 0.5s;
}

.header-main {
  position: relative;
  width: 100%;
  background: $bg-white-1;
  z-index: 201;

  &[data-onboarding-tabs] {
    .menu-item {
      ul {
        display: none;
      }
    }
  }

  &.header-fixed {
    position: fixed;
  }

  &-desktop {
    padding-top: 50px;
    padding-bottom: 50px;
    transition: padding ease-in-out 0.5s;

    .menu-item {
      margin-left: 25px;
    }

    @include media-breakpoint-down(sm) {
      display: none;
    }

    &.sticky-header {
      padding-top: 20px;
      padding-bottom: 20px;
      @media (min-width: $min-size) and (max-width: $max-size) {
        padding-bottom: 64px;
      }

      .navbar-brand__wrapper {
        width: 70%;
      }
    }

    .global-search-icon {
      display: inline-flex;
      vertical-align: middle;
      margin: 0 10px;

      &:hover {
        cursor: pointer;
      }

      .fa-search {
        font-size: 36px;
        color: $color-pink-dark;
      }
    }
  }

  &-mobile {
    display: none;
    padding-top: 20px;
    padding-bottom: 20px;

    @include media-breakpoint-down(sm) {
      display: block;
    }

    .global-search-icon {
      .fa-search {
        color: $color-pink-dark;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    z-index: 201;
  }

  &__wrapper {
    .container-fluid {
      @include media-breakpoint-down(sm) {
        padding-left: 20px;
        padding-right: 20px;
      }
    }

    @include media-breakpoint-down(sm) {
      margin-left: 0;
    }
  }

  .navbar {
    padding: 0;

    &-brand {
      padding: 0;
      margin: 0;

      &__wrapper {
        display: block;
        width: 100%;
        transition: all 0.5s ease-in-out;
      }
    }

    &-avatar {
      position: relative;
      display: inline-flex;
      @include initSize(49px, 49px);
      border-radius: 50%;
      margin-left: 10px;
      overflow: hidden;
      vertical-align: middle;
      transition: box-shadow ease-in-out 0.5s;

      @include media-breakpoint-down(sm) {
        margin-left: 0;
        @include initSize(40px, 40px);
      }

      &.active {
        box-shadow: 0px 0px 0px 6px $border-pink-1;
      }
    }

    &-list {
      padding: 0;

      &.active {
        visibility: visible;
        opacity: 1;
      }

      &__wrapper {
        display: inline-flex;
        padding: 0;
        vertical-align: middle;
      }
    }

    .action-list {
      top: calc(100% + 20px);
      right: 0;
    }
  }

  .menu-item {
    display: inline-block;
    border-radius: 20px;
    font-size: 16px;
    font-family: $font-header-regular;
    text-transform: uppercase;
    overflow: hidden;

    a {
      display: inline-block;
      background: transparent;
      padding: 4px 20px 2px;
      color: $color-gray;
      text-decoration: none;
      letter-spacing: 1px;
      transition: all 0.3s ease-in-out;
    }

    &.completed {
      &:after {
        content: "";
        background-color: transparent;
        position: absolute;
        right: 10px;
        top: 8px;
        width: 6px;
        border-bottom: 1px solid $bg-pink;
        height: 11px;
        border-right: 1px solid $bg-pink;
        @include transform(rotate(45deg));
      }

      a {
        color: $color-pink-dark;
      }
    }

    &.active {
      a {
        background: $bg-pink;
        color: $color-white;
      }
    }

    @include media-breakpoint-up(sm) {
      &:hover {
        a {
          background: $bg-pink;
          color: $color-white;
        }
      }
    }
  }

  .overlay {
    &-search {
      width: 100%;
      position: fixed;
      z-index: 1;
      top: 0;
      left: 0;
      background-color: rgba(214, 213, 211, 0.9);
      visibility: hidden;
      opacity: 0;
      transition: visibility 0.5s ease-in-out, opacity 0.5s ease-in-out;

      &.active {
        visibility: visible;
        opacity: 1;
      }

      .close-global-search {
        top: 50%;
        right: 10px;
        width: 24px;
        height: 24px;
        position: absolute;
        overflow: hidden;
        background: transparent;
        border: none;
        border-radius: unset;
        outline: none;
        @include transform(translateY(-50%));

        &:hover {
          cursor: pointer;
        }

        &::before,
        &::after {
          content: "";
          position: absolute;
          height: 2px;
          width: 100%;
          top: 50%;
          left: 0;
          margin-top: -1px;
        }

        &::before {
          @include transform(rotate(45deg));
          background-color: $color-white;
        }

        &::after {
          @include transform(rotate(-45deg));
          background-color: $color-white;
        }
      }
    }

    &-content {
      display: inline-block;
      position: absolute;
      top: 50%;
      right: 5%;
      width: 100%;
      transform: translateY(-50%);
      text-align: right;

      @include media-breakpoint-down(sm) {
        right: 15%;
      }

      input[type="text"] {
        padding: 15px;
        font-size: 17px;
        border: none;
        width: 30%;

        @include media-breakpoint-down(sm) {
          width: 70%;
        }

        &:hover {
          background: $bg-grey-2;
        }
      }

      button {
        float: right;
        width: 50px;
        padding: 15px;
        font-size: 17px;
        border: none;
        cursor: pointer;

        &:hover {
          background: $bg-grey-6;
        }
      }
    }
  }
}

.header-main-mobile {
  .navbar {
    justify-content: center;

    .logged-in & {
      justify-content: space-between;
    }

    &-brand {
      &__wrapper {
        width: 70%;
        margin: auto;
      }
    }

    &-toggle {
      position: absolute;
      top: 50%;
      left: 0;
      padding: 0;
      margin: 0;
      background-color: transparent;
      background-image: none;
      border: 1px solid transparent;
      border-radius: 4px;
      transform: translateY(-50%);

      &.active {
        .icon-bar {
          background: $bg-pink;

          &:nth-of-type(1) {
            top: 6px;
            transform: rotate(45deg);
          }

          &:nth-of-type(2) {
            background-color: transparent;
          }

          &:nth-of-type(3) {
            top: -6px;
            transform: rotate(-45deg);
          }
        }
      }

      .logged-in & {
        position: unset;
        transform: unset;
      }

      .icon-bar {
        display: block;
        position: relative;
        width: 24px;
        height: 2px;
        border-radius: 1px;
        background: $bg-black-1;
        transition: all 0.5s ease-in-out;

        &:not(:first-child) {
          margin-top: 4px;
        }

        &:nth-of-type(2) {
          top: 1px;
        }

        &:nth-of-type(3) {
          top: 2px;
        }
      }
    }

    &-list {
      position: absolute;
      top: 100%;
      left: -10px;
      width: calc(100% + 20px);
      margin-top: 10px;
      border-radius: 10px;
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.4);
      opacity: 0;
      visibility: hidden;
      overflow: hidden;
      transition: visibility 0.5s ease-in-out, opacity 0.5s ease-in-out;

      &__wrapper {
        width: 100%;
        padding: 0 20px;
        background: $bg-tan-light;
        flex-wrap: wrap;
      }
    }
  }

  .menu-item {
    position: relative;
    width: 100%;
    text-transform: uppercase;
    overflow: visible;

    &:not(:first-child) {
      &:before {
        content: "";
        position: absolute;
        left: 50%;
        top: 0;
        width: 95%;
        height: 1px;
        transform: translateX(-50%);
        background: $bg-grey;
      }
    }

    a {
      padding: 15px 0;
      color: $color-black-1;
    }

    &.active {
      a {
        background: transparent;
        color: $color-pink-dark;
      }
    }

    &:hover {
      a {
        color: $color-pink-dark;
      }
    }
  }
}

.on-boarding {
  .header-main-desktop {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .header-main-mobile {
    .navbar-list {
      overflow: unset;
    }
  }

  .menu-item {
    position: relative;
    margin: 0 5px;

    &.disabled {
      pointer-events: none;
    }
  }

  @include media-breakpoint-down(md) {
    .navbar {
      flex-wrap: nowrap;
    }
  }

  @include media-breakpoint-down(sm) {
    .navbar {
      justify-content: space-between;
    }

    .navbar-brand__wrapper {
      margin-left: 0;
    }

    .navbar-list {
      position: relative;
      left: 0;
      margin-top: 0;
      opacity: 1;
      visibility: visible;
      width: auto;
      box-shadow: unset;

      &__wrapper {
        padding: 0;
      }
    }

    .menu-item {
      text-transform: uppercase;
      margin: 0;

      &:not(.active) {
        display: none;
      }

      &:not(:first-child):before {
        content: none;
      }

      &.active {
        a {
          background: $bg-pink;
          color: $color-white;
          padding: 4px 20px 2px;
          border-radius: 20px;
        }
      }
    }
  }
}

@media (min-width: $min-size) and (max-width: $max-size) {
  .on-boarding .header-main-desktop.sticky-header {
    padding-bottom: 64px;
  }
}

.logged-in {
  .header-main-desktop {
    padding-top: 20px;
    padding-bottom: 20px;
    @media (min-width: $min-size) and (max-width: $max-size) {
      padding-bottom: 64px;
    }
  }
}

@include media-breakpoint-down(md) {
  body:not(.logged-in) {
    .header-main-desktop {
      .navbar-brand {
        padding-bottom: 10px;
      }
    }
  }
}

/* Bugfix on scaling on resolution (>767px and <993px) in header's icons and navigation menu */

@media (min-width: $min-size) and (max-width: $max-size) {
  .header-main {
    // height: 146px;
  }

  .navbar-list__wrapper {
    position: absolute;
    display: flex;
    width: 100%;
    left: 0;
    top: 75px;
    margin: 0 auto;
    justify-content: space-around;
  }

  .greeting {
    position: relative;
    margin-bottom: 12px;
    margin-top: 60px;
  }
} /* End bugfix */
