.redesigned-wrapper:not(.header-deprecated) {
  header {
    position: sticky;
    top: 0;
    z-index: 999;
  }

  .main-header-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
  }

  .top-header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    position: relative;
    padding: 0 32px;
    
    background-color: $neutralSurface;
    box-shadow: $headerShadow;

    @media (max-width: $lg) {
      height: 64px;
      padding: 0 16px;
    }

    
  }
  
  .dropdown-secondary-menu {
    display: none;
    width: 100%;

    .secondary-header-menu {
      display: flex;
      justify-content: space-between;
  
      width: 100%;
      background-color: $neutralSurface;
      box-shadow: $headerShadow;

      &:not(:last-child) {
        border-bottom: 1px solid $neutralLight;
      }
  
      .header-menu__content {
        display: flex;
        // flex-basis: 100%;
        min-width: 50%;
        
        .header-menu__description {       
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          padding: 56px 16px 56px 32px;
          flex: 1 1 33%;
  
          h5 {
            @include fontHeader5Md();
            color: $neutralText;
          }
  
          p {
            padding-top: 16px;
            @include fontText1Md();
            color: $neutralMiddle;
          }
        }
    
        .header-menu__navigation {
          display: flex;
          flex: 2 5 67%;
  
          padding: 56px 0px 56px;
  
          .navigation__button-wrapper {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
  
            padding-left: 64px;
            border-left: 1px solid $neutralBorder;
  
            a {
              @include fontText1Md();
              padding: 6px 20px;
            }
          }
        }
  
      }
      
      .header-menu__image {
        display: flex;
        border-radius: 8px;
        overflow: hidden;
        width: 408px;
        height: 208px;
        min-width: 408px;
        margin: 24px 32px 24px 0px;
  
        // background-image: url('../images/banner-bg-image.png');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
  
        img {
          width: 100%;
          object-fit: cover;
        }
  
        @media (max-width: $md) {
          display: none;
        }
      }
  
      @media (max-width: $lg) {
        .header-menu__content {
          flex-direction: column;
          flex-basis: 50%;
          flex-grow: 2;
  
          .header-menu__description {       
            padding: 48px 32px 16px;
          }
  
          .header-menu__navigation {
            display: flex;
            padding: 0;
            // flex: 2 5 67%;
    
            padding: 0 32px;
    
            .navigation__button-wrapper {
              padding: 24px 0 48px;
              gap: 8px;
              width: 100%;
              border-left: none;
              border-top: 1px solid $neutralBorder;
  
              a {
                padding-left: 0;
              }
            }
          }
        }
  
        .header-menu__image {
          flex-basis: 50%;
          height: 360px !important;
          max-width: 352px;
          min-width: unset !important;
        
        }
      }
  
  
  
  
    }
  }

  // links to At Home page, In Studio, About
  .header-external-links {
    display: flex;
    flex-grow: 1;
    align-items: center;
    
    height: 100%;
    // padding: 0px 0px 0px 32px;

    .nav-item {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 8px;
      height: 100%;
      
      padding: 0px;
      margin: 0 20px;

      background: $neutralSurface;

      text-transform: none;
      // transition: all 0.3s;

      &.enabled .button-circle {
        display: block;
      }

      &.active .button-title {
        @include fontButtonMd()
      }

      .button-circle {
        display: none;
        width: 16px;
        height: 16px;
        margin: 0px;
        background: radial-gradient(50% 50% at 50% 50%, #FF7A00 42.19%, rgba(255, 122, 0, 0) 100%);
      }

      .button-title {
        display: flex;
        align-items: center;

        @include fontText1Md();
        color: $neutralText;
      }

      &:hover {
        // border-bottom: 2px solid $primary;
      
        .button-title {
          color: $primary;
        }
      }
      
      &:active {
        transition: none;
        // border-bottom: 2px solid $primaryAccented;
        .button-title {
          color: $primaryAccented;
        }

      }
    }

    .navbar-toggle {
      padding: 6px 2px;
      // margin: 24px 0 0;
      background-color: transparent;
      background-image: none;
      border: 1px solid transparent;
      border-radius: 4px;
      // transform: translateY(-50%);
      display: none;
  
      @media (max-width: $lg) {
        display: block;
      }
  
      &.active {
        .icon-bar {
          &:nth-of-type(1) {
            top: 6px;
            transform: rotate(45deg);
          }
  
          &:nth-of-type(2) {
            background-color: transparent;
          }
  
          &:nth-of-type(3) {
            top: -6px;
            transform: rotate(-45deg);
          }
        }
      }
  
      .icon-bar {
        display: block;
        position: relative;
        width: 26px;
        height: 2px;
        border-radius: 1px;
        background: $bg-black-1;
        transition: all 0.3s ease-in-out;
  
        &:not(:first-child) {
          margin-top: 4px;
        }
  
        &:nth-of-type(2) {
          top: 1px;
        }
  
        &:nth-of-type(3) {
          top: 2px;
        }
      }
    }

    @media (max-width: $lg) {
      .navbar-toggle {
        display: block;    
      }

      .nav-item {
        display: none;
      }

    }
  }

  // logo section
  .navbar-brand {
    position: absolute;
    // width: 200px;
    height: 44px;
    left: calc(50% - 200px/2);
    top: calc(50% - 44px/2);
    
    padding: 0;

    img {
      height: 44px;    
    }

    .logo-without-text {
      display: none;
      width: 44px;
    }

    @media (max-width: $md) {
      left: calc(50% - 44px/2);

      .full-logo {
        display: none;
      }

      .logo-without-text {
        display: block;
      }
    }

  }

  // auth buttons section (Login, Try for free)
  .header-auth-wrapper {
    display: flex;
    align-items: center;
    // padding: 0px 32px 0px 0px;

    a {
      padding: 16px 40px;
    }

    a.header__user-icon-button {
      padding: 0 0 0 16px;
    }

    span.user-greetings__text {
      @include fontText1Md();
      color: $neutralText;
    }

    img {
      width: 40px;
      height: 40px;
      min-width: 40px;

      border: 1px solid $neutralBorder;
      border-radius: 50%;
    }

    @media (max-width: $md) {
      span.user-greetings__text {
        display: none;
      }

      .header-auth__login-button {
        display: none;
      }

      .header-auth__try-free-button {
        padding: 12px 24px;
      }
    }
  }



}
