.subscribe-new {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: $bg-white-1;
  max-width: 100%;
  align-items: center;

  // Header
  // --------------------------------------------------------------------------------
  > header {
    width: 100%;
    height: auto !important;

    .header-main {
      position: static;
      background: inherit;

      .container-fluid {
        padding-left: 16px;
        padding-right: 16px;
      }

      .header-main-desktop,
      .header-main-mobile {
        padding: 24px 0;

        .navbar-list__wrapper {
          position: static;
          background: inherit;

          .menu-item {
            font-family: $font-context-bold;
            font-size: 20px;
            color: $color-gray-13;
            text-transform: none;
            border-radius: 0;
            margin: 0;

            @include media-breakpoint-down(sm) {
              font-size: 14px;
            }

            &:not(:first-child) {
              @include media-breakpoint-up(md) {
                &::before {
                  content: "";
                  width: 20px;
                  height: 20px;
                  background: url("../images/yw-mini.svg") !important;
                  opacity: 0.5;
                  display: inline-block;
                  vertical-align: middle;
                  margin: 0 16px;
                }
              }
            }

            a {
              padding: 0;
            }
          }

          .menu-item.active {
            a {
              background: inherit;
              color: $color-green-9;

              > span {
                border-radius: 50%;
                width: 20px;
                height: 20px;
                color: $color-white;
                display: inline-flex;
                justify-content: center;
                letter-spacing: 1px;
              }

              > .active {
                background: $color-green-9;
                margin-right: 4px;
              }

              > .prev {
                background: $color-green-8;
                margin-right: 15px;
                position: relative;

                &:after {
                  content: "";
                  display: inline-block;
                  color: $color-green-8;
                  position: absolute;
                  left: 24px;
                }
              }

              > .next {
                background: $color-gray-13;
                margin-left: 15px;

                &:before {
                  content: "";
                  display: inline-block;
                  color: $color-gray-13;
                  position: absolute;
                  right: 24px;
                }
              }
            }

            &:not(:first-child) {
              @include media-breakpoint-up(md) {
                &::before {
                  background: $color-green-8;
                }
              }
            }
          }

          .menu-item.prev {
            a {
              background: inherit;
              color: $color-green-8;
            }
          }

          .next {
            &:not(:first-child) {
              @include media-breakpoint-up(md) {
                &::before {
                  background: $color-gray-13;
                }
              }
            }
          }
        }
      }

      .header-main-mobile {
        .navbar-brand {
          .navbar-brand__wrapper {
            width: 132px;
          }

          img {
            min-width: 132px;
          }
        }

        .menu-item {
          > a {
            letter-spacing: 0;
          }
        }
      }
    }
  }

  // Main section Step 1
  // --------------------------------------------------------------------------------
  > section {
    flex-grow: 1;
    max-width: 1440px;
    display: flex;

    .container-fluid {
      padding: 0;
      border-radius: 8px;
      background: $bg-white-1;
      display: flex;

      .form {
        display: flex;
        margin: 0;

        .btn-new-primary,
        .btn-new-primary-green {
          width: 100%;
          padding: 16px 0;
          background: $color-orange-2;
          border-radius: 100px;
          font-family: $font-1;
          font-size: 24px;
          line-height: 100%;
          color: $color-white;

          &:hover,
          &:focus {
            background: $bg-orange-3;
          }

          &:active {
            background: $bg-orange-4;
          }

          &:disabled {
            background: $color-gray-11;
          }
        }

        .btn-new-secondary,
        .btn-new-secondary-green {
          width: 100%;
          padding: 16px 0;
          background: $bg-white-1;
          border-radius: 100px;
          font-family: $font-1;
          font-size: 20px;
          line-height: 100%;
          color: $color-orange-2;
          border: 2px solid $color-orange-2;

          &:hover,
          &:focus {
            color: $bg-orange-3;
            border: 2px solid $bg-orange-3;
          }

          &:active {
            color: $bg-orange-4;
            border: 2px solid $bg-orange-4;
          }
        }

        .btn-new-primary.disabled {
          background: $color-gray-11;
        }

        .btn-new-primary-green {
          background: $color-green-4;

          &:hover,
          &:focus {
            background: $color-green-5;
          }

          &:active {
            background: $color-green-6;
          }
        }

        .btn-new-secondary-green {
          color: $color-green-9;
          border: 2px solid $color-green-9;

          &:hover,
          &:focus {
            color: $color-green-2;
            border: 2px solid $color-green-2;
          }

          &:active {
            color: $color-green-9;
            border: 2px solid $color-green-9;
          }
        }

        .row {
          margin: 0;

          .col-md-6 {
            padding: 0;
            display: flex;

            &:last-child {
              height: calc(100vh - 92px);

              @include media-breakpoint-down(sm) {
                display: none;
              }
            }

            .img-wrapper {
              border-radius: 0;
              display: flex;
              padding: 48px;
              padding-bottom: 10px;

              img {
                object-fit: cover;
                border-top-right-radius: 8px;
                border-bottom-right-radius: 8px;
              }
            }

            .form-content {
              padding: 48px 48px 48px 0;
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              width: 100%;

              @media (max-width: 1167px) {
                padding: 16px 16px 16px 0;
              }

              @include media-breakpoint-down(sm) {
                padding: 0;
              }

              .form-wrapper {
                flex-grow: 1;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
              }

              .title-wrapper {
                margin-bottom: 0;

                .description-paragraph {
                  //width: 190px;
                  margin-bottom: 8px;
                  font-family: $font-context-bold;
                  font-size: 18px;
                  line-height: 24px;
                  color: $color-green-9;
                  cursor: pointer;
                  width: fit-content;

                  span {
                    padding-right: 16px;
                  }
                }

                h1 {
                  margin-bottom: 8px;
                  font-family: $font-header-extralight;
                  font-size: 40px;
                  line-height: 110%;
                  color: $color-black-6;

                  @media (max-width: 1167px) {
                    font-size: 32px;
                  }
                }

                h3 {
                  font-family: $font-1;
                  font-size: 20px;
                  line-height: 24px;
                  letter-spacing: 1px;
                  text-transform: uppercase;
                  color: $color-black-6;
                  padding-top: 40px;
                  margin-bottom: 0;
                }
              }

              .form-wrapper {
                .row {
                  .col-6 {
                    &:first-child {
                      padding-left: 0;
                    }

                    &:last-child {
                      padding-right: 0;
                    }
                  }
                }

                .form-control {
                  margin-top: 30px;
                  color: $color-black-6;
                  font-family: $font-context-bold;
                  font-size: 20px;
                  line-height: 110%;

                  @media (max-width: 1167px) {
                    margin-top: 14px;
                  }

                  &::placeholder {
                    color: $color-gray-13;
                  }
                }

                .form-control.error {
                  border-color: $color-red;
                }
              }

              .validate-error {
                color: $color-red;
                margin: 0;
                padding-top: 4px;
              }

              .phone-form-wrapper {
                @media (max-width: 1167px) {
                  flex-direction: column;
                }

                .country-code-wrapper {
                  min-width: 160px;
                  padding-top: 0;
                  margin-right: 24px;

                  @media (max-width: 1167px) {
                    width: 100%;
                  }

                  .country-code-chosen-item {
                    > img {
                      height: 22px;
                    }
                  }

                  .country-code-select {
                    top: auto;
                  }
                }

                .phone-number {
                  flex-grow: 1;

                  @media (max-width: 1167px) {
                    width: 100%;
                  }

                  .form-group {
                    width: 100%;
                  }
                }
              }

              .form-group {
                margin-bottom: 0;
              }

              .show-pwd {
                font-family: $font-2;
                font-size: 16px;
                line-height: 18px;
                letter-spacing: 1px;
                text-transform: uppercase;
                color: $color-green-9;

                &:hover,
                &:focus {
                  color: $color-green-2;
                }

                &:active {
                  color: $color-green-9;
                }
              }

              .form-checkbox {
                margin-top: 24px;

                .checkmark {
                  width: 24px;
                  max-width: 24px;
                  width: 100%;
                  height: 24px;
                  border: 2px solid $color-gray-13;

                  &:after {
                    border: solid $color-green-9;
                    border-width: 0 2px 2px 0;
                  }
                }

                span {
                  font-family: $font-2;
                  font-size: 14px;
                  line-height: 110%;
                  color: $color-black-6;

                  a {
                    font-family: $font-context-bold;
                    color: $color-green-9;
                    text-decoration: none;

                    &:hover,
                    &:focus {
                      color: $color-green-2;
                    }

                    &:active {
                      color: $color-green-9;
                    }
                  }
                }

                .step3-checkbox {
                  .checkmark {
                    border: 1px solid $color-gray-13;
                  }

                  span {
                    color: $color-gray-13;
                  }
                }
              }

              .form-footer {
                margin-top: 32px;

                @media (max-width: 1167px) {
                  flex-wrap: wrap;
                }

                .btn-group {
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }
  }

  // Footer
  // --------------------------------------------------------------------------------
  > footer {
    margin: 32px 0 16px;
    align-self: center;
    max-width: 1440px;

    .wrapper-ml {
      margin: 0;

      .footer-link {
        padding: 0;
        font-family: $font-2;
        font-size: 18px;
        line-height: 23px;

        @media (max-width: 1167px) {
          display: flex;
          flex-direction: column;
          align-items: center;
        }

        li {
          color: $color-gray-11;
          text-align: center;

          &:before {
            padding: 0 4px;
            color: $color-gray-12;

            @media (max-width: 1167px) {
              display: block;
              text-align: center;
            }
          }

          a {
            color: $color-green-9;

            &:hover,
            &:focus {
              color: $color-green-2;
            }

            &:active {
              color: $color-green-9;
            }
          }
        }
      }
    }
  }

  // Main section Step 2
  .onboarding-plan,
  .onboarding-thumbnail {
    margin: 8px 0;
    height: auto;

    .onboarding-plan-item,
    .onboarding-thumbnail-radio-item {
      margin: 0;
      padding: 16px 32px 16px 16px;
      border-radius: 8px;
      margin: 8px 0;
      cursor: pointer;

      [type="radio"]:checked,
      [type="radio"]:not(:checked) {
        position: absolute;
        left: -9999px;
      }

      [type="radio"]:checked + div,
      [type="radio"]:not(:checked) + div {
        position: relative;
        padding-left: 24px;
        padding-top: 0;
        cursor: pointer;
        font-family: $font-1;
        font-size: 24px;
        line-height: 110%;
        color: $color-black-5;
      }

      [type="radio"]:checked + div:before,
      [type="radio"]:not(:checked) + div:before {
        content: "";
        position: absolute;
        left: 0;
        top: calc(50% - 12px);
        width: 24px;
        height: 24px;
        border: 1px solid $color-gray-13;
        border-radius: 100%;
        background: $bg-white-1;
      }

      [type="radio"]:checked + div:after,
      [type="radio"]:not(:checked) + div:after {
        content: "";
        width: 12px;
        height: 12px;
        background: $color-green-9;
        position: absolute;
        top: calc(50% - 6px);
        left: 6px;
        border-radius: 100%;
        transition: all 0.2s ease;
      }

      [type="radio"]:not(:checked) + div:after {
        opacity: 0;
        transform: scale(0);
      }

      [type="radio"]:checked + div:after {
        opacity: 1;
        transform: scale(1);
      }

      .onboarding-plan-item-wrapper,
      .onboarding-thumbnail-radio-item-wrapper {
        box-shadow: none;
        height: auto;
        display: flex;
        justify-content: space-between;

        .title-wrapper {
          padding: 0 16px;

          .description-paragraph {
            display: none;
            margin-bottom: 0;
            font-family: $font-header-light;
            font-size: 14px;
            line-height: 110%;
            color: $color-black-6;

            p,
            li {
              font-family: $font-header-light;
              font-size: 14px;
              line-height: 110%;
              color: $color-black-6;
              padding-top: 6px;
            }

            &-note {
              font-family: $font-header-light;
              font-size: 12px;
              line-height: 110%;
              color: $color-gray-13;
              margin-top: 8px;
            }
          }

          h5,
          h6 {
            font-family: $font-1;
            font-size: 24px;
            line-height: 110%;
            color: $color-black-6;
          }

          h5 {
            margin-bottom: 8px;
          }

          h6 {
            font-size: 14px;
            margin-bottom: 0;
            color: $color-gray-13;
          }
        }

        .price-section {
          padding-left: 16px;
          font-family: $font-1;
          font-size: 24px;
          line-height: 110%;
          color: $color-black-6;
          border-left: 1px solid $color-gray-13;
          display: flex;
          flex-direction: column;
          justify-content: center;
          min-width: 110px;

          @media (max-width: 1167px) {
            display: none;
          }

          .price-number {
            text-align: right;

            span {
              font-size: 14px;
            }
          }

          .price-time {
            text-align: right;
            font-size: 14px;
            color: $color-gray-13;
          }
        }
      }

      .onboarding-thumbnail-radio-item-wrapper {
        .title-wrapper {
          h5 {
            margin-bottom: 0;
          }
        }
      }
    }

    .onboarding-thumbnail-radio-item {
      padding: 16px 0;
      width: fit-content;
    }

    .onboarding-plan-item.active {
      background: $bg-green-4;

      .onboarding-plan-item-wrapper {
        .title-wrapper {
          .description-paragraph {
            display: block;
          }

          h6 {
            color: $color-black-6;
          }
        }
      }
    }
  }

  .enter-code {
    margin: 0;
    padding: 16px 8px;
    border-top: 1px solid $color-gray-13;
    border-bottom: 1px solid $color-gray-13;

    .enter-code-item {
      text-align: center;
    }

    a {
      font-family: $font-context-regular;
      color: $color-orange-2 !important;
    }

    .apply-code {
      text-align: left;
      font-family: $font-context-regular;
      color: $color-green-9;

      div {
        span {
          display: block;
          margin-right: 20px;
          text-transform: none;
        }
      }

      .cancel-icon {
        position: absolute;
        top: 50%;
        right: 15px;
        color: $color-green-9;
        font-size: 24px;
        transform: translateY(-50%);
      }
    }
  }

  .gift-plan-desc {
    font-size: 16px;
    color: $color-gray-13;
  }

  .footer-left {
    margin-right: 24px;

    @media (max-width: 1167px) {
      margin-right: 0;
      margin-bottom: 16px;
    }
  }

  .onboarding {
    .onboarding-plan-promotional {
      display: flex;
    }
  }

  // Main section Step 3
  .onboarding-subscription {
    margin: 14px 0 24px;

    .onboarding-subscription-item {
      display: block;
      border: 2px solid $color-green-9;
      border-radius: 8px;
      padding: 8px 16px;

      h5 {
        margin-bottom: 0;
        font-family: $font-2;
        font-size: 18px;
        line-height: 110%;
        color: $color-black-6;

        span {
          font-family: $font-context-bold;
          font-size: 18px;
          line-height: 110%;
          color: $color-green-9;
          padding-bottom: 8px;
        }
      }

      p,
      li {
        font-family: $font-header-light;
        font-size: 14px;
        line-height: 110%;
        color: $color-black-6;
      }
    }
  }

  .wrap-apple {
    background: $bg-green-4;
    border-radius: 16px;
    padding: 32px;

    @media (max-width: 1167px) {
      padding: 16px;
    }

    .col-12 {
      padding: 0;
    }

    .col-4 {
      padding-left: 0;
      padding-right: 24px;

      @media (max-width: 1167px) {
        padding-right: 16px;
      }

      &:last-child {
        padding-right: 0;
      }
    }
  }

  // Personalize
  .onboarding-thumbnail {
    padding-top: 32px;
    display: flex;
    flex-wrap: wrap;

    @media (max-width: 1167px) {
      padding-top: 16px;
    }

    &-item {
      background: $bg-green-4;
      border-radius: 100px;
      padding: 10px 16px;
      color: #016572;
      margin-right: 16px;
      margin-bottom: 16px;
      font-family: $font-1;
      font-size: 24px;
      line-height: 110%;
      cursor: pointer;

      @media (max-width: 1167px) {
        margin-right: 8px;
        margin-bottom: 8px;
        font-size: 20px;
      }

      &:hover,
      &:focus {
        background: $color-green-9;
        color: $color-white;
        box-shadow: none;
      }

      &:active {
      }

      &.unchecked {
        background: $bg-green-4;
        color: #016572;
      }
    }

    &-item.active {
      background: $color-green-9;
      color: $color-white;
    }
  }

  .thumbnail-radio {
    flex-wrap: nowrap;
    flex-direction: column;
  }

  // Well done
  .well-done-content {
    padding: 32px 0 24px;
    margin: -8px;

    &-item {
      border: 2px solid $color-gray-12;
      border-radius: 8px;
      padding: 32px;
      margin: 8px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @media (max-width: 1167px) {
        padding: 24px;
      }

      &:first-child {
        position: relative;
      }

      p {
        font-family: $font-1;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 1px;
        color: $color-black-6;
        margin-bottom: 24px;
      }
    }

    &-notification {
      font-family: $font-1;
      font-size: 16px;
      line-height: 110%;
      color: $color-orange-2;
      background: $bg-white-1;
      border: 2px solid $color-orange-2;
      border-radius: 100px;
      padding: 6px 12px;
      position: absolute;
      top: -16px;
      right: 16px;
    }

    .row {
      flex-wrap: nowrap;

      @media (max-width: 1167px) {
        flex-direction: column;
      }
    }
  }

  .onboarding-wrapper {
    padding: 0;
  }
}
