.gift-card-wrapper {
  label {
    &.error {
      margin-top: 10px;
      color: $bg-red;
    }
  }

  .account-content {
    padding-top: 24px;
    padding-left: 0;
  }

  .choose-gift-header {
    padding: 16px 0;
  }

  .gift-plan-title {
    background-image: url("../images/subscription.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 106px;
    border-radius: 16px;
    margin: 8px 0 4px;
    padding: 16px 32px;
    font-family: $font-1;
    color: $color-white;

    &-header {
      margin-bottom: 8px;
      font-size: 36px;
      line-height: 110%;
    }

    &-description {
      font-size: 24px;
      line-height: 110%;
    }
  }

  [type="radio"]:checked,
  [type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
  }

  [type="radio"]:checked + span,
  [type="radio"]:not(:checked) + span {
    position: relative;
    padding-left: 58px;
    padding-top: 4px;
    cursor: pointer;
    font-family: $font-1;
    font-size: 24px;
    line-height: 110%;
    color: $color-black-5;
  }

  [type="radio"]:checked + span:before,
  [type="radio"]:not(:checked) + span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 34px;
    height: 34px;
    border: 1px solid $border-grey-9;
    border-radius: 100%;
    background: $bg-white-1;
  }

  [type="radio"]:checked + span:after,
  [type="radio"]:not(:checked) + span:after {
    content: "";
    width: 18px;
    height: 18px;
    background: $color-pink-dark;
    position: absolute;
    top: 8px;
    left: 8px;
    border-radius: 100%;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
  }

  [type="radio"]:not(:checked) + span:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  [type="radio"]:checked + span:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  .gift-radio {
    padding: 32px;
    border-radius: 16px;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    background-color: transparent;

    &.checked {
      background-color: #DFEEE6;
    }

    &-plan {
      @include media-breakpoint-down(sm) {
        padding: 16px;
      }
    }

    .price-wrapper {
      font-family: $font-1;
      font-size: 24px;
      line-height: 110%;
      color: $color-black-5;
    }
  }

  .form-group {
    input[type="text"],
    input[type="email"],
    textarea {
      color: $color-black-5;
      padding: 16px 0;

      &::placeholder {
        color: $color-gray-10;
      }

      &:disabled {
        color: $color-gray-9;

        &::placeholder {
          color: $color-gray-9;
        }
      }
    }

    textarea {
      border: 1px solid $border-green-2;
      padding: 18px;
      border-radius: 16px;
      line-height: 110%;
      margin: 16px 0;
      min-height: 224px;
      overflow: hidden;
    }
  }

  .textarea-length-wrapper {
    display: flex;
    justify-content: flex-end;
    font-size: 14px;
    line-height: 110%;
    color: $color-gray-9;
    font-family: $font-1;
  }

  .delivery-time {
    margin-top: 32px;
  }

  .delivery-future-date {
    text-align: center;

    input {
      width: 240px;
    }

    label {
      display: block;
      text-align: left;
    }
  }

  .payment-method-header {
    margin-bottom: 32px;
  }

  .total-payment {
    font-family: $font-1;
    font-size: 32px;
    line-height: 110%;
    color: $color-black-5;
  }

  .card-info {
    border: 1px solid $color-black-5;
    border-radius: 4px;
    display: inline-block;
    height: 28px;
    position: absolute;
    left: 126px;
    top: 29px;
    font-family: $font-2;
    font-size: 16px;
    color: $color-black-5;
    padding: 0 8px;
  }

  .radio-wrapper {
    i {
      left: 53px;
    }
  }

  .preview-wrapper {
    background: $bg-tan;
    border-radius: 16px;
    padding: 32px;
    height: 100%;
    display: flex;
    flex-direction: column;

    &-header {
      align-self: flex-end;
      font-family: $font-header-light;
      font-size: 20px;
      line-height: 110%;
      color: $color-gray-9;
    }

    .preview-main {
      margin-top: 24px;
      background-color: $bg-white-1;
      display: flex;
      flex-direction: column;

      &-logo {
        padding: 32px 0;
        border-bottom: 1px solid $border-grey-10;
        display: flex;
        justify-content: center;
      }

      &-sender {
        padding: 0 32px;
        font-family: $font-header-extralight;
        font-weight: 300;
        font-size: 48px;
        line-height: 110%;
        color: $color-black-1;
        display: none;
        flex-direction: column;
        align-items: center;
        margin-top: 64px;

        &-name {
          overflow-wrap: break-word;
          inline-size: 100%;
          text-align: center;
        }
      }

      &-card {
        padding: 64px 0;
        display: flex;
        justify-content: center;
      }

      &-recipient,
      &-message {
        display: none;
        flex-direction: column;
        align-items: center;
        padding: 0 32px;

        &-text {
          font-family: $font-2;
          font-size: 18px;
          line-height: 23px;
          text-transform: uppercase;
          color: $color-gray-9;
          margin-bottom: 4px;
        }

        &-name {
          overflow-wrap: break-word;
          inline-size: 100%;
          text-align: center;
          font-family: $font-context-light;
          font-size: 18px;
          line-height: 23px;
          color: $color-black-5;
        }
      }

      &-message {
        margin-top: 16px;
      }

      &-bottom {
        &-card-code {
          padding: 56px 32px 0;
          display: flex;
          flex-direction: column;
          align-items: center;

          &-title {
            margin-bottom: 8px;
            font-family: $font-2;
            font-size: 18px;
            line-height: 23px;
            text-transform: uppercase;
            color: $color-gray-9;

            span {
              font-size: 14px;
            }
          }

          &-number {
            padding: 16px 0;
            background-color: $bg-green-3;
            font-family: $font-2;
            font-size: 24px;
            line-height: 30px;
            letter-spacing: 0.05em;
            color: $color-black-5;
            width: 100%;
            text-align: center;
          }
        }

        &-buttons {
          margin-top: 64px;
          display: flex;
          flex-direction: column;
          align-items: center;

          a {
            padding: 16px 48px;
            border-radius: 56px;
            font-family: $font-1;
            font-size: 16px;
            line-height: 18px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: $color-white;
          }

          .sign-up-btn {
            background-color: $bg-pink;
          }

          .login-btn {
            background-color: $bg-green;
          }

          div {
            margin: 16px 0;
            font-family: $font-2;
            font-size: 18px;
            line-height: 23px;
            text-transform: uppercase;
            color: $color-gray-9;
          }
        }

        &-thanks {
          padding: 56px 32px 64px;
          display: none;
          flex-direction: column;
          align-items: center;

          &-title {
            font-family: $font-2;
            font-size: 18px;
            line-height: 23px;
            text-transform: uppercase;
            color: $color-gray-9;
            margin-bottom: 4px;
          }

          &-email {
            font-family: $font-context-light;
            font-size: 18px;
            line-height: 23px;
            text-decoration-line: underline;
            color: $color-green-2;
            overflow-wrap: break-word;
            inline-size: 100%;
            text-align: center;
          }
        }
      }
    }
  }

  .history-tab {
    .history-row {
      padding: 40px 16px;
      justify-content: space-between;
      align-items: center;
      color: $color-gray-10;
      border-bottom: 1px solid $border-grey-10;

      &-waiting {
        color: $color-black-5;
      }

      &-left {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        img {
          width: 130px;
          margin: 0 24px -8px 16px;
        }
      }
    }

    .history-row-item {
      min-width: 120px;
      padding: 8px;

      &-top {
        font-family: $font-1;
        font-size: 28px;
        line-height: 110%;
        margin-bottom: 8px;
      }

      &-bottom {
        font-family: $font-context-light;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.04em;
      }

      &-subscription {
        margin-right: 100px;
      }

      &-price {
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        &-waiting {
          color: $color-green-1;
        }
      }
      &-name, &-email {
        overflow-wrap: break-word;
        inline-size: 270px;
        min-width: 270px;
      }
    }
  }

  .header-wrapper-sent {
    margin-top: 84px;
  }

  @include media-breakpoint-down(lg) {
    .preview-section {
      display: none;
    }
  }
}

/* Bugfix on scaling on resolution (>767px and <993px) in header's icons and navigation menu */
$min-size: 768px;
$max-size: 1058px;

#gift-tab {
  margin-top: 16px;
  @media (min-width: $min-size) and (max-width: $max-size) {
    margin-top: 46px;
  }
}

.modal-gift {
  background: rgba(0, 0, 0, 0.4);

  .modal-header {
    border-bottom: none;
    margin-top: 24px;
    padding: 0;
  }

  .modal-description {
    padding: 24px 0 40px;
    font-family: $font-2;
    font-size: 16px;
    line-height: 20px;
    color: $color-gray-9;
  }

  .modal-card-wrapper {
    background: $bg-tan;
    border-radius: 16px;
    justify-content: space-between;
    align-items: center;
    padding: 16px;

    > .row {
      align-items: center;
    }

    img {
      width: 216px;
      margin-bottom: -8px;
      margin-right: 32px;
    }
  }

  .modal-card-item-title {
    font-family: $font-2;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: $color-gray-9;
  }

  .modal-card-item-name {
    font-family: $font-2;
    font-size: 16px;
    line-height: 20px;
    color: $color-black-5;
    overflow-wrap: break-word;
    inline-size: 270px;
  }

  .modal-card-item-first {
    margin-bottom: 16px;
  }

  .modal-card-price {
    font-family: $font-context-light;
    font-size: 20px;
    line-height: 22px;
    color: $color-black-5;

    span {
      font-family: $font-2;
      font-size: 24px;
      line-height: 28px;
    }
  }

  .modal-card {
    padding: 0 16px;
  }
}
