.account {
  &-wrapper {
    position: relative;

    .avatar-delete-confirm-text {
      text-align: center;
      font-size: 20px;
    }

    .btn-confirm-no {
      width: 120px;
    }

    .notification-comp {
      padding-left: 30px;

      @include media-breakpoint-down(sm) {
        padding-left: 0;
      }
    }

    h5,
    a {
      color: $color-black-2;
    }

    .btn-delete-avatar {
      color: #a9a0a0;
      position: absolute;
      right: 5px;
      top: 0;
    }

    @include media-breakpoint-down(sm) {
      &.wrapper-ml {
        margin: 0;
      }

      .form-wrapper {
        h5 {
          font-size: 18px;
        }
      }
    }
  }

  &-profile {
    a {
      &.form-control {
        border-bottom: 1px solid $border-grey-2 !important;
      }
    }

    .img {
      &-wrapper {
        position: relative;
        max-width: 150px;
        width: 100%;
        height: 150px;

        @include media-breakpoint-down(sm) {
          max-width: 90px;
          height: 90px;
          margin-left: auto;
        }

        img {
          border-radius: 50%;
        }
      }

      &-content {
        &:hover {
          .btn-edit-profile {
            visibility: visible;
            opacity: 1;
          }
        }
      }
    }

    .form {
      &-wrapper {
        padding-top: 60px;
        border-bottom: 1px solid $border-grey-2;
        margin-left: 30px;

        @include media-breakpoint-down(sm) {
          padding-top: 20px;
          margin-left: 0;
        }

        &:first-child {
          padding-bottom: 70px;

          @include media-breakpoint-down(sm) {
            padding-bottom: 20px;
          }
        }

        .center-vertically {
          height: 100%;
          display: flex;
          align-items: center;
        }

        &.no-border {
          border: none;
        }

        &.responsive-border {
          @include media-breakpoint-down(sm) {
            border: none;
          }
        }

        &:not(:first-child) {
          .title-wrapper {
            padding-top: 20px;

            @include media-breakpoint-down(sm) {
              padding-bottom: 20px;
            }
          }
        }
      }

      &-group {
        padding-bottom: 55px;
        margin-bottom: 0;

        @include media-breakpoint-down(sm) {
          padding-bottom: 5px;
        }

        &-wrap {
          border-bottom: 1px solid $border-grey-2;
        }
      }

      &-control {
        width: calc(100% - 60px);
        padding: 24px 15px 24px 0;
        font-family: $font-context-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 28px;
        line-height: 140%;
        color: $color-gray-5;
        border-bottom: none;

        @include media-breakpoint-down(sm) {
          font-size: 18px;
          width: calc(100% - 50px);
        }

        &.text-change {
          color: $color-pink-dark;
        }
      }
    }

    .btn {
      &-small {
        position: absolute;
        text-transform: uppercase;
        cursor: pointer;
        font-family: $font-context-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        border: none;
        background: none;

        @include media-breakpoint-down(sm) {
          font-size: 15px;
          line-height: 18px;
        }
      }

      &-edit-profile {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: $color-white;
        visibility: hidden;
        opacity: 0;
        transition: all 0.5s ease-in-out;

        @include media-breakpoint-down(sm) {
          visibility: visible;
          opacity: 1;
          font-size: 18px;
          line-height: 23px;
          font-weight: bold;
        }
      }

      &-save-detail {
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        color: $color-pink-dark;
      }
    }

    .file {
      &-wrapper {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        overflow: hidden;
      }

      &-input {
        opacity: 0;
        width: 100%;
      }
    }
  }

  &-payment {
    .title-wrapper {
      @include media-breakpoint-down(sm) {
        padding-bottom: 20px;
      }
    }

    .content {
      &-group {
        a {
          color: $color-pink-dark;
        }

        p {
          display: inline-block;
          color: $color-gray-5;
          margin-bottom: 0;

          @include media-breakpoint-down(sm) {
            display: block;
          }
        }
      }
    }

    .payment-history {
      .content-wrapper:not(:last-child) {
        .content-group {
          border: none;
        }
      }
    }
  }

  &-content {
    padding-top: 80px;
    padding-left: 30px;

    @include media-breakpoint-down(sm) {
      padding-top: 0;
      padding-left: 0;

      .form-wrapper {
        padding-top: 20px;
      }
    }

    .title-wrapper {
      padding-top: 24px;

      @include media-breakpoint-down(sm) {
        padding-top: 20px;
      }
    }

    .content {
      &-wrapper {
        padding-bottom: 55px;

        @include media-breakpoint-down(sm) {
          padding-bottom: 5px;
        }
      }

      &-group {
        font-family: $font-context-regular;
        font-style: normal;
        font-weight: normal;
        font-size: 28px;
        line-height: 140%;
        padding: 24px 0;
        border-bottom: 1px solid $border-grey-2;

        @include media-breakpoint-down(sm) {
          font-size: 18px;
          padding: 22px 0;
        }

        &.coupon-redemption-status {
          border-bottom: none;

          p {
            font-family: $font-header-extralight;
            font-weight: 600;
            font-size: 20px;
            @include media-breakpoint-down(xs) {
              font-size: 16px;
            }
          }
        }
      }
    }

    .edit-plan-btn {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      font-size: 18px;
      text-transform: uppercase;
    }

    .plan-notes {
      font-size: 20px;
      color: $color-gray-5;
      @include media-breakpoint-down(xs) {
        font-size: 16px;
      }

      span {
        font-weight: 700;
      }
    }
  }

  &-preferences {
    .header-wrapper {
      padding-bottom: 70px;
      margin-bottom: 0;

      @include media-breakpoint-down(sm) {
        padding-top: 35px;
        padding-bottom: 0;
        font-size: 28px;
      }
    }

    .title-wrapper {
      h5 {
        margin-bottom: 0;
      }
    }

    .content-wrapper {
      a {
        color: $color-gray-5;
      }
    }

    .content-group {
      position: relative;
    }

    .tagsinput-wrapper {
      .ui-menu {
        overflow-y: hidden;
      }

      .tagsinput,
      .tag,
      input {
        cursor: pointer;
      }

      .tag,
      input {
        font-size: 28px;

        @include media-breakpoint-down(md) {
          font-size: 22px;
        }

        @include media-breakpoint-down(sm) {
          font-size: 18px;
        }
      }

      .tagsinput {
        .tag ~ div {
          input {
            width: 0;
            height: 0;
          }
        }

        input {
          opacity: 1;
          padding: 0;
          margin: 0;
        }
      }
    }

    .ui-autocomplete-wrapper {
      ul {
        left: 0 !important;
      }
    }

    .ui-menu-item {
      &.active {
        .ui-menu-item-wrapper {
          color: $color-pink-dark;
        }
      }
    }
  }
}

.tfa-error {
  display: none;
  color: $color-pink-dark;
}

.tfa-request-again {
  padding: 0;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  color: $bg-green;
  border: 0;
  background: transparent;

  &:disabled {
    opacity: .7;
  }

  .countdown {
    text-transform: none;
    color: $color-gray-9;
    font-weight: 300;
  }
}

.tfa-verify-desc {
  color: $color-gray-9;

  a {
    color: $color-gray-9;
    border-bottom: 1px solid $color-gray-9;
  }
}

.tfa-switcher-section {
  .content-group {
    border-bottom: none;
    padding-bottom: 0;
    display: flex;
    align-items: center;
  }

  .d-inline-block {
    font-family: $font-context-bold;
    font-size: 28px;
    line-height: 110%;
    color: $color-gray-9;

    @include media-breakpoint-down(sm) {
      font-size: 20px;
      padding-bottom: 22px;
    }
  }

  input[type="text"] {
    &::placeholder {
      color: $color-gray-9;
      font-family: $font-context-bold;
    }
  }

  .checkbox-switcher-wrapper {
    margin-right: 16px;
  }
}

.checkbox-switcher-wrapper {
  position: relative;
  display: inline-block;
  width: 52px;
  height: 32px;
  margin-bottom: 0;

  input {
    opacity: 0;
    width: 0;
    height: 0;

    &:checked {
      + .checkbox-switcher {
        background-color: $bg-white-1;

        &::before {
          background-color: $bg-pink;
          transform: translateX(22px);
        }
      }
    }
  }

  .checkbox-switcher {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: transparent;
    transition: 0.4s;
    border: 1px solid $border-grey-9;
    border-radius: 34px;

    &.checked {
      background-color: $bg-white-1;

      &::before {
        background-color: $bg-pink;
        transform: translateX(22px);
      }
    }

    &::before {
      position: absolute;
      content: "";
      height: 20px;
      width: 20px;
      left: 4px;
      bottom: 5px;
      background-color: $color-gray-9;
      transition: transform 0.4s;
      border-radius: 50%;
    }
  }
}

.tab-pane {
  .activity-history {
    border-bottom: none;

    .activity-history-item {
      font-family: $font-context-bold;
      font-size: 28px;
      line-height: 110%;
      color: $color-gray-9;
      justify-content: space-between;
      padding-bottom: 54px;

      @include media-breakpoint-down(sm) {
        font-size: 20px;
      }

      p {
        margin: 0;
        word-break: break-word;
      }

      > .row {
        margin: 0;
        padding: 0;
        flex-wrap: nowrap;

        div {
          &:first-child {
            margin-right: 64px;

            @include media-breakpoint-down(lg) {
              margin-right: 16px;
            }
          }
        }
      }

      .activity-history-item-el {
        p {
          &:last-child {
            font-size: 18px;
            line-height: 110%;
            padding-top: 8px;

            @include media-breakpoint-down(sm) {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}

.show-more-btn-wrapper {
  display: flex;
  justify-content: center;

  .show-more-btn {
    background: transparent;
    border: none;
    padding: 0;
    font-family: $font-2;
    font-size: 18px;
    line-height: 23px;
    text-transform: uppercase;
    color: $color-gray;
  }
}

.modal-tfa {
  .form-group {
    .form-control {
      padding: 50px 0;
    }
  }

  .form-group {
    select.form-control {
      color: $color-green-1;
      appearance: auto;
    }

    input[type="text"].form-control {
      color: $color-gray-9;
    }
  }

  .modal-footer {
    justify-content: flex-end;
    padding: 54px 0 24px;

    .btn {
      text-transform: uppercase;
      font-size: 20px;
    }
  }
}

.account-content {
  input[type="password"] {
    color: $color-gray-9;
  }
}

.modal-tfa {
  .country-code-wrapper {
    position: relative;
    cursor: pointer;

    .country-code-select {
      display: none;
      position: absolute;
      width: fit-content;
      background: $bg-tan-light;
      box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
      border-radius: 16px;
      z-index: 1000;
      top: 100px;
      max-height: 410px;
      overflow-y: auto;
      overflow-x: hidden;
      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }

      @include media-breakpoint-down(sm) {
        top: 84px;
      }

      > li {
        display: flex;
        padding: 24px;
        border-bottom: 1px solid $border-grey-10;
        justify-content: space-between;
        align-items: center;
        font-size: 28px;

        @include media-breakpoint-down(lg) {
          font-size: 22px;
        }
      }

      .country-name-wrapper {
        display: flex;
        align-items: center;

        .country-name {
          font-family: $font-1;
          line-height: 110%;
          color: $color-black-5;
          padding: 0 16px;
          word-break: break-word;

          @include media-breakpoint-down(lg) {
            padding: 0 10px;
          }
        }
      }

      .country-code {
        font-family: $font-1;
        line-height: 110%;
        color: $color-gray-9;
      }
    }
  }

  .country-code-chosen-item {
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;

    .country-name-chosen, .country-code-chosen {
      color: $color-green-1;
    }
  }

  .country-code-item-search {
    display: none;
  }
}
