.hero-block{
  position: relative;

  .slick-slider
  {
    &.slick-vertical {
      .slick-slide {
        margin: 0 1px;
        outline: none;
        border: 0;

        &.slide-prev, &.slide-next {
          opacity: 0;
        }

        &.slick-current.slick-active {
          opacity: 1;
        }
      }
    }

    .slick-list, .slick-track {
      @include transform(unset);
    }
  }

  .slider {
    overflow: hidden;

    a:hover {
      color: $color-white;
    }
  }

  .slider-item {
    position: relative;
    max-width: $max-width;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 15px;

    &:not(:first-child):not(.slick-slide) {
      opacity: 0;
    }

    .slider-caption {
      position: absolute;
      bottom: 5%;
      left: 0;
      width: 100%;
      padding-left: calc(40 / 1118 * 100%);

      @include media-breakpoint-down(sm) {
        h2 {
          margin-bottom: 5px;
        }
      }

      h5 {
        @include media-breakpoint-down(sm) {
          margin-bottom: 0;
        }

        span {
          display: inline-flex;
          vertical-align: bottom;
          align-items: center;

          &:before {
            content: "\A";
            display: inline-flex;
            @include initSize(8px, 8px);
            border-radius: 50%;
            background: $bg-white-1;
            align-items: center;
            margin: 0 10px;

            @include media-breakpoint-down(sm) {
              @include initSize(5px, 5px);
              margin: 0 5px;
            }
          }
        }
      }
    }

    img {
      width: 100%;
      border-radius: 15px;
    }

    .slider-description {
      text-align: left;

      p {
        width: 75%;

        @include media-breakpoint-down(md) {
          width: 70%;
        }

        @include media-breakpoint-down(sm) {
          width: 95%;
          margin-bottom: 0;
        }
      }
    }

    .description-paragraph {
      color: $color-white;
    }

    .upcoming-cta-result {
      display: none;
      padding: 10px;
      color: $bg-red;
      background: $color-white;
      border: solid 1px #ffffff;
      border-radius: 10px;
    }
  }

  .slick-dots {
    width: auto;
    display: block;
    right: 5%;
    top: 50%;
    bottom: auto;
    margin: 0;
    transform: translateY(-50%);

    li {
      display: block;
      margin: 10px 0;
      width: 10px;
      height: 10px;
      border-radius: 100%;
      background: $color-white;

      &.slick-active {
        background: $bg-pink;
      }

      button {
        display: none;
      }
    }
  }
}

@include media-breakpoint-down(md) {
  .hero-block .slider-item {
    background-attachment: scroll;
  }
}

@include media-breakpoint-down(sm) {
  .hero-block {
    .bubble-wrap {
      display: none;
    }

    .slider-item {
      .slider-caption {
        padding-left: calc(40 / 1118 * 100%);
      }
    }
  }
}

@include media-breakpoint-down(xs) {
  .hero-block {
    .slick-dots {
      opacity: 0;
    }

    .slider-item {
      .slider-caption {
        bottom: 2%;
        padding-left: calc(19 / 347 * 100%);
      }
    }
  }
}
