.slick-slide {
  &:focus {
    outline: 0;
  }
}

.custom-control-slick {
  .slick-dots {
    display: inline-block;
    position: relative;
    top: 0;
    bottom: auto;
    width: auto;
    height: auto;
    margin: 0 10px;
    line-height: 1;

    li {
      position: relative;

      &.slick-active {
        button {
          background-color: $bg-pink-2;
        }
      }

      button {
        background-color: $bg-grey-5;
        border-radius: 50%;
        transition: all 0.5s;

        &:before {
          visibility: hidden;
        }
      }
    }
  }

  .slick-arrow {
    display: inline-block;
    position: relative;
    left: auto;
    right: auto;
    margin-top: 3px;
    transform: unset;
    background-size: 20px 20px;
    background-repeat: no-repeat;

    &.slick-disabled {
      pointer-events: none;
    }

    &::before {
      display: none;
    }
  }

  .slick-prev {
    background-image: url("../images/arrow-prev.png");

    &.slick-disabled {
      background-image: url("../images/arrow-prev-disabled.png");
    }
  }

  .slick-next {
    background-image: url("../images/arrow-next.png");

    &.slick-disabled {
      background-image: url("../images/arrow-next-disabled.png");
    }
  }
}

.center-control-slick {
  .slider-control {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    text-align: center;

    .slider-nav {
      display: inline-block;
      line-height: 1;
    }

    .slick-prev {
      float: left;
    }

    .slick-next {
      float: right;
    }

    @include media-breakpoint-down(sm) {
      top: 50%;
      bottom: auto;
      transform: translateY(-50%);

      .slider-nav {
        width: 100%;
      }

      .slick-prev {
        margin-left: -10px;
      }

      .slick-next {
        margin-right: -10px;
      }
    }
  }
}

.overlay-slick {
  .slick-slide,
  .slick-slide[aria-hidden="true"]:not(.slick-cloned) ~ .slick-cloned[aria-hidden="true"] {
    opacity: 0.5;
    transform: scale(0.7);
    transition: transform 0.5s, opacity 0.5s;
  }

  .slick-center,
  .slick-slide[aria-hidden="true"]:not([tabindex="-1"]) + .slick-cloned[aria-hidden="true"] {
    opacity: 1;
    transform: scale(1);
  }
}

.overlay-side {
  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 0;
    width: 30%;
    height: 100%;
    z-index: 1;
  }

  &:before {
    left: 0;
    background: linear-gradient(90deg, rgba(250,249,247,1) 20%, transparent 100%);
  }

  &:after {
    right: 0;
    background: linear-gradient(90deg, rgba(250,249,247,1) 20%, transparent 100%);
    transform: matrix(-1, 0, 0, 1, 0, 0);
  }

  @include media-breakpoint-down(sm) {
    &:before,
    &:after {
      display: none;
    }
  }
}

.slider-dots {
  width: 200px;

  .slider-dots-item {
    height: 20px;
    margin: 10px;
    background-color: $bg-grey-6;
    border-radius: 50%;
    transform: scale(0.5);
    outline: none;
    cursor: pointer;

    &.slick-active {
      transform: scale(0.70);
    }

    &.slick-current {
      transform: scale(1);
      background-color: $bg-pink-2;
    }
  }

  @include media-breakpoint-down(sm) {
    display: none;
  }
}
