.modal {
  &-password {

    @media (max-width: 320px) {
      .form-group .form-control {
        font-size: 20px;
      }
    }

    .modal {
      &-footer {
        justify-content: flex-end;
      }
    }

    .validate-error {
      font-size: 18px;
      line-height: 22px;
      font-family: $font-context-regular;
      font-style: normal;
      text-transform: uppercase;
      margin: 5px 0 0 0;

      @include media-breakpoint-down(sm) {
        font-size: 12px;
      }
    }
  }

  &-payment-card {
    .modal-description {
      font-size: 18px;
      font-style: normal;
      line-height: 110%;
      color: $color-pink-dark;
      width: 70%;

      @include media-breakpoint-down(sm) {
        width: 100%;
      }

      a {
        font-weight: normal;
        text-decoration: none;
      }
    }

    .two-col {
      .form-group {
        width: calc(50% - 30px);

        &:first-child {
          margin-right: 15px;
        }

        &:last-child {
          margin-left: 15px;
        }
      }

      @include media-breakpoint-down(sm) {
        display: block;

        .form-group {
          width: 100%;

          &:last-child {
            margin: 0;
          }
        }
      }
    }

    @include media-breakpoint-down(sm) {
      .form-group .form-control {
        padding: 20px 0;
      }

      .modal-footer {
        .btn {
          margin-left: auto;
        }
      }
    }
  }
}
