.demand {
  position: relative;
  padding-top: 40px;

  @include media-breakpoint-down(sm) {
    padding-top: 30px;
  }

  p {
    margin-bottom: 0;
  }

  .bubble-item-new {
    top: -30px;
    right: 25px;
    transition: opacity ease-in-out 0.5s, visibility ease-in-out 0.5s;

    @include media-breakpoint-down(sm) {
      top: -18px;
      right: 15px;
    }
  }

  &-bg {
    position: absolute;
    top: 0;
    @include initSize(100%, 100%);
  }

  &-top {
    display: flex;
    padding-top: 5%;
    padding-bottom: 40px;
    padding-left: 40px;
    align-items: center;
    justify-content: space-between;

    @include media-breakpoint-down(sm) {
      padding-bottom: 9%;
      padding-left: 0;
    }

    &-left {
      h3 {
        margin-bottom: 0;
      }
    }

    &-right {
      @include media-breakpoint-down(md) {
        width: 30%;
      }

      @media (max-width: 320px) {
        width: 50%;
      }

      a {
        margin-bottom: 0;
        color: $color-gray;
        font-size: 18px;
        font-family: $font-context-regular;
        text-transform: uppercase;

        @include media-breakpoint-down(md) {
          float: right;
        }

        @include media-breakpoint-down(sm) {
          font-size: 12px;
        }
      }
    }
  }

  &-item {
    position: relative;
    margin-bottom: 40px;
    border-radius: 15px;

    .bc-player-YXPyO2n56_default {
      .vjs-poster {
        background-color: transparent;
      }
    }

    @include media-breakpoint-down(sm) {
      margin-bottom: 30px;
    }

    &-hover {
      z-index: 1;
      cursor: pointer;

      &.active,
      &:hover {
        z-index: 2;

        .demand-item-description {
          bottom: 0;
          visibility: visible;
          opacity: 1;
          pointer-events: auto;
        }
      }

      .demand-item-description {
        position: absolute;
        bottom: -20%;
        width: 100%;
        height: auto;
        opacity: 0;
        margin-bottom: 0;
        transition: all .5s ease-in-out;
        pointer-events: none;
        cursor: auto;

        &:before {
          content: '';
          position: absolute;
          top: 0;
          width: 100%;
          height: 100%;
          background: $color-pink-dark-2;
          border-radius: 15px;
        }
      }

      @include media-breakpoint-down(sm) {
        .demand-item-backdrop__wrapper {
          padding: 15px;
        }
      }
    }

    &-primary {
      &.demand-item {
        .action-list {
          top: 15%;
          right: 100px;

          @include media-breakpoint-down(sm) {
            right: 45px;
            width: calc(100% - 45px);
          }
        }
      }

      .demand-item-text {
        display: none;
      }

      .bubble-item-new,
      .bubble-item-live {
        top: 20px;
        right: 20px;

        @include media-breakpoint-down(sm) {
          display: none;
        }
      }

      .demand-item-img {
        position: relative;

        .bubble-item-primary {
          right: 35px;
          bottom: 35px;

          @include media-breakpoint-down(sm) {
            display: none;
          }
        }
      }

      .demand-item-bubble-content {
        a {
          font-size: 28px;
          font-family: $font-header-regular;
        }
      }

      @include media-breakpoint-up(md) {
        .demand-item-content {
          padding-top: 40px;
          margin-top: 0;
        }

        .demand-item-info {
          width: 50%;
        }

        .demand-item-bubble-list {
          width: 16%;
        }

        .demand-item-text {
          display: block;
        }
      }

      &-content-first {
        .demand-item-content {
          margin: 0;
          padding-top: 0;
          padding-bottom: 40px;

          @include media-breakpoint-down(sm) {
            padding-bottom: 20px;
          }
        }

        .demand-item-info {
          width: 35%;

          @include media-breakpoint-down(sm) {
            width: 70%;
          }
        }

        .demand-item-text {
          width: 49%;
          font-size: 18px;
        }
      }
    }

    &-description {
      .demand-item-content {
        .demand-item-bubble-list {
          width: 100%;
          min-height: 87px;
          align-items: flex-start;

          @include media-breakpoint-down(sm) {
            min-height: 57px;
          }
        }
      }

      .demand-item-info {
        margin-bottom: 15px;
      }
    }

    &-backdrop {
      position: relative;
      padding-top: 40%;
      border-radius: 15px;
      overflow: hidden;

      &__wrapper {
        display: flex;
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        padding: 6.4% 25px 6.4% 6.4%;
        flex-wrap: wrap;
        align-content: space-between;
        background-color: $color-pink-dark-2;
        overflow: auto;
      }
    }

    &-text {
      width: 34%;
      font-size: 18px;

      @include media-breakpoint-down(sm) {
        font-size: 16px;
      }

      a {
        color: inherit;
      }
    }

    &-detail {
      font-size: 18px;

      @include media-breakpoint-down(sm) {
        font-size: 16px;
      }

      a {
        color: inherit;
      }
    }

    &-img {
      border-radius: 15px;
      overflow: hidden;

      img {
        width: 100%;
      }
    }

    &-content {
      display: flex;
      position: relative;
      padding-left: 25px;
      padding-right: 25px;
      margin-top: -30px;
      align-items: flex-start;

      @include media-breakpoint-down(sm) {
        padding-left: 15px;
        padding-right: 15px;
        margin-top: -20px;
      }
    }

    .action-list {
      &.action-right {
        top: calc(100% - 110px);
        left: 100%;
      }

      &.action-left {
        top: calc(100% - 110px);
        right: 110px;
      }

      @include media-breakpoint-down(md) {
        width: 70%;

        &.action-right,
        &.action-left {
          top: 35%;
          left: 0;
        }
      }

      @include media-breakpoint-down(sm) {
        width: 85%;

        &.action-right,
        &.action-left {
          top: 25%;
        }
      }
    }

    &-info {
      display: flex;
      width: 69%;
      align-items: flex-start;
      flex-wrap: wrap;

      &__avatar {
        position: relative;
        width: 75px;
        height: 75px;
        border-radius: 50%;
        overflow: hidden;

        @include media-breakpoint-down(sm) {
          width: 40px;
          height: 40px;
        }

        img {
          position: absolute;
          top: 50%;
          left: 50%;
          width: 100%;
          height: auto;
          transform: translate(-50%, -50%);
        }
      }

      &__detail {
        width: calc(100% - 90px);
        margin-left: 15px;

        @include media-breakpoint-down(sm) {
          width: calc(100% - 50px);
          margin-left: 10px;
        }

        h5 {
          margin-bottom: 0;
          color: $color-black-1;

          @include media-breakpoint-down(sm) {
            font-size: 18px;
          }
        }

        h1 {
          margin-bottom: 0;
          color: $color-black-1 !important;
          font-family: protogrotesk-regular,sans-serif;
          font-size: 28px;
          color: #fff;

          @include media-breakpoint-down(sm) {
            font-size: 18px;
          }
        }

        .context-1 {
          color: $color-black-1;

          @include media-breakpoint-down(sm) {
            font-size: 12px;
          }

          a {
            color: inherit;
          }
        }

        .context-2 {
          margin-top: 10px;
          color: $color-gray-2;

          @include media-breakpoint-down(sm) {
            font-size: 12px;
          }
        }
      }
    }

    &-bubble {
      &-list {
        position: relative;
        display: flex;
        width: 31%;
        flex-wrap: wrap;
        align-items: center;
        justify-content: flex-end;
      }

      &-item {
        position: relative;
        margin-bottom: 5px;

        &:not(:first-child) {
          margin-left: 15px;
        }

        @include media-breakpoint-down(sm) {
          margin-bottom: 0;

          &:not(:first-child) {
            margin-left: 10px;
          }
        }

        &__wrapper {
          position: relative;
          @include initSize(60px, 60px);

          @include media-breakpoint-down(sm) {
            @include initSize(35px, 35px);
          }
        }

        .context-3 {
          display: block;
          margin-top: 10px;
          margin-bottom: 0;
          color: $color-gray-1;
          text-transform: uppercase;

          @include media-breakpoint-down(sm) {
            margin-top: 5px;
          }
        }
      }

      &-circle {
        @include initBubbleSize(100%, 100%);
        box-shadow: $bubble-box-shadow-1;
        overflow: hidden;
      }

      &-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        @include initSize(100%, 100%);
        justify-content: center;
        align-items: center;

        h5 {
          color: $color-white;
          margin-bottom: 0;
        }

        a, button {
          display: flex;
          @include initSize(100%, 100%);
          justify-content: center;
          align-items: center;
          border-radius: 50%;
        }

        i {
          display: inline-block;
          @include initSize(8px, 8px);
          background: $bg-white-1;
          border-radius: 50%;

          @include media-breakpoint-down(sm) {
            @include initSize(5px, 5px);
          }

          &:first-child + i {
            margin: 0 5px;

            @include media-breakpoint-down(sm) {
              margin: 0 3px;
            }
          }
        }
      }
    }
  }
}

.bubble-item-new,
.bubble-item-live {
  position: absolute;
  z-index: 1;

  .demand-item-bubble-content {
    h5 {
      font-size: 18px;
      color: $color-black-2;
      font-family: $font-context-regular;

      @include media-breakpoint-down(sm) {
        font-size: 10px;
        line-height: 1;
      }
    }
  }
}


.bubble-item-live {
  .demand-item-bubble-circle {
    box-shadow: none;
  }

  .demand-item-bubble-content {
    h5 {
      color: $color-white;
    }
  }
}

.bubble-item-primary {
  position: absolute;

  .demand-item-bubble-item__wrapper {
    @include initSize(150px, 150px);
  }

  .demand-item-bubble-circle {
    filter: blur(3px);
  }
}

.video-js {
  .vjs-big-play-button {
    color: $color-white;
    background-color: $color-black-1;
  }
}