.meet-teacher {
  position: relative;
  padding-top: 20px;

  .wrapper-heading {
    margin-top: 5%;
  }
}

.card-list {
  &-item {
    position: relative;
    margin-bottom: 20px;

    .bubble-item-new {
      position: absolute;
      top: 20px;
      right: 20px;

      @include media-breakpoint-down(md) {
        top: 10px;
        right: 10px;
      }

      .demand-item-bubble-item__wrapper {
        @include media-breakpoint-down(md) {
          @include initSize(40px, 40px);
        }
      }

      .demand-item-bubble-content {
        h5 {
          @include media-breakpoint-down(md) {
            font-size: 14px;
          }
        }
      }
    }

    &-reverse {
      .row {
        flex-direction: row-reverse;
      }
    }

    .description-paragraph {
      color: $color-black-1;

      p {
        margin-bottom: 50px;

        @include media-breakpoint-down(md) {
          margin-bottom: 30px;
        }

        @include media-breakpoint-down(md) {
          margin-bottom: 15px;
        }
      }
    }

    &-primary {
      .card-list-item-wrapper {
        height: 100%;
        padding-left: 35px;

        @include media-breakpoint-down(lg) {
          padding-left: 20px;
        }

        @include media-breakpoint-down(sm) {
          padding-left: 0;
        }
      }

      .card-list-item-content {
        display: flex;
        height: 100%;
        padding: 60px 0 30px 0;
        flex-wrap: wrap;
        align-content: space-between;
        border-top: 1px solid $border-grey-6;

        @include media-breakpoint-down(lg) {
          padding-top: 30px;
        }

        @include media-breakpoint-down(md) {
          padding-bottom: 15px;
        }

        @include media-breakpoint-down(sm) {
          border: 0;
        }
      }

      .card-list-item-infor {
        @include media-breakpoint-up(sm) {
          margin-top: 0;
        }
      }
    }

    &-img {
      border-radius: 15px;
      overflow: hidden;

      img {
        width: 100%;
      }
    }

    &-description {
      color: $color-gray-4;
      font-size: 38px;
      font-family: $font-header-regular;

      @include media-breakpoint-down(lg) {
        font-size: 28px;
      }

      @include media-breakpoint-down(md) {
        font-size: 22px;
      }

      q {
        display: inline-block;
        line-height: 116%;
      }
    }

    &-infor {
      margin-top: 30px;

      @include media-breakpoint-down(md) {
        margin-top: 15px;
      }

      @include media-breakpoint-down(sm) {
        margin-top: 20px;
      }

      h5 {
        color: $color-black-1;
        margin-bottom: 10px;

        @include media-breakpoint-down(md) {
          font-size: 20px;
          margin-bottom: 5px;
        }

        @include media-breakpoint-down(md) {
          font-size: 18px;
        }
      }

      p {
        margin-bottom: 10px;

        @include media-breakpoint-down(md) {
          margin-bottom: 5px;
        }

        &:last-child {
          margin-bottom: 0;
        }

        &.context-2 {
          color: $color-orange;
        }
      }
    }
  }
}
