.playlist {
  &-content {
    padding-left: 30px;
    padding-right: calc(100 / 1440 * 100%);
  }

  @include media-breakpoint-down(lg) {
    &-content {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  @include media-breakpoint-down(sm) {
    &-content {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

.lists {
  &-accordion {
    &-item {
      border-bottom: 1px solid $border-grey-2;
    }

    &-header {
      padding: 50px 0 20px 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;

      &:not(.collapsed) {
        .icon-plus {
          &:before {
            transform: translateX(-50%) rotate(90deg);
          }

          &:after {
            opacity: 0;
            transform: translateY(-50%) rotate(90deg)
          }
        }
      }
    }

    &-desc {
      h5 {
        color: $color-black-2;
        word-break: break-word;

        a {
          color: inherit;
        }
      }

      p {
        margin: 0;
        color: $color-gray-6;
        font-family: $font-header-regular;
      }
    }
  }

  &-control {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .demand-item-bubble {
      &-list {
        width: auto;
      }

      &-item {
        margin-bottom: 0;

        &__wrapper {
          width: 40px;
          height: 40px;

          @include media-breakpoint-down(sm) {
            width: 36px;
            height: 36px;
          }
        }
      }

      &-content {
        i {
          width: 6px;
          height: 6px;
        }
      }
    }

    .action-list {
      top: 15px;
      right: calc(100% + 15px);
      min-width: 300px;

      @include media-breakpoint-down(sm) {
        right: calc(100% + 5px);
        min-width: 230px;
      }

      @media (max-width: 320px) {
        min-width: 180px;
      }
    }

    .icon-plus {
      margin-left: 15px;

      @include media-breakpoint-down(sm) {
        margin-left: 10px;
      }
    }
  }

  .watched-item {
    &:first-child {
      margin-top: 0;
    }
  }

  @include media-breakpoint-down(lg) {
    &-accordion {
      &-header {
        padding-left: 30px;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    &-accordion {
      &-header {
        padding: 30px 15px;
      }

      &-desc {
        h5 {
          font-size: 18px;
        }
      }
    }
  }
}

.watched {
  &-message {
    margin-top: 20px;
    font-family: $font-context-regular;
    font-size: 18px;
    padding-left: 15px;
  }

  &-wrapper {
    padding-left: 50px;

    .demand-item-bubble-list {
      width: auto;
      flex-wrap: nowrap;
    }

    .video-control {
      position: relative;

      .action-list {
        top: 50%;
        right: calc(100% + 15px);
        min-width: 350px;

        @include media-breakpoint-down(sm) {
          top: 80%;
          right: 0;
          min-width: 220px;

          &-item {
            padding: 15px 20px;
          }
        }
      }

      .btn {
        padding: 0;

        &:focus,
        &.focus {
          box-shadow: none;
        }
      }
    }
  }

  &-item {
    display: inline-block;
    width: 100%;
    margin: 20px 0;

    &-wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-content {
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: space-between;
    }

    &-info {
      margin-left: 30px;
      margin-right: 5px;
    }

    &-avatar {
      display: none;
      max-width: 40px;
      width: 100%;
      height: 40px;
      margin-right: 10px;
      border-radius: 50%;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-title {
      font-size: 28px;
      color: $color-black-2;
    }

    &-teacher {
      margin: 0;
      font-size: 20px;
      color: $color-black-2;
      font-family: $font-context-regular;
    }

    &-img {
      position: relative;
      max-width: 275px;
      height: auto;
      border-radius: 15px;
      overflow: hidden;
    }

    &-tag {
      position: absolute;
      background: $bg-pink;
      padding: 0 10px;
      border-radius: 0 0 15px 0;
      font-family: $font-context-regular;
      font-size: 18px;
    }
  }

  @include media-breakpoint-down(lg) {
    &-wrapper {
      padding-left: 30px;
    }

    .watched-wrapper {
      padding-left: 0;
    }
  }

  @include media-breakpoint-down(sm) {
    .playlist-content,
    .watched-wrapper {
      padding-left: 0;
      padding-right: 0;
    }

    &-wrapper {
      padding: 0 15px;
    }

    &-item {
      &-wrapper {
        display: block;
      }

      &-img {
        max-width: 100%;
      }

      &-content {
        display: flex;
        position: relative;
        padding: 0 15px;
        margin-top: -20px;
        align-items: flex-start;
        z-index: 1;
      }

      &-info {
        display: flex;
        margin-left: 0;
        align-items: flex-start;
      }

      &-avatar {
        display: block;
      }

      &-title {
        margin-bottom: 0;
        font-size: 18px;
      }

      &-teacher {
        font-size: 12px;
      }
    }
  }
}
