.form {
  .validate-error {
    font-size: 14px;
    color: $color-pink-dark;

    @include media-breakpoint-down(sm) {
      font-size: 12px;
    }

    &.general-error {
      width: 100%;
    }
  }

  .btn-group-wrap {
    margin-left: 0;
    justify-content: space-between;
    @include media-breakpoint-down(md) {
      // display: block !important;
      width: 100%;
      text-align: center;

      .enter-code {
        margin-top: 15px;
      }
    }

    @include media-breakpoint-down(sm) {
      display: flex;
      flex-direction: column;
      align-items: initial;
    }
  }

  .enter-code {
    margin-left: 40px;
    font-size: 16px;
    color: $color-pink-dark;
    cursor: pointer;
    text-transform: uppercase;
    flex-grow: 1;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    @include media-breakpoint-down(lg) {
      margin-left: 20px;
    }

    @include media-breakpoint-down(sm) {
      margin-left: 0;
      margin-top: 10px;
      order: -1;
      margin-bottom: 32px;
    }

    p {
      margin: 0;
      white-space: nowrap;
      padding: 0 8px;

      &.big {
        font-size: 20px;
      }
    }
  }

  .apply-code {
    word-break: break-all;

    span.cancel-text {
      display: inline-flex;
      margin-bottom: 2px;
      padding: 3px 4px 4px;
      font-size: 10px;
      color: $color-white;
      background: $bg-grey-1;
      cursor: pointer;
      vertical-align: middle;
      line-height: 1;
    }

    p {
      display: inline;
      margin-right: 5px;
      pointer-events: none;
    }
  }

  .two-col {
    display: flex;

    .form-group {
      width: 50%;
      margin-left: 5%;

      &:first-child {
        margin-left: 0;
        margin-right: 5%;
      }
    }
  }

  &-description {
    margin-top: 15px;
    font-size: 12px;
    color: $color-gray-4;
    font-family: $font-context-regular;
  }

  &-content {
    padding: 30px 40px 0 40px;

    @include media-breakpoint-down(lg) {
      padding: 10px 10px 0 10px;
    }

    @include media-breakpoint-down(sm) {
      padding: 0;
    }
  }

  &-group {
    position: relative;

    &-wrap {
      position: relative;
      line-height: 1;
    }

    .form-control {
      display: block;
      width: 100%;
      height: auto;
      padding: 12px 0;
      background: transparent;
      appearance: none;
      font-family: $font-context-regular;
      font-size: 28px;
      line-height: 1;
      color: $color-gray-4;
      border: 0;
      border-radius: 0;
      border-bottom: 1px solid $border-grey-2;
      box-shadow: none;

      // &.visa,
      // &.mastercard,
      // &.amex,
      // &.discover {
      //   padding-right: 62px;
      // }

      @include media-breakpoint-down(lg) {
        font-size: 22px;
      }

      @include media-breakpoint-down(sm) {
        padding: 4px 0;
      }

      @media (max-width: 320px) {
        &::placeholder {
          font-size: 18px;
        }
      }

      &-pwd {
        padding-right: 60px;
      }

      &.error {
        border-color: $color-pink-dark;
      }

      &.form-large {
        padding: 50px 0;

        @include media-breakpoint-down(sm) {
          padding: 30px 0;
        }
      }
    }

    .btn-inline {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      font-size: 14px;
      font-family: $font-context-regular;
      text-transform: uppercase;
      color: $color-pink-dark;

      @include media-breakpoint-down(sm) {
        font-size: 12px;
      }

      &.show-pwd {
        text-transform: none;
      }
    }
  }

  &-checkbox {
    margin-top: 40px;

    label {
      display: inline-flex;
      position: relative;
      width: 100%;
      cursor: pointer;
      margin-bottom: 0;
      align-items: center;
    }

    span:not(.checkmark) {
      display: inline-block;
      margin: 0 0 0 10px;
      font-size: 14px;
      color: $color-gray-5;
      line-height: 110%;

      @include media-breakpoint-down(sm) {
        font-size: 12px;
      }

      a {
        display: inline-block;
        position: relative;
        color: inherit;
        font-weight: bolder;
        z-index: 2;

        &:hover {
          @include media-breakpoint-up(sm) {
            text-decoration: underline;
          }
        }
      }
    }

    .checkmark {
      position: relative;
      max-width: 35px;
      width: 100%;
      height: 35px;
      background-color: transparent;
      border: 1px solid $border-grey-3;
      border-radius: 3px;

      @include media-breakpoint-down(sm) {
        max-width: 20px;
        height: 20px;
      }

      &:after {
        content: "";
        display: none;
        position: absolute;
        left: 37%;
        top: 5%;
        width: 30%;
        height: 70%;
        border: solid $color-pink-dark;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
      }
    }

    .radiomark {
      position: relative;
      max-width: 35px;
      width: 100%;
      height: 35px;
      background-color: transparent;
      border: 1px solid $border-grey-2;
      border-radius: 22px;
      min-width: 20px;
      min-height: 20px;

      @include media-breakpoint-down(sm) {
        height: 20px;
        width: 20px;
      }

      & ~ span {
        font-family: $font-header-regular;
        font-style: normal;
        font-size: 28px;
        line-height: 110%;
        color: $color-black-1;
        word-break: break-word;

        @include media-breakpoint-down(sm) {
          font-size: 18px;
          width: 80%;
        }
      }

      &:after {
        content: "";
        display: none;
        width: 100%;
        height: 100%;
        border-radius: 22px;
        background-color: $bg-pink;
      }
    }

    input {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      z-index: 1;

      &:checked {
        & ~ .checkmark {
          &:after {
            display: block;
          }
        }

        & ~ .radiomark {
          border: none;

          &:after {
            display: block;
          }

          & ~ span {
            color: $color-pink-dark;
          }
        }
      }

      &.error {
        & ~ .checkmark {
          border-color: $color-pink-dark;
        }
      }
    }
  }

  &-radio {
    .form-option {
      position: relative;
      padding-left: 25px;
      cursor: pointer;
    }

    .checkedbox {
      position: absolute;
      top: 0;
      left: 0;
      height: 20px;
      width: 20px;
      background-color: $bg-white-1;
      border: 1px solid $border-grey-4;
      border-radius: 50%;

      &:after {
        content: "";
        display: none;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 10px;
        height: 10px;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        border-radius: 50%;
        background-color: $bg-pink;
      }
    }

    input {
      opacity: 0;

      &:checked ~ .checkedbox:after {
        display: block;
      }
    }
  }

  .form-wrapper {
    .btn-group-wrap {
      @include media-breakpoint-down(sm) {
        display: flex;
        flex-direction: column;
        align-items: initial;
      }
    }
  }
}

.login-form {
  max-width: 813px;
  margin: 0 auto 80px;
  padding: 40px;
  border: 1px solid $border-grey-1;
  border-radius: 15px;

  .form-header {
    display: block;
    padding: 0 0 40px 0;
    border-bottom: 1px solid $border-grey-1;

    @include media-breakpoint-down(sm) {
      padding: 0 0 20px 0;
    }
  }

  .form-footer {
    display: flex;
    padding: 25px 0;
    border: 0;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    align-items: center;

    .forgetmenot {
      display: inline;
      a {
        font-size: 20px;
      }
    }

    & > input {
      display: inline;
      float: right;
      line-height: initial;
    }
  }

  .form-footnote {
    margin: 40px 0 0px;
    a {
      color: $color-black-1;
      text-decoration: underline;
    }
  }

  .form-group {
    margin: 0;

    .form-control {
      border-color: $border-grey-1;
      color: $color-gray-5;
    }

    .message {
      margin-bottom: 0;
      padding-top: 40px;

      @include media-breakpoint-down(sm) {
        padding-top: 20px;
      }
    }
  }

  .btn {
    @extend %txt-btn;
  }
}

.grecaptcha-badge {
  visibility: visible !important;
  width: 70px !important;
  bottom: 14px;
  right: 4px !important;
  overflow: hidden !important;
  transition: right 0.3s ease 0s !important;
  box-shadow: grey 0px 0px 5px;
  z-index: 10;

  &:hover {
    width: 256px !important;
  }
}

.ico {
  display: inline-block;
  height: 32px;
  width: 32px;
  background-image: url(../images/card-icons.png);
  background-repeat: no-repeat;
  -webkit-background-size: 350px 350px;
  background-size: 350px;
  font-style: normal;

  &-cards {
    display: none;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    pointer-events: none;
    z-index: 2;
  }

  &-card-visa {
    display: block;
    background-position: -160px -307px;
    width: 47px;
    height: 30px;
  }

  &-card-mastercard {
    display: block;
    background-position: -208px -307px;
    width: 47px;
    height: 30px;
  }

  &-card-amex {
    display: block;
    background-position: -256px -307px;
    width: 47px;
    height: 30px;
  }

  &-card-discover {
    display: block;
    background-position: -304px -307px;
    width: 47px;
    height: 30px;
  }

  &-card-unknown-type {
    display: block;
    background-image: url(../images/card-unknown.png);
    background-repeat: no-repeat;
    width: 36px;
    height: 30px;
    background-size: 34px;
  }
}
