.modal {
  &-login {
    .modal-description {
      color: $color-pink-dark;
      font-size: 18px;
      font-family: $font-context-regular;
      text-transform: uppercase;

      a {
        font-size: 20px;
      }

      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }
  }

  &-reply-comment {
    .form-control {
      @include media-breakpoint-down(sm) {
        height: 250px;
      }
    }

    .modal-footer {
      padding-bottom: 0;
      justify-content: flex-end;
    }
  }

  &-radio-options {
    .modal {
      &-footer {
        justify-content: flex-end;
      }
    }

    .form {
      &-checkbox {
        margin-top: 0;
      }

      &-group-wrap {
        padding: 50px 0;
        border-bottom: 1px solid $border-grey-1;
      }
    }

    .title-wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .radio-detail {
      margin-left: 2em;
      font-family: $font-context-regular;
      font-size: 18px;
      text-transform: uppercase;
      color: $color-pink-dark;
      cursor: pointer;

      p {
        margin-bottom: 0;
      }

      @include media-breakpoint-down(sm) {
        font-size: 12px;
      }
    }

    .description-paragraph {
      padding: 10px 0 0 60px;

      @include media-breakpoint-down(sm) {
        padding-left: 40px;
      }
    }
  }

  .close {
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 30px;
    cursor: pointer;
    outline: none;
    background: none;
    border: none;
  }

  .btn {
    @extend %txt-btn;
  }

  &-open {
    .header-main {
      @include media-breakpoint-up(md) {
        padding-right: 17px;
      }
    }

    .modal {
      padding-left: 17px !important;
      padding-right: 17px;
    }
  }

  &-dialog {
    max-width: 815px;
  }

  &-content {
    padding: 40px;
    border-radius: 15px;

    @include media-breakpoint-down(sm) {
      padding: 30px;
    }
  }

  &-header {
    display: block;
    padding: 0 0 40px 0;
    border-color: $border-grey-1;

    @include media-breakpoint-down(sm) {
      padding: 0 0 20px 0;
    }

    h3 {
      margin-bottom: 0;

      @include media-breakpoint-down(sm) {
        font-size: 30px;
      }
    }

    p {
      margin-top: 15px;
      margin-bottom: 0;
      font-size: 18px;
      font-family: $font-context-regular;
    }
  }

  &-body {
    padding: 0;
  }

  &-footer {
    padding: 25px 0;
    border: 0;
    justify-content: space-between;

    .btn {
      padding: .375rem 0;
    }
  }

  &-description {
    color: $color-gray-1;
    font-size: 12px;
    font-family: $font-context-regular;

    p {
      margin-bottom: 0;
    }

    a {
      color: inherit;
      font-weight: bolder;

      &:hover {
        @include media-breakpoint-down(sm) {
          text-decoration: underline;
        }
      }
    }
  }

  .form {
    &-group {
      margin: 0;

      .form-control {
        border-color: $border-grey-1;
        color: $color-gray-5;
      }
    }
  }

  &-playlist-framework {
    .form-checkbox {
      .radio-detail {
        transition: all 0.3s ease-in-out;
        opacity: 0;
      }

      .title-wrap {
        &:hover {
          .radio-detail {
            opacity: 1;
          }
        }
      }
    }

    .form-group {
      label {
        width: 100%;
        margin: 0;
      }

      .form-control {
        padding: 0;
        border: 0;
        color: $color-pink-dark;

        &::placeholder,
        &::-webkit-input-placeholder {
          color: $color-pink-dark;
        }
      }

      .radio-detail {
        p {
          white-space: nowrap;
        }
      }
    }

    @include media-breakpoint-down(lg) {
      .form-checkbox {
        .radio-detail {
          opacity: 1;
        }
      }
    }

    @include media-breakpoint-down(sm) {
      .form-checkbox {

        .radiomark {
          margin-left: 0;

          ~ span {
            width: auto;
          }
        }
      }

      .form-group {
        &-wrap {
          margin: 0 8%;
        }

        .form-control {
          font-size: 22px;
          text-align: center;
        }
      }

      .modal-header {
        padding-bottom: 20px;
        border: 0;
        text-align: center;
      }

      .modal-footer {
        justify-content: center;
      }
    }
  }

  &-playlist-delete,
  &-remove-video {
    .modal {
      &-header {
        text-align: center;
        border: 0;
      }

      &-footer {
        justify-content: center;
      }
    }

    .btn {
      padding: 5px 30px;
      margin: 0 30px;
      border-radius: 30px;
      transition: all 0.5s ease-in-out;
      background-color: transparent;

      &.active {
        color: $color-white;
        background-color: $color-pink-dark;

        @include media-breakpoint-down(sm) {
          margin-top: 20px;
        }
      }

      @include media-breakpoint-up(lg) {
        &:hover {
          color: $color-white;
          background-color: $color-pink-dark;
        }
      }
    }
  }

  &-playlist-rename {
    .form-group {
      .form-control {
        padding: 0;
        border: 0;
        color: $color-pink-dark;
      }
    }
  }
}
