// Overline
@mixin fontOverlineMd {
  font-family: 'L15-SemiBold';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  
  letter-spacing: 1px;
  text-transform: uppercase;
}

@mixin fontOverlineSm {
  font-family: 'L15-SemiBold';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  
  letter-spacing: 1px;
  text-transform: uppercase;
}

// Text - Medium
@mixin fontText1Md {
  font-family: 'L15-Regular';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: unset;
}

@mixin fontText2Md {
  font-family: 'L15-Regular';
  font-style: normal;
