.redesigned-wrapper.footer-deprecated  {
  .footer-bottom {
    padding: 16px 48px;
    background: $neutralText;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: $md) {
      flex-direction: column;
      padding: 16px;
    }

    a.common {
      &:first-child {
        margin-right: 16px;
      }
    }

    &-links {
      display: flex;

      @media (max-width: $md) {
        justify-content: space-between;
        width: 100%;
        margin-bottom: 16px;
      }
    }

    &-text {
      color: $neutralLight;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 1px;
      text-transform: uppercase;
    }
  }

  .footer-external {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: $secondary;

    @media (max-width: $md) {
      flex-direction: column;
    }

    &-left {
      display: flex;
      align-items: center;

      @media (max-width: $md) {
        width: 100%;
      }

      img {
        width: 160px;
        margin: 0 16px;
      }

      .logo-section {
        a {
          &:first-child {
            @media (max-width: $md) {
              display: none;
            }
          }

          &:last-child {
            display: none;
            @media (max-width: $md) {
              display: inline;
            }

            img {
              width: 35px;
            }
          }
        }
      }
    }

    nav {
      display: flex;

      @media (max-width: $md) {
        flex-grow: 1;
      }
    }

    .nav-item {
      @media (max-width: $lg) {
        font-size: 12px;
        padding: 24px 20px;
      }

      @media (max-width: $md) {
        flex-basis: calc(100% / 3);
        text-align: center;
        padding: 24px 10px;
      }
    }

    .nav-item.active {
      background: $neutralSurface;
      position: relative;

      .nav-item-active-before-first,
      .nav-item-active-before-second {
        position: absolute;
        top: 0;
        left: -8px;
        height: 8px;
        width: 8px;
      }

      .nav-item-active-before-first {
        background: #fff;
        z-index: 1;
      }

      .nav-item-active-before-second {
        background: $secondary;
        border-radius: 0 8px 0 0;
        z-index: 2;
      }

      .nav-item-active-after-first,
      .nav-item-active-after-second {
        position: absolute;
        top: 0;
        right: -8px;
        height: 8px;
        width: 8px;
      }

      .nav-item-active-after-first {
        background: #fff;
        z-index: 1;
      }

      .nav-item-active-after-second {
        background: $secondary;
        border-radius: 8px 0 0 0;
        z-index: 2;
      }
    }

    &-right {
      display: flex;
      align-items: center;
      padding-right: 8px;

      @media (max-width: $lg) and (min-width: $md) {
        padding-right: 6px;
      }

      @media (max-width: $md) {
        padding: 24px 8px;
      }

      .social-link {
        margin: 0 8px;

        @media (max-width: $lg) and (min-width: $md) {
          margin: 0 6px;
        }

        img {
          @media (max-width: $lg) and (min-width: $md) {
            width: 16px;
          }
        }
      }
    }
  }

  .footer-internal {
    padding: 16px 48px;
    display: flex;

    @media (max-width: $lg) {
      padding: 16px;
    }
  }

  .footer-internal-wrapper {
    display: flex;
    flex-wrap: wrap;

    .footer-internal-item {
      @media (max-width: 476px) {
        margin-right: 0;
      }

      .subTitle1 {
        word-break: initial;
      }
    }
  }

  .footer-internal-item {
    margin-right: 80px;

    @media (max-width: 476px) {
      margin-right: 16px;
    }

    &:last-child {
      margin-right: 0;
    }

    .common {
      color: $neutralMiddle;
    }
  }

  .footer-internal-links {
    a {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
