.promo-free-class {
  background: $bg-white-1;

  p,
  h1,
  h6 {
    margin: 0;
  }

  .wrapper {
    overflow-x: hidden;
  }

  .promo-free-class-banner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    margin: 0 auto;

    .logo-wrapper {
      height: 92px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      position: fixed;
      top: 0;
      background: $bg-white-1;
      z-index: 10;
      max-width: 1440px;

      @include media-breakpoint-down(sm) {
        height: 80px;
      }
    }

    .banner-wrapper {
      flex-grow: 1;
      background-image: url("../images/promo-landing-banner.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 32px;
      margin-top: 92px;

      @include media-breakpoint-down(sm) {
        padding: 16px;
        margin-top: 80px;
      }

      .banner-title-wrapper {
        text-align: center;
        margin-bottom: 86px;

        h1,
        h6 {
          color: $color-black-5;
          line-height: 110%;
        }

        h1 {
          font-family: $font-1;
          font-size: 96px;
          text-transform: uppercase;

          @include media-breakpoint-down(sm) {
            font-size: 48px;
          }
        }

        h6 {
          font-family: $font-header-light;
          font-size: 28px;
          margin-top: 16px;

          @include media-breakpoint-down(sm) {
            font-size: 24px;
          }
        }
      }

      .banner-schedule-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 1136px;

        @media (max-width: 1168px) {
          width: 100%;
        }

        .banner-schedule {
          margin: 0 40px;
          padding: 16px;
          background: rgba(0, 122, 133, 0.8);
          border-radius: 42px 144px 0px 0px;
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 1056px;

          @media (max-width: 1168px) {
            margin: 0 20px;
            width: calc(100% - 48px);
          }

          @include media-breakpoint-down(sm) {
            padding: 8px 16px;
            border-radius: 20px 48px 0px 0px;
          }

          > p {
            font-family: $font-1;
            font-size: 24px;
            line-height: 110%;
            color: $color-white;
            margin-bottom: 16px;

            @include media-breakpoint-down(sm) {
              font-size: 14px;
            }
          }
        }

        .banner-time-wrapper {
          display: flex;
          justify-content: space-between;
          padding: 0 64px;
          max-width: 900px;
          width: 100%;

          @media (max-width: 1168px) {
            padding: 0 32px;
          }

          @include media-breakpoint-down(sm) {
            padding: 0;
          }
        }

        .banner-time-item {
          text-align: center;
        }

        .banner-time {
          font-family: $font-1;
          font-size: 64px;
          line-height: 110%;
          letter-spacing: -0.05em;
          color: $color-white;

          @include media-breakpoint-down(sm) {
            font-size: 32px;
          }
        }

        .banner-time-description {
          margin-top: 6px;
          font-family: $font-header-light;
          font-size: 16px;
          line-height: 110%;
          text-transform: uppercase;
          color: $color-white;

          @include media-breakpoint-down(sm) {
            font-size: 12px;
          }
        }
      }
    }
  }

  .form-section {
    width: 100%;
    display: flex;

    @include media-breakpoint-down(sm) {
      flex-direction: column;
    }

    input {
      border: 3px solid $color-orange-2;
      font-family: $font-2;
      font-size: 30px;
      line-height: 110%;
      padding: 24px 48px;
      border-radius: 150px 0 0 150px;
      flex-grow: 1;
      z-index: 1;

      @media (max-width: 1168px) {
        padding: 24px;
      }

      @include media-breakpoint-down(sm) {
        border-radius: 32px 32px 0 0;
        font-size: 20px;
        padding: 16px;
      }
    }

    button {
      border-radius: 0 150px 150px 0;
      background: $color-orange-2;
      padding: 24px 48px;
      font-family: $font-1;
      font-size: 36px;
      line-height: 100%;
      color: $color-white;
      border: none;

      @media (max-width: 1168px) {
        padding: 24px;
        font-size: 30px;
      }

      @include media-breakpoint-down(sm) {
        border-radius: 0 0 32px 32px;
        font-size: 24px;
        padding: 16px;
      }
    }
  }

  .promo-free-subheader {
    font-family: $font-1;
    font-size: 32px;
    line-height: 110%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #007c89;

    @include media-breakpoint-down(sm) {
      font-size: 24px;
    }
  }

  .promo-free-text {
    font-family: $font-context-light;
    font-size: 18px;
    line-height: 23px;
    color: $color-black-5;
    text-align: start;
  }

  .promo-free-about-class-wrapper {
    background: #fff8f1;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    > img {
      position: absolute;
      left: -104px;
      bottom: 0;
      mix-blend-mode: multiply;

      @include media-breakpoint-down(sm) {
        height: 100%;
        object-fit: cover;
      }
    }

    .promo-free-about-class {
      max-width: 1136px;
      text-align: center;
      padding: 96px 16px 64px;
      z-index: 1;

      @include media-breakpoint-down(sm) {
        padding: 80px 16px 52px;
      }
    }

    .promo-free-about-class-main {
      display: flex;
      margin-top: 48px;

      @include media-breakpoint-down(sm) {
        flex-direction: column;
      }
    }

    .promo-free-about-class-calendar-wrapper {
      border-radius: 16px 96px 16px 16px;
      padding: 32px;
      background: $bg-white-1;
      position: relative;
      margin-right: 32px;
      min-width: 328px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @include media-breakpoint-down(sm) {
        margin-right: 0;
        margin-bottom: 16px;
        min-width: auto;
        padding: 16px;
      }

      > .addtocalendar {
        position: initial;
      }

      > button,
      > .addtocalendar .atcb-link {
        padding: 12px 30px;
        font-family: $font-1;
        font-size: 24px;
        line-height: 100%;
        color: $color-orange-2;
        background: transparent;
        border: none;

        @include media-breakpoint-down(sm) {
          padding: 0 8px;
        }
      }
    }

    .promo-free-about-class-calendar {
      display: flex;
      align-items: center;
    }

    .addtocalendar .atcb-list {
      width: 100%;
      position: absolute;
      top: 186px;
      left: 0;
      padding: 8px 0;
      background: $bg-white-1;
      box-shadow: 0px 0px 12px rgba(25, 38, 39, 0.25);
      border-radius: 16px;
      text-align: start;
      overflow: hidden;

      @include media-breakpoint-down(sm) {
        top: 154px;
      }

      a {
        padding: 8px 16px;
        display: block;
        font-family: $font-header-light;
        font-size: 24px;
        line-height: 100%;
        color: $color-gray-10;

        &:hover,
        &:active {
          color: $color-orange-2;
        }
      }
    }

    .promo-free-calendar-dropdown {
      width: 100%;
      position: absolute;
      top: 186px;
      left: 0;
      padding: 8px 0;
      background: $bg-white-1;
      box-shadow: 0px 0px 12px rgba(25, 38, 39, 0.25);
      border-radius: 16px;
      text-align: start;
      display: none;

      @include media-breakpoint-down(sm) {
        top: 154px;
      }

      > a {
        padding: 8px 16px;
        display: block;
        font-family: $font-header-light;
        font-size: 24px;
        line-height: 100%;
        color: $color-gray-10;

        &:hover,
        &:active {
          color: $color-orange-2;
        }
      }
    }

    .promo-free-about-class-schedule {
      padding: 8px 0;
      margin-left: 24px;

      > p {
        font-family: $font-1;
        font-size: 24px;
        line-height: 110%;
        color: #001113;
        text-align: start;

        &:first-child {
          margin-bottom: 15px;
        }

        &.start-date {
          span {
            font-size: 36px;
          }
        }

        &.class-duration {
          span {
            font-family: $font-header-light;
            font-size: 16px;
          }
        }
      }
    }

    .promo-free-about-class-description {
      background: $bg-white-1;
      padding: 32px;
      border-radius: 16px 144px 16px 16px;

      @include media-breakpoint-down(sm) {
        border-radius: 16px;
        padding: 16px;
      }
    }

    .promo-free-about-class-description-title {
      display: flex;
      align-items: center;
      margin-bottom: 24px;

      @include media-breakpoint-down(sm) {
        margin-bottom: 16px;
      }

      h6 {
        font-family: $font-1;
        font-size: 36px;
        line-height: 110%;
        color: #001113;
      }
    }
  }

  .promo-free-about-instructor-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .promo-free-about-instructor {
    max-width: 1136px;
    padding: 96px 16px 64px;
    width: 100%;
    position: relative;

    @include media-breakpoint-down(sm) {
      padding: 80px 16px 52px;
      overflow: hidden;
    }

    > h1 {
      text-align: center;
    }
  }

  .promo-free-about-instructor-main {
    padding-top: 80px;

    > h6 {
      font-family: $font-1;
      font-size: 24px;
      line-height: 110%;
      color: #001113;

      @media (max-width: 1168px) {
        z-index: 1;
        position: relative;
      }
    }
  }

  .promo-free-about-instructor-main-notification {
    padding: 32px;
    box-shadow: 0px 0px 20px rgba(137, 81, 0, 0.25);
    border-radius: 16px;
    margin-top: 32px;
    position: relative;
    max-width: 540px;
    margin-bottom: 64px;

    @media (max-width: 1168px) {
      z-index: 1;
      background: $bg-white-1;
    }
  }

  .promo-free-quotes {
    position: absolute;
    top: -48px;
    left: 48px;
    font-family: $font-2;
    font-size: 144px;
    line-height: 180px;
    color: $color-black-5;
  }

  .promo-free-certificate-wrapper {
    margin: 16px 0 -8px;

    @media (max-width: 1168px) {
      z-index: 1;
      position: relative;
    }
  }

  .promo-free-certificate-item {
    display: flex;
    align-items: center;
    padding: 10px;
    margin: 10px 0 15px 0;
    max-width: 540px;
    background: $bg-white-1;
    box-shadow: 0 0 8px rgba(165, 193, 113, 0.25);
    border-radius: 8px;

    @include media-breakpoint-down(sm) {
      padding: 8px;
    }

    > p {
      font-family: $font-2;
      font-size: 18px;
      line-height: 32px;
      color: $color-black-5;
      margin-left: 8px;
    }
  }

  .promo-free-instructor-img-wrapper {
    position: absolute;
    right: 0;
    top: 148px;

    @include media-breakpoint-down(sm) {
      right: -240px;
      top: 110px;
    }
  }

  .promo-free-instructor-img {
    position: relative;
  }

  .promo-free-notification {
    width: 486px;
    position: absolute;
    top: 28px;
    right: 62%;
    padding: 32px;
    background: #fff8f1;
    box-shadow: 0px 0px 10px rgba(137, 81, 0, 0.25);
    border-radius: 50px 50px 0px 50px;

    @media (max-width: 1168px) {
      display: none;
    }
  }

  .promo-free-bottom-section-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff8f1;
  }

  .promo-free-bottom-section {
    max-width: 1136px;
    padding: 16px;
    width: 100%;

    > p {
      font-family: $font-context-light;
      font-size: 20px;
      line-height: 28px;
      color: #001113;
      padding-bottom: 16px;

      span {
        font-family: $font-context-bold;
      }
    }

    .form-section {
      input,
      button {
        padding: 18px 48px;

        @include media-breakpoint-down(sm) {
          padding: 16px;
        }
      }
    }
  }
}

// Footer
.footer-new {
  max-width: 100%;
  padding: 32px 16px;
  background: $color-black-5;

  .footer-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;

    @media (max-width: 1167px) {
      flex-direction: column;
    }

    li {
      color: $color-gray-9;
      font-size: 18px;

      &::before {
        content: none;
      }

      a {
        font-family: $font-2;
        font-size: 18px;
        line-height: 23px;
        color: $color-white;

        &:hover,
        &:focus {
          color: #8fb14d;
        }

        &:active {
          color: #6c8e2b;
        }
      }
    }

    .copyright {
      font-family: $font-2;
      font-size: 18px;
      line-height: 23px;
      color: $color-gray-9;

      @media (max-width: 1167px) {
        letter-spacing: -1px;
      }
    }

    .social-list {
      padding: 28px 0 12px;
      margin: 0;
      display: flex;
      align-items: center;

      @media (max-width: 1167px) {
        order: -1;
        margin-bottom: 8px;
      }

      a {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: $color-gray-9;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 12px;

        @media (max-width: 1167px) {
          margin: 0 8px;
        }

        &:hover,
        &:focus {
          background: #8fb14d;
        }

        &:active {
          background: #6c8e2b;
        }

        img {
          width: 25px;
          height: 25px;
        }
      }
    }
  }
}

.free-class-btn {
  background: #f18d7e !important;
  color: #fff !important;
  padding: 10px 50px 10px 50px !important;
  border-radius: 26px !important;
  font-size: 17px !important;
}
#freeClass .modal-footer {
  display: block;
  text-align: center;
}
#freeClass .modal-header {
  border-bottom: 0px;
}
#freeClass .modal-body {
  text-align: center;
}

.freeclass-modal-desc {
  margin-top: 34px;
}



.free-class-txt1 {
  font-family: AkkuratLLWeb-Light, sans-serif;
  font-size: 19px;
  font-weight: 900;
}

.free-class-txt-grey {
  font-family: AkkuratLLWeb-Light, sans-serif;
  color: #8d9690;
  font-size: 19px;
  line-height: 1.1;
}

#freeClassFail .modal-footer{
  display:block;
  text-align: center;
}
#freeClassFail .modal-header{
  border-bottom: 0px;
}
#freeClassFail .modal-body{
  text-align: center;
}
