.greeting {
  position: relative;
  margin-bottom: 12px;

  &-bg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: $bg-tan-linear,url(../images/wide-desk.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 25px;

    @include media-breakpoint-down(sm) {
      background: $color-tan-dark-1;
      border-radius: 15px;
    }
  }

  &-wrapper {
    position: relative;
  }

  &-container {
    display: flex;
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
    justify-content: space-between;

    @include media-breakpoint-down(sm) {
      padding-top: 25px;
      padding-bottom: 25px;
    }
  }

  &-gap {
    @include media-breakpoint-up(md) {
      margin-right: calc(132 / 1440 * 100%);
    }

    .greeting-content {
      margin-right: 0;
    }
  }

  &-content {
    margin: 25px 25px 25px 0;
    color: $color-black-2;
    word-break: break-word;

    @include media-breakpoint-down(sm) {
      margin: 0;
    }

    h3 {
      margin-bottom: 0;

      @include media-breakpoint-down(sm) {
        font-size: 24px;
      }
    }

    a {
      color: inherit;
    }
  }

  &-bubble {
    &-list {
      position: relative;
      display: flex;
      margin-right: 35px;

      @include media-breakpoint-down(sm) {
        display: none;
      }
    }

    &-item {
      position: relative;

      &:not(:last-child) {
        margin-right: 20px;
      }
    }

    &-circle {
      @include initFuzzyBubble(150px, 150px);
      filter: blur(3px);

      @include media-breakpoint-down(md) {
        @include initFuzzyBubble(120px, 120px);
      }
    }

    &-content {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      p {
        margin: 0;
        font-size: 28px;
        font-family: $font-header-regular;
        color: $color-white;

        @include media-breakpoint-down(md) {
          font-size: 22px;
        }
      }
    }

    &-temperature {
      .greeting-bubble-content {
        p {
          position: relative;

          &:after {
            content: '';
            position: absolute;
            top: 10px;
            right: -10px;
            width: 9px;
            height: 9px;
            border: 3px solid $color-white;
            border-radius: 50%;
          }
        }
      }
    }
  }
}
