.one-pass-section,
.renew-active-section {
  video {
    min-height: 600px;
  }

  .promo-background {
    background: radial-gradient(
      93.13% 93.12% at 50% 50%,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0.8) 100%
    );
    position: absolute;
    height: 100%;
    width: 100%;
  }

  .vert-center-text {
    position: absolute;
    top: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    .video-btn {
      color: $color-white;
      background-color: $color-pink-dark;
      padding: 18px 48px;
      border-radius: 25px;
      text-transform: uppercase;
      font-family: $font-1;
      font-size: 16px;
      transition: transform 1s;
      text-align: center;
      width: fit-content;
    }
  }

  .vert-center-container {
    position: relative;
  }

  @media only screen and (min-width: 790px) {
    .logo-copy {
      font-size: 28px;
      line-height: 1.5;
      color: $color-gray-4;
      display: inline-block;
      font-style: italic;
      vertical-align: top;
      width: 40%;
      padding-top: 15px;
    }

    .custom-column {
      display: inline-block;
      width: 40%;
      text-align: center;
      vertical-align: bottom;
    }

    img.todays-show,
    img.usa-today {
      height: 48px;
    }
  }

  @media only screen and (max-width: 790px) {
    img.todays-show {
      width: 70px;
    }

    img.usa-today {
      width: 135px;
    }

    .copy-today {
      padding-right: 10px;
      padding-left: 10px;
    }

    .logo-copy {
      font-size: 16px;
      line-height: 1.5;
      color: $color-gray-4;
      display: inline-block;
      font-style: italic;
      vertical-align: top;
      width: 40%;
      padding-top: 15px;
    }

    .custom-column {
      display: inline-block;
      width: 90%;
      text-align: center;
      vertical-align: bottom;
    }
  }

  .custom-column {
    display: inline-block;
    width: 40%;
    padding-top: 50px;
    text-align: center;
  }

  .custom-row-logo {
    text-align: center;

    &::after {
      content: "";
      clear: both;
      display: table;
      text-align: center;
    }
  }

  .livestream-header {
    font-family: $font-1;
    font-size: 64px;
    line-height: 110%;
    text-align: center;
    color: $color-black-1;
    padding: 96px 0 60px;
    margin: 0;

    @include media-breakpoint-down(sm) {
      font-size: 24px;
      padding: 54px 0 40px;
    }
  }

  .livestream-info-section {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0 10%;
    @include media-breakpoint-down(sm) {
      margin: 0;
    }

    > p {
      margin: 0;
      font-family: $font-header-extralight;
      font-size: 34px;
      line-height: 110%;
      color: $color-black-1;
      padding-right: 16px;
      @media only screen and (max-width: 1100px) {
        font-size: 18px;
      }
    }
  }

  .full-schedule-link {
    font-family: $font-2;
    font-size: 18px;
    line-height: 23px;
    text-transform: uppercase;
    color: $color-gray;
    white-space: nowrap;
    display: block;
    text-align: center;

    &:hover {
      transform: scale(1.05);
      color: $color-pink-dark;
    }
  }
}

.renew-active-section {
  .vert-center-text {
    top: 26%;

    .video-btn {
      margin-top: 32px;
    }
  }
}

.one-pass-reject-popup,
.renew-active-reject-popup {
  .modal-content {
    @include media-breakpoint-down(xl) {
      padding: 24px 40px;
    }
  }
  .modal-header {
    @include media-breakpoint-down(xl) {
      padding: 0 0 24px 0;
    }
    h3 {
      text-align: center;
      max-width: 638px;
      margin: 0 auto;

      @include media-breakpoint-down(xl) {
        font-size: 36px;
      }
    }
  }

  .modal-body {
    display: flex;
    flex-direction: column;
    align-items: center;

    > p {
      font-family: $font-2;
      font-size: 18px;
      line-height: 20px;
      color: $color-black-5;
      padding: 32px 0 48px;
      margin: 0;

      @include media-breakpoint-down(xl) {
        padding: 12px 0 36px;
      }
    }
  }

  .modal-footer {
    display: flex;
    justify-content: space-between;
    padding: 50px 0 25px;

    &-text {
      max-width: 350px;
      font-family: $font-2;
      font-size: 16px;
      line-height: 20px;
      color: $color-gray-9;
      margin: 0;
      margin-right: 16px;

      @include media-breakpoint-down(xl) {
        padding: 32px 0 0 0;
      }

      @include media-breakpoint-down(sm) {
        max-width: fit-content;
      }
    }

    &-btn {
      padding: 10px 48px;
      background: $bg-pink;
      border-radius: 56px;
      font-family: $font-1;
      font-size: 16px;
      line-height: 18px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $color-white;
      border: none;

      @include media-breakpoint-down(sm) {
        margin-top: 24px;
        width: 100%;
      }
    }
  }
}

.renew-active-reject-popup {
  .modal-footer {
    flex-wrap: nowrap;

    @include media-breakpoint-down(sm) {
      flex-wrap: wrap;
    }

    &-text {
      max-width: fit-content;
    }
  }
}
