.live-schedule-block {
  padding-top: 20px;
  position: relative;

  &.filter-off {
    .live-content-wrap {
      max-height: none;
    }

    .live-wrapper {
      padding-left: 3%;
    }
  }

  .wrapper-heading {
    margin-top: 5%;
  }
}

.live-wrapper {
  border-radius: 15px;
  margin-bottom: 13px;

  .live-content {
    @include media-breakpoint-up(md) {
      &-col {
        padding-right: 0;
      }
    }

    &-wrap {
      height: 100%;
      max-height: 750px;
      padding-right: 15px;
      outline: none;

      @include media-breakpoint-down(sm) {
        padding-right: 0;
      }
    }

    &-item {
      display: flex;
      justify-content: space-between;
      padding: 40px 0;
      font-style: normal;
      font-weight: normal;
      cursor: pointer;

      &[data-view-schedule-cta] {
        cursor: unset;
      }

      &.item-active {
        @include media-breakpoint-up(md) {
          .live-content-item__description {
            h5,
            .context-1,
            a {
              color: $color-pink-dark;

              span {
                color: $color-pink-dark;

                &:after {
                  background: $color-pink-dark;
                }
              }
            }
          }

          .live-content-item__schedule {
            p,
            .atcb-link {
              color: $color-pink-dark;
            }

            .atcb-checkmark:after {
              border-color: $color-pink-dark
            }
          }
        }
      }

      &:hover:not(.avoid-click) {
        @include media-breakpoint-up(lg) {
          .live-content-item__description {
            h5,
            .context-1,
            a {
              color: $color-pink-dark;

              span {
                color: $color-pink-dark;

                &:after {
                  background: $color-pink-dark;
                }
              }
            }
          }

          .live-content-item__schedule {
            p,
            .atcb-link {
              color: $color-pink-dark;
            }

            .atcb-checkmark:after {
              border-color: $color-pink-dark
            }
          }
        }
      }

      p:last-child {
        margin-bottom: 0;
      }

      &__info {
        display: flex;
        width: 75%;
      }

      &__description {
        margin-left: 16px;

        h5 {
          margin-bottom: 9px;
          color: $color-black-2;
          transition: color 0.5s ease;
        }

        .context-1 {
          color: $color-black-2;
          transition: color 0.5s ease;
          font-size: 15px;
          margin-bottom: 2px;

          span {
            transition: color 0.5s ease;

            &::after {
              content: "\A";
              width: 5px;
              height: 5px;
              border-radius: 50%;
              background: $bg-black-2;
              display: inline-block;
              margin: 2px 5px;
              transition: all 0.5s ease;
            }

            &:last-of-type {
              &::after {
                display: none;
              }
            }
          }

          .substitute {
            text-decoration: line-through;
            text-decoration-color: $color-pink-dark;
            text-decoration-thickness: 3px;
          }
        }

        .context-2 {
          color: $color-gray-1;
        }

        &-watch {
          animation: pulse 2s infinite;

          &:hover {
            transform: scale(1.05);
            animation-play-state: paused;
            @include media-breakpoint-down(sm) {
              transform: none;
            }
          }

          a {
            font-size: 15px;
            font-weight: 600;
            color: $color-pink-dark;
            text-transform: uppercase;
          }
          @include media-breakpoint-down(sm) {
            animation: none;
          }
        }
      }

      &__schedule {
        display: flex;
        flex-direction: column;
        align-items: baseline;
        width: 25%;
        padding-right: 0;
        padding-top: 2px;

        p {
          transition: color 0.5s ease;

          &.context-3 {
            color: $color-green-2;
            margin: 0;
            padding-bottom: 5px;
          }

          &.live-content-item__schedule-zone {
            padding-bottom: 0;
            padding-top: 5px;
          }
        }
      }

      &__schedule-time {
        font-family: $font-header-extralight;
        font-weight: 400;
        font-size: 28px;
        line-height: 35px;
        color: $color-green-1;
        align-items: center;
        margin-bottom: 0;
        line-height: 1;

        span {
          font-size: 20px;
        }
      }

      .schedule-cta-result {
        padding-top: 10px;
        color: $bg-red;
      }

      .heading-small-section {
        color: $color-gray;
        margin: 0 auto;
      }
    }

    &__avatar {
      position: relative;
      min-width: 76px;
      max-width: 76px;
      width: 100%;
      height: 76px;
      border-radius: 50%;
      overflow: hidden;
      text-align: center;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-bubble {
      display: flex;
      justify-content: center;

      & ~ img {
        margin-top: -20px;
      }

      .heading-small-section {
        color: $color-black-2;
      }
    }
  }

  .live-thumbnail {
    &-col {
      padding-left: 0;
    }

    &-wrap {
      height: 100%;
      padding-left: 0;
      border-radius: 15px;
      overflow: hidden;

      .bubble-wrap {
        bottom: 5%;
        display: none;
      }
    }

    &-item {
      position: relative;
      background-repeat: no-repeat;
      background-size: 50%;
      background-attachment: fixed;
      background-position: 50vw;
      height: 0;
      transition: all 2s ease;
      background-color: $bg-tan-light;

      &.thumbnail-active {
        height: 100%;

        .bubble-wrap {
          display: block;
        }
      }
    }

    &__content {
      position: absolute;
      top: 6%;
      left: 9%;

      &-description {
        width: 80%;
      }

      span:not(:first-child) {
        &::before {
          content: "\A";
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background: $color-white;
          display: inline-block;
          margin: 4px 5px;
        }
      }
    }
  }

  .line-separator {
    border-bottom: 1px solid $color-gray-3;

    &:first-child {
      border: none;
    }
  }

  .line-separator-text {
    display: flex;
    align-items: center;
    text-align: center;

    &:before,
    &:after {
      content: "";
      display: inline-block;
      height: 1px;
      flex: 1;
      background: $bg-grey-2;
    }

    p {
      margin: 0 10px;
      font-family: $font-context-regular;
      font-style: normal;
      font-weight: normal;
      font-size: 10px;
      line-height: 12px;
      text-align: center;
      text-transform: uppercase;
      color: $color-green-2;
    }
  }
}

