@include media-breakpoint-down(md) {
  .live-schedule-block {
    &.filter-off {
      .live-wrapper {
        padding-left: 0;
      }
    }

    .live-content {
      &-wrap {
        height: auto;
        max-height: none;
      }

      &-item {
        &__info {
          padding-right: 0;
        }

        &__description {
          margin-left: 10px;

          .context-1 {
            span {
              display: block;
            }
          }
        }

        &__schedule-time {
          font-size: 25px;
        }
      }

      &__avatar {
        .live-content-bubble ~ img {
          margin-top: -10px;
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .live-schedule-block {
    padding-bottom: 0;

    .live-content {
      &-item {
        padding: 30px 0;

        &__description {
          margin-left: 11px;

          h5 {
            font-size: 18px;
          }

          .context-1 {
            margin-bottom: 5px;

            span {
              display: inline;
            }
          }
        }

        &__schedule {
          width: 21%;

          p {
            justify-content: flex-end;
          }
        }

        &__schedule-time {
          font-size: 18px;
          line-height: 22px;

          span {
            font-size: 14px;
          }
        }
      }

      &__avatar {
        max-width: 40px;
        height: 40px;
        min-width: auto;
      }

      &-bubble {
        .bubble-circle {
          width: 40px;
          height: 40px;
        }

        .context-new {
          font-size: 10px;
        }
      }
    }
  }

  .live-wrapper {
    background: unset;

    .live-thumbnail__content {
      left: 5%;
    }
  }
}

@keyframes pulse {
  0% {
    transform: scale(1.05);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.05);
  }
}
