.sub-header {
  .header-ticket {
    width: 100%;

    .header-inner {
      position: relative;
      overflow: hidden;
    }

    .x-close {
      top: 50%;
      right: 10px;
      width: 24px;
      height: 24px;
      position: absolute;
      overflow: hidden;
      background: transparent;
      border: none;
      border-radius: unset;
      outline: none;
      @include transform(translateY(-50%));

      &:hover {
        cursor: pointer;
      }

      &::before,
      &::after {
        content: "";
        position: absolute;
        height: 2px;
        width: 100%;
        top: 50%;
        left: 0;
        margin-top: -1px;
        background: #000;
      }

      &::before {
        @include transform(rotate(45deg));
        background-color: $color-white;
      }

      &::after {
        @include transform(rotate(-45deg));
        background-color: $color-white;
      }
    }
  }

  .gift-notification {
    .header-inner {
      background: url("../images/pattern.svg"), $bg-orange-2;
      background-color: #8FB14D;
      background-repeat-x: no-repeat;
      &-left {
        display: flex;
        align-items: center;

        @include media-breakpoint-down(sm) {
          margin-bottom: 16px;
        }

        > img {
          margin-right: 24px;
        }

        > p {
          margin-bottom: 0;
          //font-family: $font-1;
          //font-size: 16px;
          line-height: 22px;
          color: $color-white;
          margin-right: 24px;
          font-size: 32px;

          @media (max-width: 1100px) {
            font-size: 30px;
          }

          @include media-breakpoint-up(xl) {

          }
          @include media-breakpoint-down(sm) {
            margin-right: 0;
          }
          font-family: Gotham SSm A,Gotham SSm B,sans-serif;
          font-style: italic;
          font-weight: 700;
          line-height: 100%;
        }
      }

      .link-1 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 40px 16px;
        text-decoration: none;
        @include media-breakpoint-down(sm) {
          flex-direction: column;
          margin-left: 10px;
        }
        margin-left: 40px;
        @media (max-width: 1100px) {
          margin-left: 0px;
        }
      }

      .gift-fake-button {
        padding: 16px 48px;
        background: $bg-white-1;
        border-radius: 56px;
        font-family: $font-1;
        font-size: 16px;
        line-height: 18px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #007C89;
        white-space: nowrap;
        @include media-breakpoint-down(sm) {
          width: 100%;
          text-align: center;
        }
      }

      .yw-gift-link {
        border-radius: unset;
      }
    }

    .x-close {
      top: 12px;
      right: 6px;
      width: 16px;
      height: 16px;
      padding: 4px;
    }
  }
}
