// should be removed
.redesigned-wrapper.header-deprecated {
  header {
    position: sticky;
    top: 0;
    z-index: 999;
  }

  .avatar-wrapper {
    min-width: 56px;
    width: 56px;
    height: 56px;
    outline: 1px solid $neutralBorder;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.2s;

    &:hover,
    &:focus {
      outline: 3.5px solid $primary;
    }
  }

  .live-indicator {
    display: inline-block;
    width: 16px;
    height: 16px;
    background: radial-gradient(
      50% 50% at 50% 50%,
      #ff7a00 42.19%,
      rgba(255, 122, 0, 0) 100%
    );
  }

  .user-profile-dropdown,
  .mobile-nav-dropdown,
  .mobile-nav-dropdown-logout {
    position: absolute;
    right: 16px;
    top: calc(100% + 8px);
    width: 328px;
    box-shadow: 0px 2px 2px rgba(57, 69, 49, 0.15),
      0px 4px 12px rgba(0, 0, 0, 0.2);
    border-radius: 16px;
    overflow: hidden;
    display: none;

    @media (max-width: 450px) {
      width: calc(100% - 32px);
    }

    .header-external {
      padding: 0;

      .nav-item {
        flex-basis: calc(100% / 3);
        text-align: center;
        padding: 24px 0;
      }
    }

    .header-dropdown-main {
      padding: 16px 0;
      background: $neutralSurface;

      &-links {
        .common {
          padding: 16px 0;
          color: $neutralText;
          font-size: 20px;
          line-height: 24px;
          width: 100%;
          text-align: center;
          font-family: $poppins;

          &::after {
            content: none;
          }

          &:hover {
            color: $primary;
          }

          &:focus {
            color: $primaryAccented;
          }
        }

        .common.active {
          color: $primary;

          &:focus {
            color: $primaryAccented;
          }
        }
      }

      .btn-negative {
        margin: 16px;
      }
    }
  }

  .navbar-toggle {
    padding: 6px 2px;
    margin: 24px 0 0;
    background-color: transparent;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
    transform: translateY(-50%);
    display: none;

    @media (max-width: $lg) {
      display: block;
    }

    &.active {
      .icon-bar {
        &:nth-of-type(1) {
          top: 6px;
          transform: rotate(45deg);
        }

        &:nth-of-type(2) {
          background-color: transparent;
        }

        &:nth-of-type(3) {
          top: -6px;
          transform: rotate(-45deg);
        }
      }
    }

    .icon-bar {
      display: block;
      position: relative;
      width: 26px;
      height: 2px;
      border-radius: 1px;
      background: $bg-black-1;
      transition: all 0.3s ease-in-out;

      &:not(:first-child) {
        margin-top: 4px;
      }

      &:nth-of-type(2) {
        top: 1px;
      }

      &:nth-of-type(3) {
        top: 2px;
      }
    }
  }

  .header-dropdown-social {
    display: flex;
    align-items: center;
    padding: 12px 16px;
    background: $secondary;
    justify-content: space-between;
  }

  .header-external {
    display: flex;
    background: $secondary;
    padding: 0 32px;

    @media (max-width: $lg) {
      padding: 0;
    }

    @media (max-width: $md) {
      display: none;
    }

    .nav-item {
      padding: 16px 32px;
      font-size: 12px;
      letter-spacing: 1px;

      @media (max-width: $md) {
        padding: 16px 28px;
      }
    }

    .nav-item.active {
      background: $neutralSurface;
      position: relative;

      .nav-item-active-before-first,
      .nav-item-active-before-second {
        position: absolute;
        bottom: 0;
        left: -8px;
        height: 8px;
        width: 8px;
      }

      .nav-item-active-before-first {
        background: #fff;
        z-index: 1;
      }

      .nav-item-active-before-second {
        background: $secondary;
        border-radius: 0 0 8px 0;
        z-index: 2;
      }

      .nav-item-active-after-first,
      .nav-item-active-after-second {
        position: absolute;
        bottom: 0;
        right: -8px;
        height: 8px;
        width: 8px;
      }

      .nav-item-active-after-first {
        background: #fff;
        z-index: 1;
      }

      .nav-item-active-after-second {
        background: $secondary;
        border-radius: 0 0 0 8px;
        z-index: 2;
      }
    }
  }

  .header-internal {
    padding: 12px 48px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    background: $neutralSurface;

    @media (max-width: $lg) {
      padding: 12px 32px;
    }

    @media (max-width: $md) {
      padding: 8px 16px;
    }

    > a {
      img {
        width: 216px;

        @media (max-width: $lg) {
          width: 160px;
        }
      }
    }

    .nav-items {
      display: flex;
      align-items: center;

      @media (max-width: $lg) {
        display: none;
      }

      .common {
        padding: 0 8px;
        color: $neutralText;
        font-size: 20px;
        line-height: 24px;
        margin-right: 16px;
        font-family: $poppins;
        &::after {
          height: 2px;
        }

        &:last-child {
          margin-right: 0;
        }

        &:focus {
          color: $primaryAccented;
        }
      }

      .common.active {
        color: $primary;

        &:focus {
          color: $primaryAccented;
        }
      }
    }

    .nav-items-wrapper {
      display: flex;
      align-items: center;
    }

    .avatar-wrapper {
      margin-left: 48px;

      @media (max-width: $lg) {
        margin-left: 20px;
      }
    }
  }

  .header-internal.header-internal-logout {
    .logo-wrapper {
      a {
        &:first-child {
          @media (max-width: $md) {
            display: none;
          }
        }

        &:last-child {
          width: 35px;
          @media (min-width: $md) {
            display: none;
          }
        }
      }
    }

    .nav-items {
      .common {
        font-size: 18px;
        line-height: 20px;
        text-transform: none;
      }
    }

    .live-indicator {
      width: 14px;
      height: 14px;
    }

    .navbar-toggle {
      @media (min-width: $md) {
        display: none;
      }
    }

    .header-auth-wrapper {
      display: flex;
      align-items: center;

      .btn-transparent,
      .btn-primary {
        padding: 16px 40px;
      }

      .btn-primary {
        margin: 0 16px;

        @media (max-width: $md) {
          padding: 12px 24px;
        }
      }

      .btn-transparent {
        @media (max-width: $md) {
          display: none;
        }
      }

      .navbar-toggle {
        display: none;
        @media (min-width: $md) and (max-width: $lg) {
          display: block;
        }
      }
    }
  }

  .mobile-nav-dropdown-logout {
    .header-dropdown-main-links {
      .common {
        font-size: 18px;
        line-height: 20px;
        text-transform: none;
      }
    }

    .header-dropdown-btn-wrapper {
      border-top: 1px solid $neutralBorder;
      margin-top: 16px;

      .btn-transparent,
      .btn-primary {
        margin: 16px;
      }

      .btn-transparent {
        margin-bottom: 0;
      }
    }
  }
}
