%heading-font {
  font-style: normal;
  font-weight: normal;
  line-height: 110%;
}

%context-font {
  font-family: $font-context-regular;
  font-style: normal;
  font-weight: normal;
}

h1 {
  font-family: $font-header-bold;
  font-style: normal;
  font-weight: 300;
  line-height: 101%;
  font-size: 80px;

  @include media-breakpoint-down(md) {
    font-size: 64px;
  }

  @include media-breakpoint-down(sm) {
    font-size: 36px;
  }

  &.section-title {
    text-align: left;
    @media only screen and (min-width: 790px) {
      padding: 10% 10% 50px;
      margin: 0;
      background: $bg-tan-light;
    }

    @media only screen and (min-width: 1350px) {
      font-family: $font-header-regular;
      color: $color-black-1;
      line-height: 1.3;
      font-size: 65px;
      padding-bottom: 35px;
    }
  }
}

h2 {
  font-family: $font-header-regular;
  @extend %heading-font;
  font-size: 48px;
  color: $color-white;

  @include media-breakpoint-down(md) {
    font-size: 30px;
  }

  @include media-breakpoint-down(sm) {
    font-size: 21px;
  }
}

h3, .h3 {
  font-family: $font-header-extralight;
  @extend %heading-font;
  font-weight: 300;
  font-size: 48px;
  color: $color-black-1;

  .header-wrapper & {
    color: inherit;
    font-family: $font-header-regular;

    i {
      font-family: $font-header-extralight;
    }
  }

  @include media-breakpoint-up(lg) {
    .header-wrapper & {
      font-size: 55px;
    }
  }

  @include media-breakpoint-down(sm) {
    font-size: 21px;

    .header-wrapper & {
      font-size: 28px;
    }
  }
}

h4 {
  font-family: $font-header-regular;
  @extend %heading-font;
  font-size: 44px;

  @include media-breakpoint-down(lg) {
    font-size: 32px;
  }

  @include media-breakpoint-down(md) {
    font-size: 44px;
  }

  @include media-breakpoint-down(sm) {
    font-size: 24px;
  }
}

h5 {
  font-family: $font-header-regular;
  @extend %heading-font;
  font-size: 28px;
  color: $color-white;

  @include media-breakpoint-down(md) {
    font-size: 22px;
  }

  @include media-breakpoint-down(sm) {
    font-size: 14px;
  }
}

p {
  &.context-1 {
    @extend %context-font;
    font-size: 14px;
    line-height: 17px;

    @include media-breakpoint-down(sm) {
      font-size: 12px;
      line-height: 15px;
    }
  }

  &.context-2 {
    @extend %context-font;
    font-size: 14px;
    line-height: 110%;

    @include media-breakpoint-down(sm) {
      font-size: 12px;
    }
  }

  &.context-3 {
    @extend %context-font;
    font-size: 10px;
    line-height: 12px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    text-align: center;

    @include media-breakpoint-down(sm) {
      font-size: 8px;
      line-height: 10px;
    }
  }

  &.notification {
    font-size: 28px;
    font-family: $font-header-regular;
    margin-bottom: 0;
    margin-top: 5px;
    margin-left: 3px;

    @include media-breakpoint-down(sm) {
      margin-left: 1px;
      font-size: 14px;
    }
  }

  .description-wrapper & {
    font-size: 28px;
    margin-bottom: 0;

    @include media-breakpoint-down(sm) {
      font-size: 24px;
    }
  }
}

.description-paragraph,
.eyebrow {
  font-family: $font-context-regular;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 160%;

  @include media-breakpoint-down(md) {
    font-size: 16px;
  }

  @include media-breakpoint-down(sm) {
    font-size: 12px;
  }
}

.heading-small-section {
  @extend %heading-font;
  font-family: $font-context-regular;
  font-size: 18px;
  color: $color-gray;
  text-transform: uppercase;
  font-weight: normal;
  line-height: 1;

  @include media-breakpoint-down(sm) {
    font-size: 12px;
  }
}

.break-word {
  word-wrap: break-word;
}

.fs-16 {
  font-size: 16px;
}
