.free-trial {
  position: relative;

  .container-fluid {
    padding: 0;
  }

  &-img {
    margin-top: -7%;
    margin-bottom: -11%;

    @include media-breakpoint-down(sm) {
      margin-top: 0;
    }
  }

  &-content {
    z-index: -1;
    background-color: $bg-yellow-1;
    width: 100%;
    padding: 160px 15px 105px 15px;

    &__info {
      h1 {
        color: $color-black-1;
        text-align: center;
      }

      .description-paragraph {
        color: $color-black-1;
        text-align: center;
        margin-bottom: 50px;
      }
    }

    &__description {
      width: 75%;
      margin: 40px auto 0;
    }
  }
}

@include media-breakpoint-down(md) {
  .free-trial {
    &-content {
      padding: 100px 15px 65px 15px;
    }
  }
}

@include media-breakpoint-down(sm) {
  .free-trial {
    &-content {
      padding: 74px 15px 25px 15px;

      &__description {
        width: auto;
        margin: 20px auto 0;
      }

      &__info {
        .description-paragraph {
          font-size: 14px;
          line-height: 22.4px;
          margin-bottom: 15px;
        }
      }
    }
  }
}

.email-enroll-btn {
  float: right;
  margin: 15px 10px 20px 10px;
  background: #f18d7e !important;
  border: none;
  color: #fff !important;
  display: inline-block;
  padding: 9px 20px;
  font-size: 16px;
  font-family: protogrotesk-regular, sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  -o-transition: all 0.3s ease-in-out;
  border-radius: 18px;
}

.email-enroll-input {
  float: right;
  margin: 15px 10px 20px -34px;
  border: 1px solid #ece7e7;
  padding: 9px 5px 9px 20px;
  width: 300px;
  border-radius: 6px;
}

.em-error {
  border: 1px solid #a22d2d9c;
}

.email-enroll-text {
  display: block;
  float: left;
  margin: 25px 4%;
  font-family: AkkuratLLWeb-Regular, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
}

.email-enroll-success {
  color: green;
  display: none;
  margin: 25px 4%;
  float: right;
  font-family: AkkuratLLWeb-Regular, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
}

.email-enroll-wrapper {
  position: fixed;
  bottom: 0px;
  z-index: 100 !important;
  background-color: #faf9f7;
  border-top: 1px solid #ece7e7;
  width: 100%;
  right: 0px;

  .home-top-banner-inner-wrapper-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
  }

  .email-enroll-text {
    margin: 8px 16px 8px 0;
  }

  .home-top-banner-inner-wrapper-content-form {
    @media (max-width: 467px) {
      width: 100%;
    }

    > form {
      display: flex;
      flex-wrap: wrap;

      @media (max-width: 467px) {
        width: 100%;
        flex-direction: column;
      }

      .email-enroll-btn {
        margin: 0 0 0 16px;

        @media (max-width: 467px) {
          margin: 16px 0 0 0;
          width: 100%;
        }
      }

      .email-enroll-input {
        margin: 0;
        order: -1;

        @media (max-width: 467px) {
          width: 100%;
        }
      }
    }
  }
}
