.step-content {
  .btn-circle {
    text-align: center;
    white-space: normal;
    display: inline-block;
  }

  label {
    margin-bottom: 0;
  }

  .image-checkbox {
    display: block;

    input[type="checkbox"] {
      display: none;
    }
  }

  .item-checked {
    .onboarding-thumbnail__image {
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(241, 141, 126, 0.8), rgba(241, 141, 126, 0.8));
        border-radius: 10px;
      }
    }
  }

  .btn-mb {
    z-index: 1;
    margin: 0 auto;
  }
}

.onboarding {
  &-info {
    height: 100%;
    padding: 0 0 45px 20px;

    &__button {
      margin-top: 35px;

      p {
        color: $color-pink-dark;
        display: inline-block;
        padding-left: 45px;
        text-transform: uppercase;

        a {
          color: inherit;
          display: inline-block;
        }
      }
      .onboarding-later-link {
        cursor: pointer;
      }
    }
  }

  &-thumbnail {
    &-item {
      position: relative;
      margin-bottom: 20px;
      transition: box-shadow 0.5s ease;
      border-radius: 10px;

      @include media-breakpoint-up(md) {
        &:hover {
          cursor: pointer;
          box-shadow: 0 0 0 5px $border-pink-1;

          .image-checkbox {
            cursor: pointer;
          }
        }
      }
    }

    &__image {
      position: relative;
    }

    &__desc {
      position: absolute;
      left: 20px;
      bottom: 15px;

      @include media-breakpoint-between(sm, md) {
        left: 10px;
      }

      h5 {
        margin: 0;
        text-shadow: 0 0 5px #bbb;
      }

      p {
        margin-bottom: 0;
        margin-left: 1px;
        color: $color-white;
        font-family: $font-header-regular;
      }
    }

    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      object-fit: cover;
    }

    .slick-slide {
      outline: none;
    }
  }
}

@include media-breakpoint-down(md) {
  .step-content {
    h3 {
      font-size: 30px;
    }
  }

  .onboarding {
    &-info {
      &__button {
        p {
          padding-left: 15px;
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .step-content {
    h3 {
      font-size: 36px;
    }

    h5 {
      font-size: 28px;
    }

    .description-paragraph,
    .eyebrow {
      font-size: 14px;
    }

    .slick-list {
      padding: 0 20% 0 0 !important;
    }

    .btn-circle {
      display: block;
      width: 100%;
      max-width: 90px;
      max-height: 90px;
      margin: 0 10px;
    }
  }

  .onboarding {
    &-info {
      padding: 0;

      &__button {
        &.btn-mb {
          display: flex !important;
          position: relative;
          width: 100%;
          padding-top: 25px;
          text-align: center;
          align-items: center;
        }

        .error {
          position: absolute;
          top: 0;
          left: 50%;
          transform: translateX(-50%);
        }

        p {
          flex-basis: 50%;
          padding: 0;
          margin: 0;
        }
      }
    }

    &-thumbnail {
      padding: 50px 0 30px 0;
    }
  }
}

@media (max-device-width: 350px) {
  .step-content {
    h5 {
      font-size: 20px;
    }
  }
}
