.onboarding {
  .onboarding-plan {
    &-promotional {
      display: flex;
      flex-direction: column;
      height: 100%;
      .same-height-item {
        flex-grow: 1;
        .onboarding-plan-item-wrapper {
          height: 100%;
        }
      }
      @include media-breakpoint-down(sm) {
        display: none;
      }
    }
    &-item {
      &.onboarding-plan-promo {
        .img-wrapper {
          position: relative;
        }
      }

      .title-wrapper {
        position: relative;
        z-index: 1;

        li {
          color: $color-white;
        }
      }

      .img-wrapper {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }

  &-subscription {
    margin-top: 20px;

    &-switch {
      margin-top: 20px;
      text-align: center;

      a {
        display: inline-block;
        padding: 0 20px;
        color: $color-black-1;
        text-transform: uppercase;
        text-decoration: underline !important;
        font-size: 14px;
      }
    }

    &-item {
      display: none;
      padding: 10px 20px;
      border: 2px solid $border-pink-1;
      border-radius: 25px;
      overflow: hidden;

      @include media-breakpoint-down(sm) {
        border-radius: 15px;
        padding: 10px;
      }

      span {
        display: block;
        color: $color-pink-dark;
      }

      &.active {
        display: block;
      }
    }

    span {
      font-size: 18px;
      font-family: $font-header-regular;

      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }

    h5 {
      font-size: 18px;
      color: $color-black-1;
      font-family: $font-context-regular;
      line-height: 160%;

      br {
        display: none;
      }

      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }

    .description-paragraph {
      margin-top: 10px;

      * {
        line-height: 160%;
      }

      @include media-breakpoint-down(sm) {
        display: none;
      }
    }
  }

  &-plan {
    .title-wrapper {
      padding: 25px;
      margin-bottom: 0;

      ul {
        li {
          font-size: 14px;
        }
      }
    }

    input[type="radio"] {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      z-index: 2;
    }

    .onboarding-plan-item-wrapper {
      border-radius: 15px;
      transition: box-shadow 0.5s ease;
    }

    &-item {
      position: relative;
      margin: 24px 0;

      &.active {
        .onboarding-plan-item-wrapper {
          box-shadow: 0 0 0 5px $border-pink-1;
        }
      }

      &:hover {
        @include media-breakpoint-up(xl) {
          .onboarding-plan-item-wrapper {
            box-shadow: 0 0 0 5px $border-pink-1;
          }
        }
      }

      .description-paragraph {
        @include media-breakpoint-up(md) {
          font-size: 14px;
        }
      }
    }

    &-primary {
      .onboarding-plan-item-wrapper {
        height: 547px;
      }

      .title-wrapper {
        padding-top: 60px;
      }
    }

    &-secondary {
      margin-top: 25px;

      .onboarding-plan-item-wrapper {
        background-color: $bg-tan-2;
      }

      .title-wrapper {
        padding-top: 35px;
      }
    }
  }
}

.modal-onboarding-tfa,
.modal-onboarding-tfa-success {
  .modal-header {
    > h3 {
      text-align: center;
    }

    .description-paragraph {
      line-height: 20px;
      color: $color-black-5;
      padding-top: 16px;

      > p {
        margin: 0;

        &:last-child {
          padding-top: 8px;
          color: $color-gray-9;
        }
      }
    }
  }

  .modal-footer {
    .btn-orange {
      background: $bg-pink;
      border-radius: 56px;
      padding: 10px 48px;
      font-family: $font-1;
      font-size: 16px;
      line-height: 18px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $color-white;
    }

    .modal-description {
      .btn-orange {
        font-family: $font-1;
        border: none;
        background: transparent;
        font-size: 18px;
        line-height: 18px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: $color-green-2;
        padding: 0;

        &:disabled {
          color: $color-gray-9;
          text-transform: none;
          font-family: $font-2;
        }
      }
    }
  }
}

.modal-onboarding-tfa-success {
  .modal-header {
    border-bottom: none;
    text-align: center;
  }

  .modal-body {
    text-align: center;
  }

  .modal-footer {
    justify-content: flex-end;
  }
}

.phone-form-wrapper {
  align-items: flex-start;

  input[name="phone_number"] {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .country-code-wrapper {
    position: relative;
    cursor: pointer;
    padding-top: 3px;

    @include media-breakpoint-down(lg) {
      padding-top: 2px;
    }

    .country-code-select {
      display: none;
      position: absolute;
      width: fit-content;
      background: $bg-tan-light;
      box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
      border-radius: 16px;
      z-index: 1000;
      top: 70px;
      max-height: 410px;
      overflow-y: auto;
      overflow-x: hidden;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        display: none;
      }

      @include media-breakpoint-down(sm) {
        top: 50px;
      }

      > li {
        display: flex;
        padding: 24px;
        border-bottom: 1px solid $border-grey-10;
        justify-content: space-between;
        align-items: center;
        font-size: 28px;
        line-height: 110%;

        @include media-breakpoint-down(lg) {
          font-size: 22px;
        }
      }

      .country-name-wrapper {
        display: flex;
        align-items: center;

        .country-name {
          font-family: $font-1;
          line-height: 110%;
          color: $color-black-5;
          padding: 0 16px;

          @include media-breakpoint-down(lg) {
            padding: 0 10px;
          }

          @include media-breakpoint-down(sm) {
            word-break: break-word;
          }
        }
      }

      .country-code {
        font-family: $font-1;
        line-height: 110%;
        color: $color-gray-9;
      }
    }
  }

  .country-code-chosen-item {
    display: flex;
    justify-content: space-between;
  }

  .country-code-item-search {
    display: none;
    padding-top: 5px;
    @include media-breakpoint-down(lg) {
      padding-top: 8px;
    }
  }
}
