//Reset Css
textarea {
  resize: none;
}

ul {
  padding: 0;
  margin: 0 auto;
  list-style: none;

  a {
    text-decoration: none;
  }
}

img {
  max-width: 100%;
}

a:hover,
a:focus {
  outline: none;
}

input {
  &:focus {
    outline: none;
  }
}

button {
  &:focus {
    outline: none;
  }
}
//End Reset Css

//Fix issue plugin on WP
img[width="1"][height="1"] {
  left: -1px;
  position: absolute;
  z-index: -1;
}

// End Fix issue plugin on WP

%txt-btn {
  color: $color-pink-dark;
  background: transparent;
  padding: 0;
  font-size: 28px;
  font-family: $font-header-regular;
  line-height: 110%;

  &:focus {
    box-shadow: none;
  }

  @include media-breakpoint-down(sm) {
    font-size: 24px;
  }
}

body {
  background: $bg-white-1;
}

footer {
  max-width: $max-width;
  margin: 0 auto;
}

.modal-dialog-centered {
  display: flex;
  align-items: center;

  @include media-breakpoint-down(sm) {
    margin: 0;
  }
}

.header-main {
  max-width: $max-width;
  margin: 0 auto;
}

.content-width {
  position: relative;
  width: 100%;
}

.wrapper {
  max-width: $max-width;
  margin: 0 auto;

  &-ml {
    position: relative;

    @include media-breakpoint-up(md) {
      margin-left: calc(132 / 1440 * 100%);
    }
  }

  a {
    color: $color-white;

    &:hover {
      text-decoration: none;
    }
  }

  .jspHorizontalBar {
    display: none;
  }

  .jspTrack {
    background-color: $bg-grey;
  }

  .jspDrag {
    background: $bg-pink;
    border-radius: 15px;
  }
}

.wrapper-heading {
  display: flex;
  position: relative;
  margin-bottom: 40px;
  padding-left: calc(40 / 1440 * 100%);
  justify-content: space-between;
  align-items: center;

  h3 {
    margin: 0;
  }

  .heading-small-section {
    color: $color-gray;
  }
}

.btn-transparent {
  background: none;
  border: none;
  padding: 0;
}

// Simple title body widget
.simple {
  &-heading {
    width: 75%;
    margin: 50px auto;
    text-align: center;

    .description-paragraph {
      @include media-breakpoint-up(md) {
        display: inline-block;
        width: 85%;
      }
    }

    h3 {
      @include media-breakpoint-up(md) {
        margin-bottom: 35px;
      }
    }

    p {
      margin: 0;
      color: $color-black-1;
    }
  }
}

//Decrease spacing grid
.grid-space-small {
  .row {
    margin-left: -10px;
    margin-right: -10px;
  }

  [class*="col-"] {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.bg-linear {
  background: linear-gradient(
    180deg,
    rgba(16, 85, 82, 0.05) 0%,
    rgba(250, 249, 247, 0.05) 100%
  );
}

.bg-green {
  background: $bg-green;
}

.bg-pink {
  background: $bg-pink;
}

.full-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.action-list {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  width: 100%;
  max-width: 400px;
  background: $bg-tan-light;
  border-radius: 15px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.4);
  transition: all ease-in-out 0.5s;
  z-index: 1;
  pointer-events: none;

  @include media-breakpoint-down(sm) {
    width: 100%;
  }

  &.active {
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }

  &-item {
    padding: 20px 30px;
    border-bottom: 1px solid $border-grey-1;

    @include media-breakpoint-down(sm) {
      padding: 15px 30px;
    }

    &.active {
      a,
      button {
        color: $border-pink-1;
      }
    }

    a,
    button {
      display: inline-block;
      font-size: 28px;
      font-family: $font-header-regular;
      font-weight: 400;
      color: $color-black-1;
      transition: all ease-in-out 0.3s;

      &:hover {
        text-decoration: none;
        color: $border-pink-1;
      }

      @include media-breakpoint-down(md) {
        font-size: 20px;
      }

      @include media-breakpoint-down(sm) {
        font-size: 16px;
      }
    }
  }
}

//Define background bubble
.bubble-hot {
  background: $bubble-bg-hot;
}

.bubble-hot-light {
  background: $bubble-bg-hot-light;
}

.bubble-green {
  background: $bubble-bg-green;
}

.bubble-green-light {
  background: $bubble-bg-green-light;
}

.bubble-green-full {
  background: $bubble-bg-green-full;
}

.bubble-green-hot {
  background: $bubble-bg-green-hot;
}

.bubble-pink {
  background: $bubble-bg-pink;
}

.bubble-pink-light {
  background: $bubble-bg-pink-light;
}

.bubble-pink-dark {
  background: $bubble-bg-pink-dark;
}

.bubble-red {
  background: $bubble-bg-red;
}

.bubble-red-dark {
  background: $bubble-bg-red-dark;
}

// loading
.img-loader {
  position: fixed;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: #ececec;
  opacity: 0.9;
  z-index: 190;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 196px;
    width: 196px;

    @include media-breakpoint-down(sm) {
      height: 128px;
      width: 128px;
    }
  }
}

.zoom-effect {
  transform: scale(1);
  transition: transform 250ms ease-in-out;
  -webkit-font-smoothing: subpixel-antialiased;
  transform-origin: center center;
  backface-visibility: hidden;

  @include media-breakpoint-up(lg) {
    &:hover {
      transform: scale(1.1) translateZ(0);
    }
  }
}

.link-hover-effect {
  display: inline-block;
  transition: all 0.3s ease-in-out;

  &:hover {
    transform: scale(1.05);
    color: $color-pink-dark !important;
  }
}

.box-filter {
  min-height: 90vh;

  @include media-breakpoint-down(sm) {
    min-height: 100vh;
  }

  @include media-breakpoint-down(xs) {
    min-height: 120vh;
  }
}

.icon-plus {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
  padding: 20px;
  border: 0;
  background: transparent;

  &:before,
  &:after {
    content: "";
    position: absolute;
    background: $bg-pink;
    border-radius: 15px;
    transition: all 0.5s;
  }

  &:before {
    left: 50%;
    top: 4px;
    bottom: 4px;
    width: 4px;
    transform: translateX(-50%) rotate(-90deg);
  }

  &:after {
    top: 50%;
    left: 4px;
    right: 4px;
    height: 4px;
    transform: translateY(-50%) rotate(-90deg);
  }

  @include media-breakpoint-down(sm) {
    padding: 18px;
  }

  &--gray {
    &:before,
    &:after {
      content: "";
      position: absolute;
      background: $bg-grey-3;
      border-radius: 15px;
      transition: all 0.5s;
    }
  }
}

.icon-close {
  position: relative;
  margin-top: 0;
  margin-left: 10px;
  width: 15px;
  height: 15px;
  cursor: pointer;

  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 8px;
    width: 13px;
    height: 1px;
    background-color: $bg-pink;
    transform: rotate(-45deg);
  }

  &:after {
    transform: rotate(45deg);
  }
}

.btn-free-trial {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  width: 304px;
  height: 46px;
  background-color: $bg-pink;
  border-radius: 30px;

  @include media-breakpoint-down(sm) {
    width: 186px;
    height: 34px;
  }

  &.free-trial-text {
    font-family: $font-header-regular;
    font-style: normal;
    font-weight: normal;
    font-size: 23.2846px;
    line-height: 29px;
    letter-spacing: 0.06em;
    text-transform: uppercase;

    @include media-breakpoint-down(sm) {
      font-size: 14px;
      line-height: 17px;
    }
  }
}

// TagsInput, AutoComplete
.tagsinput-wrapper {
  .ui {
    &-menu {
      width: 385px !important;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
      border-radius: 15px;
      max-height: calc(100vh - 200px);
      overflow-x: hidden;
      overflow-y: scroll;

      @include media-breakpoint-down(sm) {
        width: 90% !important;
        left: 0 !important;
        top: 100% !important;
      }

      &-item {
        position: relative;
        padding: 0 30px;

        @include media-breakpoint-down(sm) {
          padding: 0 20px;
        }

        &:not(:first-child) {
          .ui-menu-item-wrapper {
            &:before {
              content: "";
              position: absolute;
              left: 50%;
              top: 0;
              width: 100%;
              height: 1px;
              transform: translateX(-50%);
              background: $bg-grey;
            }
          }
        }

        &-wrapper {
          font-family: $font-header-regular;
          font-style: normal;
          font-weight: normal;
          font-size: 28px;
          line-height: 110%;
          color: $color-black-1;
          padding: 30px 0;

          @include media-breakpoint-down(sm) {
            padding: 20px 0;
            font-size: 18px;
          }
        }
      }
    }

    &-widget-content {
      .ui-state-active {
        color: $color-pink-dark;
        border: none;
        background: none;
      }
    }

    &-autocomplete-category {
      font-family: $font-header-regular;
      font-weight: bold;
      line-height: 110%;
      font-size: 28px;
      padding: 30px 0 30px 30px;

      @include media-breakpoint-down(sm) {
        padding: 20px 0 20px 20px;
        font-size: 18px;
      }
    }
  }

  .tagsinput {
    display: block;
    background: no-repeat;
    border: none;

    .tag,
    input {
      font-family: $font-context-regular;
      color: $color-gray-1;
      line-height: 1;
    }

    .tag {
      display: inline-block;
      background: $bg-blue-1;
      color: $color-black-1;
      text-transform: capitalize;
    }
  }
}

@include media-breakpoint-down(md) {
  .wrapper-heading {
    padding-left: 0;
  }
}

@include media-breakpoint-down(sm) {
  .wrapper-heading {
    margin-bottom: 22px;
  }

  .simple {
    &-heading {
      width: auto;
      margin: 40px 30px;

      h3 {
        font-size: 28px;
        padding: 0 25px;
      }

      p {
        font-size: 14px;
      }
    }
  }
}
