@mixin transform($command) {
  -webkit-transform: $command;
    -moz-transform: $command;
      -o-transform: $command;
         transform: $command;
}

@mixin initFuzzyBubble($width, $height) {
  width: $width;
  height: $height;
  border-radius: 50%;
  background: $bubble-bg-hot-light;
}

@mixin initBubbleSize($width, $height) {
  width: $width;
  height: $height;
  border-radius: 50%;
}

@mixin initSize($width, $height) {
  width: $width;
  height: $height;
}

@mixin animateCountDown($count) {
  $step: (100 / $count);
  $progress: 0%;
  $translate: -$digit-height;
  @while $progress < 100 {
    #{$progress} { transform: translateY($translate); }
    $progress: $progress + $step;
    $translate: $translate - $digit-height;
  }
}
