.card-bubble {
  &-item {
    position: relative;
    margin-bottom: 20px;

    &-img {
      img {
        width: 100%;
        border-radius: 15px;
      }
    }

    .demand-item-bubble-item {
      display: flex;
      position: absolute;
      top: 30px;
      left: 30px;
      margin: 0;

      @include media-breakpoint-down(md) {
        top: 15px;
        left: 15px;
      }

      &__wrapper {
        @include media-breakpoint-down(md) {
          @include initSize(40px, 40px);
        }
      }
    }

    .demand-item-bubble-title {
      margin-left: 25px;
      align-self: center;

      @include media-breakpoint-down(md) {
        margin-left: 10px;
      }

      h5 {
        color: $color-black-3;
        margin: 0
      }

      p {
        margin: 0;
        font-size: 14px;
      }
    }
  }
}