.modal-account-plan-management {
  h3 {
    font-size: 38px;

    @include media-breakpoint-down(sm) {
      font-size: 30px;
    }

    @include media-breakpoint-down(xs) {
      font-size: 24px;
    }

    &[data-header-plan] {
      @include media-breakpoint-down(xs) {
        position: absolute;
        top: 40px;
        left: 0;
        text-align: center;
        width: 100%;
        font-size: 24px;
      }
    }
  }

  .modal-content {
    @include media-breakpoint-down(xs) {
      padding: 10px;
    }
  }

  .modal-header {
    @include media-breakpoint-down(xs) {
      padding-bottom: 15px;
    }
  }

  .modal-body {
    @include media-breakpoint-down(xs) {
      padding-top: 15px;
    }
  }

  .modal-footer {
    @include media-breakpoint-down(xs) {
      padding-top: 0;
    }
  }

  .modal-dialog {
    max-width: 680px;
  }

  .radio-wrapper {
    text-align: left;

    [type="radio"]:checked,
    [type="radio"]:not(:checked) {
      position: absolute;
      left: -9999px;
    }

    [type="radio"]:checked + span,
    [type="radio"]:not(:checked) + span {
      position: relative;
      padding-left: 35px;
      cursor: pointer;
      font-family: $font-1;
      font-size: 20px;
      color: $color-black-5;
    }

    [type="radio"]:checked + span:before,
    [type="radio"]:not(:checked) + span:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 24px;
      height: 24px;
      border: 1px solid $border-grey-9;
      border-radius: 100%;
      background: $bg-white-1;
    }

    [type="radio"]:checked + span:after,
    [type="radio"]:not(:checked) + span:after {
      content: "";
      width: 14px;
      height: 14px;
      background: $color-pink-dark;
      position: absolute;
      top: 5px;
      left: 5px;
      border-radius: 100%;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;
    }

    [type="radio"]:not(:checked) + span:after {
      opacity: 0;
      -webkit-transform: scale(0);
      transform: scale(0);
    }

    [type="radio"]:checked + span:after {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  .radio-desc {
    padding-left: 35px;
    margin-bottom: 15px;
    text-align: left;
    font-family: $font-header-light;

    ul {
      list-style-type: circle;
      margin-left: 18px;
    }
  }

  .cancellation-radios {
    display: block;
    text-align: left;

    [type="radio"]:checked + span,
    [type="radio"]:not(:checked) + span {
      padding-top: 2px;
      font-family: $font-header-regular;
      font-size: 16px;
    }

    .radio-wrapper {
      margin-bottom: 10px;
    }
  }

  .btn-action {
    font-family: $font-header-regular;
    color: $color-pink-dark;
    font-size: 18px;

    &:hover {
      text-decoration: none;
    }

    @include media-breakpoint-down(sm) {
      font-size: 15px;
      width: 100%;
      text-align: center;
    }
  }

  .btn-cta {
    margin-left: auto;
    padding: 10px 40px;
    border-radius: 30px;
    font-size: 18px;
    color: $color-white;
    background-color: $bg-pink;
    text-transform: uppercase;

    @include media-breakpoint-down(sm) {
      padding: 10px 20px;
      font-size: 15px;
    }

    &:disabled {
      background-color: $color-gray-6;
      opacity: .7;
    }
  }

  .modal-header {
    display: flex;
    align-items: center;
    border: 0;
  }

  .btn {
    &-close {
      position: static;
      margin: 0;
      padding: 0;
    }

    &-back {
      border: 0;
      opacity: 0.75;
      background: transparent;
      text-transform: uppercase;
      font-family: $font-header-extralight;
      font-weight: 600;

      &:hover {
        opacity: 1;
      }
    }
  }

  .form-error-msg {
    margin-top: 15px;
    margin-bottom: 0;
  }

  .form-footnote {
    display: none;
    width: 100%;
    padding-top: 10px;
    margin-top: 15px;
    font-family: $font-header-extralight;
    font-size: 14px;
    font-weight: 600;
    color: $color-gray-6;
    border-top: 1px solid $border-grey-1;

    span {
      font-family: $font-header-regular;
      font-weight: 300;
    }
  }

  .account-plan-deactivate {
    &-footer {
      justify-content: center;

      * {
        display: inline-block;
        margin: 0 10px;
      }
    }
  }
}

.account-plan {
  &-title {
    padding: 0 20px;
    text-align: center;

    @include media-breakpoint-down(xs) {
      padding: 0;
    }

    p {
      margin-top: 20px;
    }
  }

  &-desc {
    font-size: 14px;

    @include media-breakpoint-down(xs) {
      margin-bottom: 15px !important;
    }
  }

  &-total {
    margin-top: 30px;
    font-size: 20px;
    text-align: right;
    font-family: $font-header-regular;

    @include media-breakpoint-down(xs) {
      margin-top: 0;
      font-size: 18px;
    }

    .renews-note {
      color: $color-gray-6;
      font-size: 12px;
      font-style: italic;
    }

    .total-detail, .account-discount {
      margin-top: 10px;
      font-size: 16px;
      color: $color-pink-dark;

      p {
        margin-top: 10px;
      }
    }

    .pay-now {
      margin-top: 10px;
    }
  }
}

.account-plan-select {
  &-item {
    position: relative;

    input[type="radio"] {
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      z-index: 2;
      cursor: pointer;

      &:checked {
        ~ .account-plan-select-item-wrapper {
          background-color: $bg-green-3;
        }
      }
    }

    p {
      margin-bottom: 0;
    }

    &-wrapper {
      display: flex;
      padding: 20px 25px;
      border-radius: 15px;
      overflow: hidden;
      align-items: center;
      justify-content: space-between;

      @include media-breakpoint-down(sm) {
        padding: 20px 15px;
      }
    }

    &-info {
      @include media-breakpoint-down(sm) {
        margin-right: 5px;
      }

      @include media-breakpoint-down(xs) {
        display: block;
        padding: 15px 10px;
      }
    }

    &-status {
      font-style: italic;

      &--pink {
        color: $color-pink-dark;
      }
    }

    &-title {
      font-size: 22px;
      font-family: $font-header-regular;

      @include media-breakpoint-down(sm) {
        font-size: 20px;
      }
    }

    &-desc {
      li {
        display: block;

        &:before {
          content: "";
          display: inline-block;
          margin-right: 5px;
        }
      }
    }

    &-price {
      font-size: 28px;
      font-family: $font-header-regular;

      @include media-breakpoint-down(xs) {
        text-align: center;
        font-size: 20px;
      }
    }
  }

  .no-one-time-available-message {
    color: $color-pink-dark;
    text-align: center;
  }
}

.account-plan-card {
  .ico-cards {
    left: 50%;
    transform: translate(-50%, -50%);
  }

  p {
    margin-bottom: 0;
  }

  .icon-plus {
    padding: 14px;
    margin-left: -3px;
    margin-right: 25px;
  }

  &-wrapper {
    padding: 0 25px;

    @include media-breakpoint-down(sm) {
      padding: 0;
    }

    @include media-breakpoint-down(xs) {
      padding: 0 5px;
    }
  }

  &-list,
  &-form {
    padding: 40px 0;
  }

  &-list {
    text-align: center;

    .form-radio {
      .form-option {
        padding-left: 50px;
        margin-bottom: 0;
      }

      .checkedbox {
        top: 50%;
        transform: translateY(-50%);
      }

      ~ .form-radio {
        margin-top: 20px;
      }
    }

    .card-tag {
      display: inline-flex;
      padding-right: 10px;
      border-radius: 5px;
      align-items: center;
      overflow: hidden;
      box-shadow: 0 0 0 1px #000 inset;
      font-size: 18px;
    }

    .ico-cards {
      display: inline-flex;
      position: relative;
      left: auto;
      padding-right: 0;
      margin-right: 30px;
      transform: unset;

      &:after {
        content: "";
        display: inline-block;
        position: absolute;
        right: -30px;
        top: 50%;
        margin-right: 5px;
        transform: translateY(-50%);
      }
    }
  }

  &-add {
    display: inline-flex;
    margin-top: 20px;
    align-items: center;
    cursor: pointer;

    .text {
      position: relative;
      display: inline-block;
      font-size: 18px;

      @include media-breakpoint-down(xs) {
        font-size: 16px;
      }

      &:after {
        content: "";
        position: absolute;
        width: 100%;
        bottom: -1px;
        left: 0;
        background: $bg-grey-3;
        height: 2px;
      }
    }
  }
}

.account-plan-status {
  max-width: 500px;
  margin: auto;
  text-align: center;

  img {
    width: 100px;
    margin-bottom: 30px;
  }

  h3 {
    margin-bottom: 30px;
  }
}

.account-plan-deactivate {
  &-content {
    padding: 30px 0;
  }

  textarea {
    padding: 20px;
    font-size: 18px;
    border-radius: 15px;
    color: $color-gray-6;
    font-family: $font-header-regular;
    font-style: italic;

    &:focus {
      color: $color-gray-6;
    }

    &::placeholder,
    &::-webkit-input-placeholder {
      color: $color-gray-6;
      opacity: 0.75;
    }
  }
}

.promo-code {
  &-mess {
    font-style: italic;
    color: $color-pink-dark;
    text-align: right;
  }

  &-label {
    position: relative;
    display: flex;
    line-height: 1;
    border: 1px solid $border-pink-1;
    border-radius: 5px;
    padding: 5px 8px;
    margin: 0;
  }

  &-text {
    color: $color-pink-dark;
  }
}

.account-plan-vote {
  display: flex;
  justify-content: center;

  .img-wrapper {
    position: relative;
    margin: 0 40px;
    cursor: pointer;

    @media (max-width: 320px) {
      margin: 0 25px;
    }
  }

  img {
    transition: transform 0.25s ease-in-out;

    @include media-breakpoint-up(lg) {
      &:hover {
        transform: scale(1.1) translateZ(0);
      }
    }
  }

  input[type="radio"] {
    position: absolute;
    opacity: 0;
    height: 0;
    width: 0;

    &:checked {
      ~ img {
        transform: scale(1.1) translateZ(0);
      }
    }
  }
}
