[data-global-search] {
  .simple-heading {
    font-family: $font-header-extralight;
    font-weight: 300;
    font-size: 48px;
    color: $color-black-1;
    word-break: break-word;
  }

  .search-not-found {
    text-align: center;
    font-family: $font-header-extralight;
    font-weight: 300;
    font-size: 32px;
    color: $color-black-1;

    @include media-breakpoint-down(sm) {
      font-size: 22px;
    }
  }

  .search-results {
    margin-bottom: 5em;
  }

  .search-cta-result {
    flex: 1 0 100%;
    color: $bg-red;
  }

  .coming-soon {
    animation: unset;
    transform: none;

    span {
      font-size: 14px;
      font-weight: 700;
      text-transform: capitalize;
      color: $color-gray-6;
    }
  }

  .live-content {
    &-wrap {
      .col-md-4 {
        &:after {
          content: '';
          position: absolute;
          top: 10%;
          right: 0;
          width: 1px;
          height: 80%;
          background-color: $border-grey-2;
        }
      }
    }

    &-item {
      padding: 25px 0;

      &__content {
        display: none;
      }
    }

    &__avatar {
      max-width: 50px;
      min-width: 50px;
      height: 50px;
    }
  }

  @include media-breakpoint-down(sm) {
    .yah-carousel-widget {
      padding-bottom: 0;

      .demand-item {
        padding-bottom: 25px;
      }
    }

    .simple-heading {
      margin: 25px 5px 15px 5px;
      font-size: 24px;
    }

    .center-control-slick {
      .wrapper-heading {
        margin-bottom: 0;
      }
    }

    .live {
      &-schedule-wrapper {
        position: relative;

        .slick-list {
          padding: 0 20% 0 0 !important;
        }
      }

      &-content-item__description {
        margin-left: 10px;
      }

      &-content-item__schedule {
        width: 30%;

        &-time {
          font-size: 24px;
        }
      }
    }
  }
}
