.plan-item {
  position: relative;
  margin-bottom: 16px;

  .btn-cta {
    display: inline-block;
    color: $color-pink-dark;
    text-transform: uppercase;

    a {
      color: inherit;
    }
  }

  ul {
    list-style: disc;
    padding: 20px;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 70%;
    height: 100%;
    border-radius: 24px;
  }

  &__info {
    padding: 60px 0 60px 65px;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    position: relative;

    .description-paragraph {
      color: $color-black-1;
    }
  }

  &__description {
    display: flex;
    flex-wrap: wrap;
  }

  &__price {
    font-family: $font-header-regular;
    font-weight: 300;
    font-size: 80px;
    color: $color-pink-dark;
    line-height: 110%;

    &_wrapper {
      display: flex;
      margin-top: 32px;
      flex-wrap: wrap;
      position: relative;
    }

    &_full {
      text-decoration-line: line-through;
      font-size: 32px;
      line-height: 110%;
      color: $color-gray-9;
      font-weight: bold;
    }

    span {
      font-size: 40px;
    }
  }

  &__discount {
    background-image: url(../images/plan_price_discount-1.svg);
    width: 140px;
    height: 140px;
    min-width: 140px;
    position: relative;
    margin-left: 16px;

    &_value, &_text {
      font-size: 32px;
      line-height: 110%;
      font-family: $font-1;
      color: $color-white;
      position: absolute;
      transform: rotate(25deg);
    }

    &_value {
      top: 38px;
      left: 32px;
      text-align: center;
    }

    &_text {
      text-transform: uppercase;
      top: 76px;
      left: 36px;
    }
  }

  &__thumbnail {
    height: 100%;

    img {
      border-radius: 0px 22px 22px 0px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 60% 100%;
    }

    .plan-bubble {
      position: absolute;
      bottom: 60px;
      right: 54px;
    }

    .bubble {
      &-circle {
        @include initBubbleSize($width: 150px, $height: 150px);
      }

      &-content {
        width: 100%;
        text-align: center;

        p {
          margin-top: 0;
          margin-bottom: 0;
          font-family: $font-header-regular;
          font-size: 22px;
          line-height: 110%;
          color: $color-white-1;
        }

        span {
          white-space: nowrap;
        }
      }
    }
  }
}

@include media-breakpoint-down(md) {
  .plan-item {
    &__info {
      padding: 40px 0 40px 30px;
    }

    &__price {
      font-size: 64px;
    }

    &__thumbnail {
      .plan-bubble {
        bottom: 10px;
        right: 30px;
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .plan-row {
    overflow-x: hidden;

    .container-fluid {
      padding: 0;
    }
  }

  .plan-item {
    margin-bottom: 0;
    margin-right: 12px;
    max-width: 352px;
    width: 100%;

    .col-md-6 {
      &:nth-child(2n) {
        order: 1;
      }

      &:nth-child(2n+1) {
        order: 2;
      }
    }

    &__bg {
      width: 100%;
      border-radius: 8px;
    }

    &__info {
      display: block;
      text-align: center;
      padding: 30px 20px 0 25px;

      h3 {
        font-size: 24px;
      }
    }

    &__description {
      display: block;
    }

    &__price {
      font-size: 48px;
      margin-bottom: 0;

      span {
        font-size: 24px;
      }
    }

    &__thumbnail {
      img {
        border-radius: 8px 8px 0px 0px;
      }
    }
  }
}

.plan-wrapper {
  .plan-item__discount {
    position: absolute;
    right: calc(100% - 420px);
    top: -100px;
    z-index: 55;

    @include media-breakpoint-down(sm) {
      top: -214px;
      right: 0;
    }
  }

  .plan-item__billed {
    font-size: 40px;
    color: $color-gray-9;
    font-weight: 700;
    line-height: 110%;


  }@include media-breakpoint-down(sm) {
    font-size: 28px;
    }
}