// Header
.header-new {
  .header-main__wrapper {
    margin-left: 0;

    .header-main-desktop {
      padding: 24px 16px;

      .navbar-brand {
        padding-bottom: 0;
      }

      .navbar-list__wrapper {
        position: static;
      }
    }

    .menu-item {
      a {
        border: 2px solid $color-black-6;
        border-radius: 28px;
        font-family: $font-gotham;
        font-weight: 400;
        font-size: 16px;
        line-height: 18px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: $color-black-6;
        padding: 6px 20px;

        &:hover,
        &:focus {
          background: none;
          color: $color-orange-2;
          border: 2px solid $color-orange-2;
        }

        &:active {
          background: $bg-white-2;
          color: $color-orange-2;
          border: 2px solid $color-orange-2;
        }
      }
    }

    .menu-item.active {
      a {
        background: $bg-orange-2;
        border-radius: 100px;
        border: 2px solid $bg-orange-2;
        color: $color-white;

        &:hover,
        &:focus {
          background: $bg-orange-3;
          border: 2px solid $bg-orange-3;
        }

        &:active {
          background: $bg-orange-4;
          border: 2px solid $bg-orange-4;
        }
      }
    }

    .header-main-mobile {
      .navbar-brand__wrapper {
        width: auto;
      }

      .navbar-list {
        padding: 32px 16px 64px;
        background: $bg-white-1;

        .navbar-subscribe {
          box-shadow: 0 0 20px rgba(137, 81, 0, 0.25);
          border-radius: 28px;
          overflow: hidden;

          &-title {
            padding: 16px 0;
            font-family: $font-1;
            font-size: 32px;
            line-height: 40px;
            text-align: center;
            color: $color-black-5;
          }

          .menu-item {
            &::before {
              height: 0;
            }

            a {
              font-size: 20px;
              line-height: 18px;
              font-family: $font-1;
              width: 100%;
              padding: 20px 0;
              text-align: center;
              border: none;
              border-radius: 0;
            }
          }

          &-text {
            font-family: $font-gotham;
            font-weight: 400;
            font-size: 16px;
            line-height: 18px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: $color-gray-10;
            text-align: center;
            padding: 22px 0;
          }
        }

        .menu-item {
          &::before {
            height: 0;
          }

          a {
            font-size: 20px;
            line-height: 18px;
            font-family: $font-1;
            width: 100%;
            padding: 20px 0;
            text-align: center;
          }
        }
      }
    }
  }
}

// Footer
.footer-new {
  max-width: 100%;
  padding: 32px 16px;
  background: $color-black-5;

  .footer-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;

    @media (max-width: 1167px) {
      flex-direction: column;
    }

    li {
      color: $color-gray-9;
      font-size: 18px;

      &::before {
        content: none;
      }

      a {
        font-family: $font-2;
        font-size: 18px;
        line-height: 23px;
        color: $color-white;

        &:hover,
        &:focus {
          color: $color-green-9;
        }

        &:active {
          color: $color-green-3;
        }
      }
    }

    .copyright {
      font-family: $font-2;
      font-size: 18px;
      line-height: 23px;
      color: $color-gray-9;

      @media (max-width: 1167px) {
        letter-spacing: -1px;
      }
    }

    .social-list {
      padding: 28px 0 12px;
      margin: 0;
      display: flex;
      align-items: center;

      @media (max-width: 1167px) {
        order: -1;
        margin-bottom: 8px;
      }

      a {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: $color-gray-9;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 12px;

        @media (max-width: 1167px) {
          margin: 0 8px;
        }

        &:hover,
        &:focus {
          background: $color-green-4;
        }

        &:active {
          background: $color-green-6;
        }

        img {
          width: 25px;
          height: 25px;
        }
      }
    }
  }
}

.promo-new-body {
  max-width: 1920px;
  margin: 0 auto;

  // Banner section
  .vert-center-container {
    position: relative;
    height: 800px;

    video {
      height: 100%;
    }

    .promo-background {
      background: linear-gradient(
          180deg,
          rgba(255, 255, 255, 0.74) 24.44%,
          rgba(255, 255, 255, 0) 88.44%
        ),
        linear-gradient(
          0deg,
          rgba(255, 255, 255, 0.5),
          rgba(255, 255, 255, 0.5)
        );
      position: absolute;
      height: 100%;
      width: 100%;
    }

    .vert-center {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0 16px;

      @media (max-width: 1167px) {
        width: calc(100% - 32px);
        padding: 0;
        margin: 0 auto;
        background: linear-gradient(
          90deg,
          rgba(0, 124, 137, 0.8) 0%,
          rgba(0, 124, 137, 0.4) 100%
        );
        border-radius: 28px 84px 28px 28px;
        backdrop-filter: blur(8px);
      }

      &-top {
        border-radius: 42px 144px 0 0;
        background: linear-gradient(
          90deg,
          rgba(0, 124, 137, 0.8) 0%,
          rgba(0, 124, 137, 0.4) 100%
        );
        backdrop-filter: blur(8px);
        padding: 48px 32px;
        width: 1040px;

        @media (max-width: 1167px) {
          width: 100%;
          padding: 24px 16px 16px;
          border-radius: 28px 84px 0 0;
          background: none;
          backdrop-filter: none;
        }

        h1 {
          margin-bottom: 32px;
          font-family: $font-1;
          font-size: 72px;
          line-height: 90px;
          color: $color-white;

          @media (max-width: 1167px) {
            font-size: 56px;
            line-height: 70px;
          }
        }

        .sub-desc {
          margin-bottom: 24px;

          li {
            font-family: $font-context-light;
            font-size: 24px;
            line-height: 120%;
            color: $color-white;
            padding-bottom: 8px;

            @media (max-width: 1167px) {
              font-size: 16px;
            }
          }
        }

        h3 {
          font-family: $font-context-bold;
          font-size: 48px;
          line-height: 60px;
          color: #ffffff;

          @media (max-width: 1167px) {
            font-size: 40px;
            line-height: 50px;
          }

          span {
            font-family: $font-context-light;
            font-size: 32px;
            line-height: 40px;

            @media (max-width: 1167px) {
              font-size: 24px;
              line-height: 30px;
            }
          }
        }
      }

      .navbar-subscribe {
        width: 1120px;
        background: $bg-white-1;
        box-shadow: 0 0 20px rgba(137, 81, 0, 0.25);
        border-radius: 100px;
        display: flex;
        justify-content: space-between;

        @media (max-width: 1167px) {
          width: 100%;
          flex-direction: column;
          border-radius: 28px;
          text-align: center;
        }

        a {
          background: $bg-orange-2;
          border-radius: 100px;
          color: $color-white;
          font-size: 32px;
          line-height: 18px;
          letter-spacing: 1px;
          text-transform: uppercase;
          padding: 32px 48px;
          text-decoration: none;

          @media (max-width: 1167px) {
            border-radius: 0 0 28px 28px;
            font-size: 24px;
            padding: 16px;
          }

          &:hover,
          &:focus {
            background: $bg-orange-3;
          }

          &:active {
            background: $bg-orange-4;
          }
        }

        &-title {
          padding: 14px 64px;
          font-family: $font-1;
          font-size: 48px;
          line-height: 60px;
          color: $color-black-5;
          text-shadow: 0 0 10px #ffffff;

          @media (max-width: 1167px) {
            background: $bg-white-1;
            border-radius: 28px 28px 0 0;
            font-size: 32px;
            line-height: 40px;
          }
        }
      }
    }
  }

  // Slogan section
  .slogan-wrapper {
    background: $bg-white-2;

    .wrapper {
      padding: 96px 48px 48px;
      display: flex;

      @media (max-width: 1167px) {
        flex-direction: column;
        padding: 32px 16px 16px;
      }

      .slogan-item {
        padding: 32px;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-basis: 50%;

        @media (max-width: 1167px) {
          padding: 32px 0;
        }

        .text-wrapper {
          margin-top: 32px;
          background: $bg-white-1;
          box-shadow: 0 0 20px rgba(137, 81, 0, 0.25);
          border-radius: 16px;
          padding: 32px;
          position: relative;
          color: $color-black-5;
          font-family: $font-2;
          flex-grow: 1;
          width: 100%;

          > p {
            margin-bottom: 0;
            font-size: 32px;
            line-height: 40px;
          }

          > div {
            position: absolute;
            top: -50px;
            left: 50px;
            font-size: 144px;
            line-height: 180px;
          }
        }
      }
    }
  }

  // Live class section
  .live-class-wrapper,
  .on-demand-wrapper {
    padding: 0 64px;

    @media (max-width: 768px) {
      padding: 0 15px;
    }
  }

  .promo-header {
    padding: 96px 0 60px;
    font-family: $font-1;
    font-size: 65px;
    line-height: 110%;
    color: $color-green-9;
    text-align: center;

    @media (max-width: 1167px) {
      font-size: 48px;
      padding: 60px 0;
    }
  }

  .sub-header-wrapper,
  .live-footer {
    h3 {
      font-family: $font-header-light;
      font-size: 34px;
      line-height: 110%;
      color: $color-black-1;
      margin-bottom: 0;

      @media (max-width: 1167px) {
        font-size: 28px;
      }
    }

    a {
      font-family: $font-header-light;
      font-size: 18px;
      line-height: 23px;
      text-transform: uppercase;
      color: $color-gray;

      &:hover,
      &:focus {
        color: $color-green-9;
      }

      &:active {
        color: $color-green-3;
      }

      // @media (max-width: 1167px) {
      //   display: none;
      // }
    }
  }

  .sub-header-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    > a {
      @media (max-width: 1167px) {
        display: none;
      }
    }
  }

  .live-footer {
    padding: 32px 0;
    text-align: center;
  }

  .live-class-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 0;
    border-bottom: 1px solid $color-gray-14;
    position: relative;

    @media (max-width: 1167px) {
      flex-direction: column;
    }

    &-left {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-grow: 1;

      @media (max-width: 1167px) {
        width: 100%;
      }

      @media (max-width: 500px) {
        flex-direction: column;
        align-items: flex-start;
      }

      &-wrapper {
        display: flex;
        align-items: center;
      }
    }

    &-info {
      &.class-duration-level {
        width: 79px;
        margin-left: auto;

        @media (max-width: 1167px) {
          width: auto;
          margin-left: unset;
        }
      }
    }
  }

  .live-class-item-time-wrapper {
    padding-right: 32px;
    min-width: 140px;

    @media (max-width: 1167px) {
      min-width: 124px;
    }
  }

  .live-class-item-time-wrapper.last {
    @include media-breakpoint-down(sm) {
      display: none;
    }
  }

  .live-class-item-time {
    font-family: $font-1;
    font-size: 28px;
    line-height: 110%;
    color: $color-black-7;
    padding-bottom: 4px;

    @media (max-width: 1167px) {
      font-size: 22px;
    }

    span {
      font-size: 16px;
      font-family: $font-header-light;
    }
  }

  .live-class-item-text,
  .live-class-item-teacher-name {
    font-family: $font-context-light;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: $color-black-7;
    word-break: break-all;

    @media (max-width: 1167px) {
      font-size: 14px;
      line-height: 18px;
    }
  }

  .live-class-item-teacher {
    padding-left: 32px;
    display: flex;
    align-items: center;

    @media (max-width: 1167px) {
      padding-left: 8px;
    }

    @media (max-width: 500px) {
      padding-left: 0;
    }
  }

  .live-class-item-teacher-avatar {
    width: 64px;
    min-width: 64px;
    height: 64px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 32px;

    @include media-breakpoint-down(sm) {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .live-class-item-right {
    display: flex;
    align-items: center;

    @media (max-width: 1167px) {
      width: 100%;
    }
  }

  .live-class-item-style {
    font-family: $font-1;
    font-size: 28px;
    line-height: 110%;
    color: $color-black-7;
    margin-bottom: 4px;
    display: flex;
    word-break: break-all;

    @media (max-width: 1167px) {
      font-size: 22px;
    }

    img {
      align-self: flex-start;
      margin-left: 8px;
      cursor: pointer;

      @include media-breakpoint-down(sm) {
        display: none;
      }

      &:hover + .live-class-item-description {
        display: block;
      }
    }

    a {
      color: $color-black-7;
    }
  }

  .live-class-item-description {
    display: none;
    position: absolute;
    width: 700px;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
    padding: 16px 32px;
    background: $bg-white-1;
    box-shadow: 0 0 20px rgba(137, 81, 0, 0.25);
    border-radius: 8px;
    font-family: $font-2;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: $color-black-7;
    z-index: 1;

    @media (max-width: 1167px) {
      top: 54px;
    }
  }

  .live-class-item-info {
    @media (max-width: 500px) {
      order: -1;
      display: flex;
      margin-bottom: 8px;
    }
  }

  .live-class-item-info-el {
    padding: 6px 8px;
    display: flex;
    align-items: center;
    font-family: $font-context-light;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.04em;
    color: $color-gray-10;
    white-space: nowrap;

    @media (max-width: 500px) {
      padding: 0;
      min-width: 122px;
    }

    img {
      margin-right: 8px;
    }
  }

  .atcb-item-link {
    display: inline-block;
    padding: 5px 15px;
    font-family: $font-1;
    font-size: 18px;
    color: #404040;

    &:after {
      content: none;
    }
  }

  .addtocalendar.active {
    .atcb-list {
      display: block;
      visibility: visible;
    }
  }

  .atcb-list {
    display: none;
    width: 100%;
    padding: 8px;
    border: 1px solid $border-grey-1;
    border-radius: 25px;

    @include media-breakpoint-down(sm) {
      right: 0;
      left: auto;
    }

    .atcb-checkmark {
      display: inline-block;
      margin-left: 3px;
      margin-right: 5px;

      @include media-breakpoint-down(sm) {
        margin-right: 3px;
      }

      &:after {
        content: "";
        display: block;
        width: 5px;
        height: 10px;
        border: solid $color-green-2;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);

        @include media-breakpoint-down(sm) {
          width: 4px;
          height: 7px;
        }
      }

      & + .atcb-item-link {
        color: $color-green-2;
      }
    }
  }

  .live-class-item-btn-primary {
    margin-left: 46px;

    @media (max-width: 1167px) {
      margin-left: 0;
      margin-top: 24px;
      width: 100%;
    }

    button,
    a {
      padding: 16px 32px;
      background: $bg-orange-2;
      border: 2px solid $bg-orange-2;
      border-radius: 100px;
      width: 248px;
      font-family: $font-1;
      font-size: 24px;
      line-height: 100%;
      color: $color-white;
      display: block;
      text-align: center;

      @media (max-width: 1167px) {
        width: 100%;
      }

      &:hover,
      &:focus {
        background: $bg-orange-3;
        border: 2px solid $bg-orange-3;
      }

      &:active {
        background: $bg-orange-4;
        border: 2px solid $bg-orange-4;
      }
    }
  }

  .live-content-wrap {
    overflow: visible;
  }

  .live-class-item-btn-secondary {
    margin-left: 46px;

    @media (max-width: 1167px) {
      margin-left: 0;
      margin-top: 24px;
      width: 100%;
    }

    button {
      padding: 16px 0;
      border: 2px solid $color-gray-10;
      border-radius: 100px;
      background: $bg-white-1;
      width: 248px;
      font-family: $font-1;
      font-size: 24px;
      line-height: 100%;
      color: $color-gray-10;

      @media (max-width: 1167px) {
        width: 100%;
      }

      &:hover,
      &:focus {
        color: $bg-orange-2;
        border: 2px solid $bg-orange-2;
      }

      &:active {
        background: $bg-white-2;
        color: $bg-orange-2;
        border: 2px solid $bg-orange-2;
      }
    }

    & > span {
      display: block;
    }
  }

  // On-Demand library
  .on-demand-wrapper {
    background: $bg-white-2;

    @media (min-width: 1168px) {
      padding-bottom: 40px;
    }

    .demand-item {
      display: flex;
      border: 1px solid $color-gray-14;
      margin-bottom: 8px;

      @media (max-width: 1167px) {
        flex-direction: column;
      }
    }

    .live-footer {
      @media (min-width: 1168px) {
        display: none;
      }
    }

    .demand-item-content {
      width: 400px;
      background: $bg-white-1;
      padding: 32px;
      border-radius: 0 15px 15px 0;
      flex-direction: column;

      @media (max-width: 1167px) {
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        border-radius: 0 0 15px 15px;
        padding: 20px 16px;
      }
    }

    .demand-item-img {
      flex-grow: 1;
      border-radius: 15px 0 0 15px;

      @media (max-width: 1167px) {
        border-radius: 15px 15px 0 0;
      }
    }

    .live-class-item-teacher {
      padding-left: 0;
    }

    .demand-item-text {
      width: 100%;
      padding: 32px 0;
      flex-grow: 1;

      @media (max-width: 1167px) {
        display: none;
      }

      p {
        margin-bottom: 0;
        font-family: $font-2;
        font-size: 16px;
        line-height: 120%;
        color: $color-gray-10;
      }
    }

    .demand-item-footer {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;

      @media (max-width: 1167px) {
        width: auto;
      }

      > a {
        background: $bg-pink;
        border-radius: 50%;
        width: 56px;
        height: 56px;
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-width: 1167px) {
          display: none;
        }
      }
    }

    .live-class-item-teacher-avatar {
      display: block;

      @media (max-width: 1167px) {
        width: 48px;
        min-width: 48px;
        height: 48px;
        margin-right: 16px;
      }
    }

    .live-class-item-info {
      @media (max-width: 500px) {
        flex-direction: column;
      }
    }

    .live-class-item-info-el {
      @media (max-width: 500px) {
        padding: 6px 8px;
        min-width: auto;
      }
    }

    .demand-item-content {
      margin-top: 0;
    }

    .demand-item-list {
      display: flex;
      margin: 0 -8px;

      @media (max-width: 1167px) {
        flex-direction: column;
      }
    }

    .demand-item-secondary {
      flex-basis: calc(100% / 3);
      border: 2px solid $color-gray-14;
      border-radius: 8px;
      overflow: hidden;
      margin: 8px;

      > img {
        width: 100%;
      }

      .demand-item-content {
        width: 100%;
        border-radius: 0;
        overflow: hidden;
        padding: 16px;
        flex-direction: row;
        justify-content: space-between;

        .demand-item-footer {
          width: auto;
        }

        .live-class-item-style {
          font-size: 22px;
        }

        .live-class-item-teacher-name {
          font-size: 14px;
          line-height: 18px;
        }

        .live-class-item-teacher-avatar {
          width: 48px;
          min-width: 48px;
          height: 48px;
          margin-right: 16px;
        }
      }
    }
  }

  .promo-new-accept-offer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 32px 64px;
    background: $bg-white-2;

    @media (max-width: 1167px) {
      flex-direction: column;
      padding: 32px 15px;
      text-align: center;
    }

    &-text {
      padding-right: 16px;
      font-family: $font-1;
      font-size: 48px;
      line-height: 100%;
      color: $color-black-7;

      @media (max-width: 1167px) {
        font-size: 36px;
        margin-bottom: 16px;
      }
    }

    a {
      padding: 32px;
      background: $bg-orange-2;
      border-radius: 56px;
      font-family: $font-1;
      font-size: 32px;
      line-height: 18px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $color-white;
      min-width: 360px;
      text-align: center;

      @media (max-width: 1167px) {
        font-size: 24px;
        padding: 16px;
        width: 100%;
        min-width: auto;
      }

      &:hover,
      &:focus {
        background: $bg-orange-3;
      }

      &:active {
        background: $bg-orange-4;
      }
    }
  }

  .faq-wrapper {
    padding: 32px 0;
    margin: 0 auto;

    .container {
      max-width: initial;
    }

    .custom-accordion {
      .card {
        margin-bottom: 10px;
        border-radius: 5px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.125);

        &:last-of-type {
          margin-bottom: 0;
        }
      }

      .card-header {
        position: relative;
        padding: 0;
        margin-bottom: 0;
        border-bottom: 0;

        a {
          font-family: $font-header-regular;
          font-size: 18px;
          color: $color-green-9;
          padding: 5px 15px;
          width: 100%;

          .accordion-icon {
            position: absolute;
            display: inline-block;
            top: 50%;
            right: 10px;
            padding: 3px;
            border: solid $color-green-9;
            border-width: 0 3px 3px 0;
            transform: translateY(-50%) rotate(45deg);
          }

          &.collapsed {
            .accordion-icon {
              transform: translateY(-50%) rotate(-45deg);
            }
          }
        }
      }

      .card-body {
        padding: 15px;
        border-top: 1px solid rgba(0, 0, 0, 0.125);

        p,
        a,
        li {
          font-family: $font-context-regular;
        }

        p {
          &:last-of-type {
            margin-bottom: 0;
          }
        }

        a {
          color: $color-green-9;
        }

        ul {
          list-style: circle;
          padding-left: 25px;
        }

        ol {
          padding-left: 30px;
        }
      }
    }
  }
}

.wppb-builder-container {
  .promo-new-wrapper.wppb-row-parent {
    position: initial;

    .wppb-container {
      > .wppb-row {
        overflow-x: initial;

        .wppb-column {
          position: initial;

          .wppb-builder-addons {
            position: initial;
          }
        }
      }
    }
  }
}
