.footer-link {
  padding: 20px 0px;

  @include media-breakpoint-down(sm) {
    padding-left: 0;
    padding-right: 0;
  }

  li {
    display: inline-block;
    color: $color-gray-1;

    &:not(:first-child) {
      @include media-breakpoint-down(md) {
        padding-top: 5px;
      }

      &:before {
        content: '';
        display: inline-block;

        @include media-breakpoint-down(md) {
          display: none;
        }
      }
    }

    a {
      font-size: 18px;
      color: $color-gray-1;

      &:hover {
        @include media-breakpoint-down(md) {
          text-decoration: none;
        }
      }
    }

    @include media-breakpoint-down(md) {
      display: block;
    }
  }
}
