.nav-tabs-custom {
  &.nav-tabs {
    border-bottom: 1px solid $border-grey-2;

    @include media-breakpoint-down(sm) {
      display: block;
    }

    &.nav-tabs-sub {
      border-bottom: none;
    }
  }

  .nav {
    &-item {
      margin: 0;
      padding: 0 30px 20px 30px;

      &:last-child {
        padding-right: 0;
      }

      @include media-breakpoint-down(sm) {
        display: inline-block;
        padding-left: 0;
        padding-right: 15px;
      }
    }

    &-link {
      border: unset;
      padding: 0 0 10px 0;

      h5, h6 {
        margin: 0;
        color: $color-black-2;
      }

      @include media-breakpoint-down(sm) {
        padding-bottom: 5px;

        h5 {
          font-size: 20px;
        }

        h6 {
          font-size: 16px;
        }
      }

      &.active {
        position: relative;
        background-color: unset;

        h5, h6 {
          color: $color-pink-dark;
        }

        &::after {
          content: "";
          display: inline-block;
          width: 100%;
          height: 2px;
          background-color: $bg-pink;
          position: absolute;
          left: 0;
          bottom: 0;
        }
      }
    }
  }
}
