.filter {
  &-panel {
    position: relative;
    margin-bottom: 50px;

    &-bg {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(16, 85, 82, 0.02) 0%, rgba(250, 249, 247, 0.02) 100%);
      z-index: -1;
    }
  }

  &-wrapper {
    margin: 0;
    justify-content: space-between;
    border-bottom: 1px solid $border-grey-5;

    @include media-breakpoint-down(sm) {
      padding-bottom: 1rem;
    }
  }

  &-control {
    margin: 0;
    justify-content: space-between;
    padding-right: 55px;

    span {
      font-family: $font-context-regular;
      font-style: normal;
      font-size: 18px;
      line-height: 22px;
      text-transform: uppercase;
      color: $color-gray;
      cursor: pointer;
      margin-top: 20px;
    }

    &-prev,
    &-next {
      position: relative;
      padding-left: 15px;

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        padding: 4px;
        margin-top: -1px;
        border: solid $border-grey-8;
        border-width: 0 2px 2px 0;
        transform: translateY(-50%) rotate(135deg);
      }

      @include media-breakpoint-down(sm) {
        display: none;
        font-size: 14px;

        &:before {
          padding: 3px;
        }
      }
    }

    &-next {
      padding-left: 0;
      padding-right: 15px;

      &:before {
        left: auto;
        right: 0;
        transform: translateY(-50%) rotate(-45deg);
      }
    }

    [data-select-all] {
      @include media-breakpoint-down(sm) {
        display: none;
      }
    }

    .hidden {
      visibility: hidden;
      pointer-events: none;
    }
  }

  &-item {
    text-align: center;
    cursor: pointer;

    &:last-child {
      padding-right: 55px;

      @include media-breakpoint-down(sm) {
        padding: 0;
      }
    }

    &.active {
      .filter-content {
        background: $bg-orange;
        border: none;
      }

      h5 {
        color: $color-white;
      }
    }
  }

  &-content {
    width: 54px;
    height: 54px;
    border: 2px solid $border-grey-2;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

    h5 {
      color: $color-gray-5;
      margin-bottom: 0;
    }

    @include media-breakpoint-down(sm) {
      width: 40px;
      height: 40px;
    }
  }

  &-description {
    font-family: $font-context-regular;
    font-style: normal;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    color: $color-gray;
    padding-top: 20px;

    @include media-breakpoint-down(sm) {
      display: none;
    }
  }

  &-demand {
    &-list {
      &__wrapper {
        display: flex;
        flex-wrap: wrap;

        @include media-breakpoint-down(md) {
          display: block;
        }

        @include media-breakpoint-down(sm) {
          position: relative;
        }
      }
    }

    &-item {
      padding: 30px 100px 30px 0px;

      font-family: $font-header-regular;
      font-style: normal;
      font-weight: normal;
      font-size: 28px;
      line-height: 110%;
      color: $color-black-2;

      &:last-child {
        padding-right: 0;
      }

      @include media-breakpoint-down(lg) {
        padding-right: 70px;
      }

      @include media-breakpoint-down(md) {
        display: inline-block;
      }

      @include media-breakpoint-down(sm) {
        font-size: 18px;
        padding: 30px 50px;
        width: 45%;

        &:nth-child(2n + 1) {
          padding-left: 0;
        }

        &.dropdown {
          position: unset;
        }
      }

      &.show, .active {
        color: $color-pink-dark;
        background-color: unset;
      }

      .dropdown {
        &-toggle {
          position: relative;
          cursor: pointer;

          &::after {
            content: none;
          }
        }

        &-menu {
          top: 30px !important;
          color: $color-black-1;
          border-radius: 15px;
          background-color: $bg-tan-light;
          box-shadow: 0 0 10px rgba(0,0,0,.25);
          max-height: 460px;
          margin: 0;
          padding: 0;

          @include media-breakpoint-down(sm) {
            max-height: 300px;
            top: 20px !important;
          }
        }

        &-menu,
        &-item {
          &:focus {
            outline: none;
          }
        }

        &-item,
        &-header {
          position: relative;
          padding: 30px;
          font-size: 28px;
          color: $color-black-4;

          &:not(:first-child) {
            &:before {
              content: "";
              position: absolute;
              left: 50%;
              top: 0;
              width: 90%;
              height: 1px;
              transform: translateX(-50%);
              background: $bg-grey;
            }
          }

          @include media-breakpoint-down(sm) {
            font-size: 18px;
            padding: 20px;
          }
        }

        &-header {
          text-transform: capitalize;
          font-weight: 600;
        }

        &-item {
          &.empty-line {
            &:before {
              display: none;
            }

            &:hover {
              background-color: inherit;
            }
          }

          &.active,
          &:active {
            color: $color-pink-dark;
            background-color: unset;
          }
        }
      }

      .scroll-panel {
        &:focus {
          outline: none;
        }
      }
    }

    &-selection {
      position: absolute;
      top: 50%;
      left: 100%;
      transform: translate(0, -30%);
      padding-left: 5px;
      width: 200%;


      @include media-breakpoint-down(sm) {
        font-size: 12px;
      }
    }

    &-label {
      display: inline-block;
      position: relative;
      padding: 10px 100px 10px 10px;
      margin: 0 5px 5px 0;
      border: 1px solid $border-grey-6;

      font-family: $font-context-regular;
      font-size: 18px;
      line-height: 22px;
      text-transform: uppercase;
      color: $color-black-2;

      @include media-breakpoint-down(sm) {
        font-size: 12px;
        padding: 0 50px 0 5px;
      }
    }

    &-close {
      position: absolute;
      top: 50%;
      right: 5px;
      padding: 10px 0;
      border: transparent;
      transform: translateY(-50%);
      cursor: pointer;

      @include media-breakpoint-down(sm) {
        padding: 5px 0;
      }

      .icon-bar {
        display: block;
        position: relative;
        width: 20px;
        height: 2px;
        background: $bg-black-1;

        @include media-breakpoint-down(sm) {
          width: 11px;
        }

        &:first-child {
          top: 1px;
          transform: rotate(45deg);
        }

        &:last-child {
          top: -1px;
          transform: rotate(-45deg);
        }
      }
    }

    &-reset {
      position: absolute;
      top: 50%;
      right: 15px;
      @include transform(translate(0, -50%));
    }
  }

  &-message {
    font-family: $font-context-regular;
    font-size: 18px;
    padding-left: 15px;
  }

  &-result {
    font-family: $font-header-extralight;
    font-size: 18px;
    text-transform: uppercase;

    span {
      font-weight: bold;
    }
  }
}

[data-reset-filter] {
  background-color: $bg-red;
  color: $color-white;
  cursor: pointer;
}

.search {
  &-wrapper {
    input {
      position: relative;
    }

    .form {
      &-icon {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        border: 2px solid $border-grey-4;
        border-radius: 50%;
        width: 60px;
        height: 60px;

        @include media-breakpoint-down(sm) {
          width: 40px;
          height: 40px;
        }

        img {
          position: absolute;
          top: 55%;
          left: 55%;
          transform: translate(-50%, -50%);
        }
      }

      &-control.form-large {
        padding: 50px 0 50px 95px;
      }
    }

    .tagsinput {
      padding: 50px 0 50px 95px;
      border-bottom: 1px solid $border-grey-5;

      .tag, input {
        font-size: 36px;

        @include media-breakpoint-down(md) {
          font-size: 26px;
        }

        @include media-breakpoint-down(sm) {
          font-size: 18px;
        }
      }

      @include media-breakpoint-down(lg) {
        input {
          &::placeholder {
            white-space: pre-line;
            overflow: visible;
          }
        }
      }

      @include media-breakpoint-down(sm) {
        padding: 20px 0 20px 45px;
      }

      input {
        .tablet.android10 & , .tablet.android11 & {
          // padding-bottom: 25px;
        }

        @include media-breakpoint-down(lg) {
          padding-bottom: 0;

          .desktop.mac & {
            // padding-bottom: 25px;
          }
        }

        @include media-breakpoint-down(sm) {
          // padding-bottom: 20px;

          .mobile.iphone.smallscreen & {
            padding-bottom: 0;
          }
        }
      }
    }
  }
}
.filter-live-list__wrapper{
  .dropdown-menu{
    overflow-y: scroll;
    top: 28px !important;
  }
}
