.demand-class {
  background: $bg-grey-2;

  .header-main {
    background: $bg-grey-2;
  }

  &-primary {
    padding-top: 0;

    .demand-item {
      margin-bottom: 0;

      &-content {
        position: relative;
        padding-bottom: 40px;
        
        @include media-breakpoint-down(sm) {
          margin-top: 0;
          padding: 30px 0;
          justify-content: space-between;
        }

        &:after {
          content: '';
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          width: calc(100% - 50px);
          height: 1px;
          background: $bg-grey-3;
          bottom: 0;

          @include media-breakpoint-down(sm) {
            width: 100%;
          }
        }
      }

      &-info {
        @include media-breakpoint-down(sm) {
          max-width: calc(100% - 130px);
          width: auto;
        }
      }
    }

    .demand-item-bubble-item {
      @include media-breakpoint-down(sm) {
        margin-bottom: 0;
      }
    }

    .demand-item-bubble-list {
      @include media-breakpoint-up(sm) {
        width: 50%;
      }

      @include media-breakpoint-down(sm) {
        width: auto;
      }
    }
  }
}