.redesigned-wrapper .wrapper {
  .content-width.ondemand-single-video {  
    background-color: $tetriarySurface;

    .vertical-line { 
      display: none;
    }

    // Single video section
    .demand.demand-class-primary {
      background: none;
      overflow: unset;

      .scrollmagic-pin-spacer {
        display: none;
      }

      .container-fluid {
        padding: 32px 32px 0px;

        @media (max-width: $lg) {
          padding: 32px 16px 0px;
        }

        @media (max-width: $md) {
          padding: 0;
        }


        .row > .col-md-12 {
          padding: 0;
        }

        .demand-item-img {
          border-radius: 16px 16px 0 0 ;

          @media (max-width: $md) {
            border-radius: 0;
          }
  
        }
      }


      video-js.video-js {
        button.vjs-big-play-button {
          background-color: $neutralText;
        }

        // player's control button bar
        .vjs-control-bar {
          color: $neutralSurface;
        }
      }
      
      // Video description section
      .demand-item-content {
        padding: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        border-radius: 0 0 16px 16px;

        @media (max-width: $md) {
          border-radius: 0;
        }

        @media (max-width: $lg) {
          align-items: flex-start;
        }


        .demand-item-info {
          align-items: flex-start;
          width: 100%;
          max-width: 100%;
          padding: 16px 32px;

          @media (max-width: $md) {
            flex-direction: column;
          }

          @media (max-width: $lg) {
            padding: 16px;
          }

          .demand-item-info__avatar {
            width: 72px;
            height: 72px;
            min-width: 72px;
            min-height: 72px;
            margin: auto;

            border: 1px solid $neutralBorder;
            border-radius: 50%;

            @media (max-width: $md) {
              display: none;
            }
          }

          .demand-item-info__detail {
            padding: 0px 0px 0px 16px;

            @media (max-width: $md) {
              padding: 0;
            }

            // level
            .live-content-item__level {
              display: flex;
              flex-direction: row;
              justify-content: center;
              align-items: center;
              padding: 4px 12px;
    
              width: fit-content;
    
              background: $secondary;
              border-radius: 16px;
    
              @include fontOverlineMd();
              text-transform: uppercase;
              color: $neutralText;
            }
            
            // Video title
            h1 {
              white-space: wrap;
              // overflow: hidden;
                            
              @include fontHeader4Md();
              color: $neutralText;

              @media (max-width: $lg) {
                @include fontHeader4Sm();
              }
            }

            // trainer name
            div.context-1 {
              display: flex;
              margin: 0;           
              
              a {
                @include fontText1Md();
                color: $neutralMiddle;
              }
                .demand-item-info__small-avatar {
                  display: none;
                  border-radius: 4px;
                  width: 20px;
                  height: 20px;
                  min-width: fit-content;
                  min-height: 100%;
                  overflow: hidden;
                  margin: 2px 8px 2px 0;

                  img {
                    // fix bug in safari 
                    width: 20px;
                    height: 20px;
                  }
                  @media (max-width: $md) {
                    display: flex;
                  }
                }

                // 60 min - lesson time length
                .item-info__lesson-length {
                  display: flex;
                  align-items: center;
                  width: max-content;

                  padding: 0;
                  margin: 0 0 0 16px;
                  
                  order: 3;
                  
                  // img {
                  //   width: 20px;
                  //   height: 20px;
                  //   margin-right: 8px;
                  //   margin-top: 2px;
                  // }

                  span {
                    @include fontText1Md();
                    color: $neutralMiddle;
                    white-space: nowrap;                    
  
                    @media (max-width: $lg) {
                      @include fontText1Sm();
                    }
  
                  }
                }
            }
            
            // "You've liked" text
            div.context-2 {
              display: none;
            }
          }
        }     

        .demand-item-actions {
          display: flex;

          @media (max-width: $md) {
            flex-direction: row !important;
          }

          @media (max-width: $lg) {
            flex-direction: column;
          }

          a {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 0px 16px;

            @media (max-width: $lg) {
              justify-content: flex-start;
            }

            svg {
              width: 24px;
              height: 24px;
              &:nth-child(2) {
                display: none;
              }
            }

            span {
              padding: 12px 8px;
              @include fontButtonMd();
              color: $neutralText;
              word-break: keep-all;
            }

            &:hover {
              span {
                transition: all 0.3s;
                color: $primary;
              }

              svg path {
                transition: all 0.3s;
                fill: $primary;
              }
            }

            &.action__favorite-button {
              @media (max-width: $md) {
                margin-left: -16px;
              }
            }

            &.action__favorite-button.active {
              svg {
                width: 24px;
                height: 24px;

                &:nth-child(1) {
                  display: none;
                }

                &:nth-child(2) {
                  display: block;

                  path {
                    fill: $primary;
                  }
                }
              }
            }
          }
        }

        .comment-post {
          padding: 24px 32px 24px;
          width: 100%;
          border-top: 1px solid $neutralBorder;

          @media (max-width: $lg) {
            padding: 16px;
          }

          p {
            @include fontText1Md();
            color: $neutralText;

            @media (max-width: $lg) {
              @include fontText1Sm();

            }
          }
        }

        .demand-item-bubble-list {
          display: none;
        }

        ul.action-list {
          display: none;
        }

        
        &:after {
          display: none;
        }
      }
    }

    //Playlist modal window
    #playlistFramework {
      // redefine inline styles
      padding: 0 16px !important;

      .modal-content {
        background-color: $tetriarySurface;
        padding: 56px 56px 32px;

        @media (max-width: $md ) {
          padding: 56px 16px 32px;

        }

        & > .close.button-rectangle-clear-md {
          padding: 12px;

          svg {
            width: 32px;
            height: 32px;
          }
        }
      }

      .modal-dialog {
        margin: 0 auto;
      }

      .modal-header {
        padding-bottom: 32px;
        border: unset;

        
        h3 {
          display: flex;

          @include fontHeader3Md();
          color: $neutralText;
  
          @media (max-width: $lg ) {
            @include fontHeader3Sm();
          }
        }
      }

      .form-group-wrap {
        border-top: 1px solid $tetriaryLight;
        border-bottom: unset;
      }


      .playlist-wrapper .form-checkbox {
        color: $neutralText;

        .form-group-wrap {
          margin: 0;
          padding: 6px 0px 6px 24px;
        }

        label {
          position: relative;
          span {
            @include fontSubtitle1Md();
  
            @media (max-width: $lg ) {
              @include fontSubtitle1Sm();
            }
          }


        }

        label span.radiomark {
          display: block;
          border: none;
          background: url("../images/checkbox-unchecked-md.svg");
          background-repeat: no-repeat !important;
          background-position: center;
          border-radius: unset;
          margin-left: 0;

          width: 24px;
          height: 24px;
          min-width: unset;
          min-height: unset;

          @media (max-width: $md ) {
            background: url("../images/checkbox-unchecked-sm.svg");
            width: 16px;
            height: 16px;
            min-width: 16px
          }

          &::after {
            width: 0;
            height: 0;
            display: none;
          }
        }

        input:checked~.radiomark~span {
          color: $neutralText;
        }

        input:checked~.radiomark {
          background: url("../images/checkbox-checked-md.svg");

          @media (max-width: $md ) {
            background: url("../images/checkbox-checked-sm.svg");
            width: 16px;
            height: 16px;
            min-width: 16px
          }
        }

        .radio-detail .button-rectangle-clear-md {
          padding: 16px;

          word-break: keep-all;
          text-transform: none;

          .delete-icon {
            display: none;
          }

          div {
            margin: 0;
            word-break: keep-all;
          }

          @media (max-width: $md ) {
            padding: 14px;
            .delete-text {
              display: none;
            }

            .delete-icon {
              display: block;
              width: 24px;
              height: 24px;
            }
          }

        }
      }

      .form-group {
        .form-group-wrap {
          padding: 10px 24px; 
          margin: 0;

          @media (max-width: $md ) {
            padding: 10px 0px;
            margin: 0;
          }
        }

        .title-wrap {
          display: flex;
          position: relative;
          
          label {
            display: flex;
          }

          input#newPlaylist {
            padding: 10px 12px;
            border: 1px solid black;
            width: 100%;
            height: auto;
            
            background: $neutralSurface;
            border: 1px solid $neutralSilver;
            border-radius: 4px;
            
            @include fontText1Md();
            color: rgba(57, 69, 49, 0.5);
            text-transform: unset;
            text-align: unset;

            &:active {
              color: $neutralText;
            }

            &:focus {
              padding: 9px 11px;
              border: 2px solid $primary;
              color: $neutralText;
            }

            &::placeholder {
              white-space: nowrap;
              overflow: hidden;
              color: rgba(57, 69, 49, 0.5);
            }

          }

          .radio-detail {
            position: absolute;
            right: 12px;
            top: 50%;
            transform: translateY(-50%);

            p {
              color: $primary;
              @include fontOverlineMd();
              letter-spacing: 1px;
            }
          }
        }
      }

      .modal-footer {
        padding: 32px 0 0;
        button {
          width: 100%;
        }
      }


    }

    // Delete playlist confirmation window
    #playlistDelete {
      padding: 0 16px !important;

      .modal-dialog {
        margin: 0 auto;

        .modal-content {
          background-color: $tetriarySurface;
          padding: 56px 56px 32px;
  
          @media (max-width: $md ) {
            padding: 56px 16px 32px;
  
          }
  
          // close button
          & > .close.button-rectangle-clear-md {
            padding: 12px;
  
            svg {
              width: 32px;
              height: 32px;
            }
          }

          .modal-header {
            padding: 0 0 48px 0;

            h3 {
              @include fontHeader3Md();
              color: $neutralText;
  
              @media (max-width: $lg ) {
                @include fontHeader3Sm();
              }
            }
          }

          .modal-footer {
            flex-direction: row-reverse;
            flex-wrap: nowrap;
            gap: 16px; 
            padding: 0;

            @media (max-width: $md ) {
              flex-direction: column;
            }


            button {
              width: 100%;
              margin: 0;
            }

            // delete button
            button:nth-child(1) {
              color: $negative;

              &:hover {
                color: $primary;
              }
          
              &:active {
                color: $primaryAccented;
              }
            }
          }
        }
      }
    }

    // COMMENTS SECTION
    section.comment {
      margin: 0 auto;
      max-width: $xl;

      .container-fluid {
        padding: 0;

        div.col-md-12 {
          padding: 0;
        }
      }

      div.comment-post {
        display: none;
      }

      .comment-block {
        padding: 48px 32px 64px;

        .comment-block__title {
          padding-bottom: 32px;
          
          @include fontHeader5Md();
          color: $neutralMiddle;
          letter-spacing: -0.5px;
        }

        div.comment-input {
          display: flex;
          flex-direction: column;

          border-bottom: unset;
          padding-bottom: 32px;
          transition: all 500ms;

          &.active {

            #comment-input {
              color: $neutralText;;
            }

            .comment-input__button-group{
              height: 100%;

              a {
                opacity: 1;
              }
            }
          }

          #comment-input {
            padding: 16px 12px;
            border: 1px solid black;
            width: 100%;

            background: $neutralSurface;
            border: 1px solid $neutralSilver;
            border-radius: 4px;

            // color: $neutralText;
            @include fontText1Md();
            color: rgba(57, 69, 49, 0.5);

            &:active {
              color: $neutralText;
            }

            &:focus {
              border: 2px solid $primary;
              color: $neutralText;
            }

            &::placeholder {
              white-space: nowrap;
              overflow: hidden;
              color: rgba(57, 69, 49, 0.5);
            }
          }

          .comment-input__button-group {
            display: flex;
            justify-content: flex-end;
            gap: 16px;

            padding-top: 16px;

            height: 0;
            width: 100%;

            a {
              opacity: 0;
              padding: 16px 40px;
            }
          }


        }

        .comment-block-detail {
          padding: 0px;

          .comment-item.d-block {
            padding: 0;

            .comment-item-wrapper {
              display: flex;
              flex-direction: column;
              padding: 32px 0 40px;
              border-top: 1px solid $tetriaryLight;
  
              .comment-item-wrapper__author {
                display: flex;
                
                .comment-item-name p {
                  @include fontText2Md();
                  color: $neutralText;
                  padding-right: 16px;
                }

                .comment-item-date p {
                  @include fontText2Md();
                  color: $neutralMiddle;
                }
              }
  
              .comment-item-content {
                padding: 8px 0 16px;
                p {
                  color: $neutralText;
                  @include fontText1Md();
                }
              }

              a.btn-reply {
                display: flex;
                position: unset;

                color: $neutralText;
                @include fontButtonMd();
                text-transform: none;
              }
            }
          }

        }

        .comment-total {        
          display: flex;
          justify-content: center;

          a {
            padding: 16px 40px;
            border-radius: 8px;

            color: $neutralText;
            background-color: $neutralSurface;
            @include fontButtonMd();

            text-transform: none;

            &:hover {
              transition: all 0.3s;
              background: rgba(255, 255, 255, 0.6);
            }

            &:active {
              background: rgba(255, 255, 255, 0.75);
            }
          }
        }

      }
    }

    // REPLY COMMENT BLOCK
    #reply-comment {
      .modal-content {
        padding: 56px 56px 32px;

        background: $tetriarySurface;

        @media (max-width: $md ) {
          padding: 56px 16px 32px;

        }

        & > .close.button-rectangle-clear-md {
          padding: 12px;
          
          svg {
            width: 32px;
            height: 32px;
          }
        }

        form {
          margin: 0;
        }

        div.form-group {
          display: flex;
          flex-direction: column;

          // border-bottom: unset;
          // padding-bottom: 32px;
          transition: all 500ms;

          &.active {
            #reply_content {
              color: $neutralText;;
            }
          }

          label.sr-only {
            position: unset;
            width: unset;
            height: unset;

            white-space: break-spaces;

            @include fontHeader3Md();
            color: $neutralText;

            @media (max-width: $lg ) {
              @include fontHeader3Sm();
            }
          }

          #reply_content {
            padding: 16px 12px;
            border: 1px solid black;
            width: 100%;
            height: auto;
            margin: 32px 0;

            background: $neutralSurface;
            border: 1px solid $neutralSilver;
            border-radius: 4px;

            @include fontText1Md();
            color: rgba(57, 69, 49, 0.5);
            text-transform: unset;

            &:active {
              color: $neutralText;
            }

            &:focus {
              border: 2px solid $primary;
              color: $neutralText;
            }

            &::placeholder {
              white-space: nowrap;
              overflow: hidden;
              color: rgba(57, 69, 49, 0.5);
            }
          }
        }

        .modal-footer {
          padding: 0;

          button {
            width: 100%;
          }
        }
      }
    }

    // MORE LIKE THIS SECTION
    section#more-like-this {
      
      .demand-bg.bg-linear {
        background-color: $secondary;
      }


      .demand-wrapper {
        max-width: 1440px;
        margin: 0 auto 64px;

        .container-fluid {
          padding: 0 32px;

          @media (max-width: $lg ) {
            padding: 0 16px;
          }

          .col-md-12 { 
            // padding: 0;
          }
        }
      }

      .demand-top {
        padding: 64px 0 16px;
      }

      .demand-top-left {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        gap: 16px;

        h6 {
          @include fontHeader6Md();
          text-transform: uppercase;
          color: $neutralMiddle;

          @media (max-width: $lg ) {
            @include fontHeader6Sm();
          }
          
        }
        
        h3 {
          @include fontHeader2Md();
          color: $neutralText;

          @media (max-width: $lg ) {
            @include fontHeader2Sm();
          }
        }
      }

      // Ondemand video list
      .demand-videos-list {
        margin-left: -8px;
        margin-right: -8px;
        padding-bottom: 48px;
      }

      .demand-item-col {
        padding-left: 8px;
        padding-right: 8px;
        flex: 0 0 calc(100% / 3);
        max-width: calc(100% / 3);
        margin: 8px 0;

        &:last-child {
          display: none;            
        }

        @media (max-width: $lg ) {
          flex: 0 0 50%;
          max-width: 50%;
        }

        @media (max-width: $md) {
          flex: 0 0 100%;
          max-width: 100%;
          
        }

        @media (min-width: $md ) and (max-width: $lg) {
          &:last-child {
            display: block;            
          }
        }
      }

      .demand-item-img {
        border-radius: 8px 8px 0 0;
      }

      .demand-item-content {
        background: $tetriarySurface;
        border-radius: 0 0 8px 8px;
        padding: 16px;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;

        > a {
          width: 100%;
          margin-bottom: 8px;

          h5 {
            @include fontSubtitle1Md();
            color: $neutralText;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            text-align: center;

            @media (max-width: $lg ) {
              @include fontSubtitle1Sm();
            }
          }
        }

        .demand-item__line-wrapper {
          display: flex;
          width: 100%;

          .demand-item-info {
            width: unset;
            flex: 2 10 54%;

            overflow: hidden;

            .demand-item-info__detail {
              overflow: hidden;

              .context-1 {
                display: flex;

                a {
                  white-space: nowrap;
                  overflow: hidden;
                  text-overflow: ellipsis;
                }
              }
            }

          }

          .demand-item-bubble-list {
            width: unset;
            flex: 1 0 46%;
          }

        }
      }

      .demand-item-info__avatar {
        width: 20px;
        min-width: 20px;
        height: 20px;
        border: 1px solid $neutralBorder;
        border-radius: 4px;
      }

      .demand-item-info__detail {
        width: auto;

        .context-1 {         
          margin-left: 8px;
          margin-right: 16px;
                    
          a {
            display: inline-block;

            @include fontText1Md();
            color: $neutralMiddle;
  
            @media (max-width: $lg) {
              @include fontText1Sm();
            }
          }
        }
      }

      .demand-item-bubble-item {
        display: flex;
        align-items: center;

        h5 {
          color: $neutralMiddle;
          padding: 0 4px;
          white-space: nowrap;

          @include fontText1Md();

          @media (max-width: $lg) {
            @include fontText1Sm();
          }
        }

        &:last-child {
          background: $secondary;
          border-radius: 16px;
          padding: 4px;
          margin-left: 12px;

          h5 {
            @include fontOverlineMd();
            color: $neutralText;

            @media (max-width: $lg) {
              @include fontOverlineSm();
            }            
          }
        }
      }

      .demand-item-bubble-list {
        flex-wrap: nowrap;
        justify-content: flex-start;
        width: 46%;

        @media (max-width: 420px) {
          width: 50%;
        }
      }

      .demand-item-backdrop__wrapper {
        padding: 16px 16px 0;
        background: $tetriarySurface;
        align-content: baseline;
        align-items: center;

        & > a {
          width: 100%;
          margin-bottom: 8px;

          h5 {
            @include fontSubtitle1Md();
            color: $neutralText;
            text-align: center;

            @media (max-width: $lg) {
              @include fontSubtitle1Sm();
            }
          }
        }

        & > .demand-item-info {
          margin-bottom: 0;
        }

        & > .demand-item-detail {

          padding-top: 16px;
          text-align: center;
          
          p {
            
            color: $neutralText;
            @include fontText1Md();
            
            @media (max-width: $lg) {
              @include fontText1Sm();
            }
          }
          
        }
      }

      .demand-item-description {
        .demand-item-content {
          padding: 0;
          justify-content: center;

          .watch-class-btn-wrapper {
            width: 100%;
            min-height: auto;
            justify-content: center;
            padding-bottom: 16px;
            padding-top: 8px;

            a {
              outline: none;
              border-radius: 8px;
              padding: 12px 24px;
              @include fontButtonSm() ;
              color: $neutralText;
              background-color: $neutralSurface;

              transition: all 300ms;

              &:hover {
                background: rgba(255, 255, 255, 0.6);
              }

              &:active {
                background: rgba(255, 255, 255, 0.75);
              }
            }
          }
        }
      }

      .demand-item-backdrop {
        padding-top: 50%;
        border-radius: 0;
      }

      .demand-item-info {
        width: auto;
        width: 52%;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: flex-end;

        @media (max-width: 420px) {
          width: 47%;
        }
      }

      .demand-item-col {
        > .demand-item {
          height: 100%;
          display: flex;
          flex-direction: column;

          > .demand-item-content {
            flex-grow: 1;
          }
        }
      }
      
    }
  }

}
