.redesigned-wrapper {
  .demand {
    padding-top: 0;
    // background: $tetriarySurface; 
    overflow: hidden;

    .demand-wrapper {
      max-width: 1440px;
      margin: 0 auto;
    }
    
    // Banner section 
    .ondemand-library__banner {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-grow: 1;

      height: 758px;
      max-width: 1920px;
      margin: 0 auto;

      background-image: url('../images/banner-bg-image.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;

      .banner-wrapper {        
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        max-width: $xl;
        margin: 0 auto;
        padding: 72px 32px 0px;

        .banner-title {
          @include fontHeader1Md();
          color: $neutralSurface;
          /* YW/text shadow */
          text-shadow: 0px 0px 64px rgba(57, 69, 49, 0.5);
          text-align: center;

          @media (max-width: $lg) {
            @include fontHeader1Sm();
          }
        }

        .banner-subtitle {
          @include fontHeader5Md();
          margin-top: 16px;
          color: $neutralSurface;
          /* YW/text shadow */
          text-shadow: 0px 0px 64px rgba(57, 69, 49, 0.5);
          text-align: center;

          @media (max-width: $lg) {
            @include fontHeader5Sm();
          }
        }

        .banner-button {
          margin-top: 48px;
          padding: 16px 48px;
        }
      }
    }

    // Filter section
    .filter-demand-list__wrapper {
      padding: 48px 0 20px;
      justify-content: space-between;
      margin: 0 -8px;

      @media (max-width: $lg) {
        padding: 24px 0 16px;
        justify-content: flex-start;
      }

      @media (max-width: $md) {
        display: flex;
        justify-content: space-between;
      }
    }

    .filter-demand-item {
      padding: 12px 16px;
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      text-align: center;
      color: $neutralText;
      min-width: fit-content;
      margin: 0 8px;

      @include fontSubtitle2Md();

      @media (max-width: $lg) {
        font-size: 22px;
      }

      @media (max-width: $md) {
        padding: 12px 0;
        @include fontSubtitle2Sm();
      }

      &:last-child {
        .dropdown-menu {
          right: -16px;
        }
      }

      .dropdown-toggle {
        svg {
          &:last-child {
            transform: rotate(0deg);
            transition: all 0.5s;
          }
        }
      }
    }

    .filter-demand-item.show {
      .dropdown-toggle {
        svg {
          &:last-child {
            transform: rotate(180deg);
          }
        }

        span {
          color: $primary;
        }

        svg {
          path {
            fill: $primary;
          }
        }
      }
    }

    .dropdown-toggle {
      display: flex;
      transition: all 300ms;
      align-items: center;

      &:hover {
        svg {
          &:first-child {
            path {
              fill: $primary;
            }
          }
        }
      }

      > span {
        padding: 0 8px;
        display: inline-block;
        
        color: $neutralText;
        @include fontSubtitle2Md();
  
        @media (max-width: $md) {
          @include fontSubtitle2Sm();
        }
      }
    }

    .dropdown-menu {
      background: $neutralSurface;
      border-radius: 16px;
      overflow-y: auto;
      box-shadow: 0px 2px 2px rgba(57, 69, 49, 0.15),
        0px 4px 12px rgba(0, 0, 0, 0.2);
      max-width: calc(100vw - 28px);

      .dropdown-item {
        padding: 18px 24px;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        color: $neutralText;
        cursor: pointer;

        @include fontSubtitle2Md();

        @media (max-width: $md) {
          @include fontSubtitle2Sm();
        }

        &:hover,
        &:focus {
          background: $secondarySurface;
        }

        &::before {
          content: none;
        }
      }

      .dropdown-item.active {
        color: $primary;
      }
    }

    .filter-panel {
      margin-bottom: 0;
    }

    .filter-result {
      margin-bottom: 0;
      color: $neutralMiddle;

      @include fontHeader6Md();

      @media (max-width: $md) {
        @include fontHeader6Sm();
      }

      span {
        font-family: inherit;
        font-style: inherit;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        letter-spacing: inherit;
        text-transform: inherit;
      }

    }

    .filter-demand-selections {
      margin: 0 -8px 8px;
      display: flex;
      flex-wrap: wrap;

      .btn-negative-transparent {
        text-transform: none;

        @media (max-width: $md) {
          width: 100%;
          justify-content: center;
        }
      }
    }


    // no-results section
    .no-results__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0px 32px 64px;
      margin: 8px auto 0; 

      img {
        width: 220px;
        height: 220px;
      }

      .no-results__title {
        color: $neutralMiddle;
        text-align: center;

        @include fontHeader3Md();
      }

      .no-results__subtitle {
        padding-top: 8px;
        color: $neutralText;
        text-align: center;

        @include fontHeader5Md();
      }

      @media (max-width: $lg) {
        .no-results__title {
          @include fontHeader3Sm();
        }
  
        .no-results__subtitle {
          @include fontHeader5Sm();
        }
      }

      @media (max-width: $md) {
        & {
          padding: 64px 30px 128px;
        }

        img {
          display: none;
        }

        .no-results__title {
          @include fontHeader5Sm();
        }
  
        .no-results__subtitle {
          @include fontSubtitle1Sm();
        }
      }

    }

  }

  .filter-demand-label {
    background: $secondary;
    border-radius: 32px;
    padding: 8px;
    margin: 8px;
    border: none;
    position: initial;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 300ms;

    &:hover {
      background: $secondaryLight;
    }

    .filter-demand-slug {
      padding: 0 4px;
      color: $neutralText;
      @include fontOverlineMd();

      @media (max-width: $md) {
        @include fontOverlineSm();
      }
    }

    .filter-demand-close {
      position: initial;
      padding: 0;
      transform: none;
    }

    &.btn-negative-transparent {
      @include fontButtonMd();
    }
  }

  // Ondemand video list
  .demand-videos-list {
    margin-left: -8px;
    margin-right: -8px;
    padding-bottom: 48px;
  }

  .demand-item-col {
    padding-left: 8px;
    padding-right: 8px;
    flex: 0 0 calc(100% / 3);
    max-width: calc(100% / 3);
    margin: 8px 0;

    @media (max-width: $lg) {
      flex: 0 0 50%;
      max-width: 50%;
    }

    @media (max-width: $md) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }



  .demand-item-img {
    border-radius: 8px 8px 0 0;
  }

  .demand-item-content {
    background: $tetriarySurface;
    border-radius: 0 0 8px 8px;
    padding: 16px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    > a {
      width: 100%;
      margin-bottom: 8px;

      h5 {
        @include fontSubtitle1Md();
        color: $neutralText;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: center;

        @media (max-width: $lg ) {
          @include fontSubtitle1Sm();
        }
      }
    }

    .demand-item__line-wrapper {
      display: flex;
      width: 100%;

      .demand-item-info {
        width: unset;
        flex: 2 10 54%;

        overflow: hidden;

        .demand-item-info__detail {
          overflow: hidden;

          .context-1 {
            display: flex;

            a {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }

      }

      .demand-item-bubble-list {
        width: unset;
        flex: 1 0 46%;
      }

    }
  }

  .demand-item-info__avatar {
    width: 20px;
    min-width: 20px;
    height: 20px;
    border: 1px solid $neutralBorder;
    border-radius: 4px;
  }

  .demand-item-info__detail {
    width: auto;

    .context-1 {         
      margin-left: 8px;
      margin-right: 16px;
                
      a {
        display: inline-block;

        @include fontText1Md();
        color: $neutralMiddle;

        @media (max-width: $lg) {
          @include fontText1Sm();
        }
      }
    }
  }

  .demand-item-bubble-item {
    display: flex;
    align-items: center;

    h5 {
      color: $neutralMiddle;
      padding: 0 4px;
      white-space: nowrap;

      @include fontText1Md();

      @media (max-width: $lg) {
        @include fontText1Sm();
      }
    }

    &:last-child {
      background: $secondary;
      border-radius: 16px;
      padding: 4px;
      margin-left: 12px;

      h5 {
        @include fontOverlineMd();
        color: $neutralText;

        @media (max-width: $lg) {
          @include fontOverlineSm();
        }            
      }
    }
  }

  .demand-item-bubble-list {
    flex-wrap: nowrap;
    justify-content: flex-start;
    width: 46%;

    @media (max-width: 420px) {
      width: 50%;
    }
  }

  .demand-item-backdrop__wrapper {
    padding: 16px 16px 0;
    background: $tetriarySurface;
    align-content: baseline;
    align-items: center;

    & > a {
      width: 100%;
      margin-bottom: 8px;

      h5 {
        @include fontSubtitle1Md();
        color: $neutralText;
        text-align: center;

        @media (max-width: $lg) {
          @include fontSubtitle1Sm();
        }
      }
    }

    & > .demand-item-info {
      margin-bottom: 0;
    }

    & > .demand-item-detail {

      padding-top: 16px;
      text-align: center;
      
      p {
        
        color: $neutralText;
        @include fontText1Md();
        
        @media (max-width: $lg) {
          @include fontText1Sm();
        }
      }
      
    }
  }

  .demand-item-description {
    .demand-item-content {
      padding: 0;
      justify-content: center;

      .watch-class-btn-wrapper {
        width: 100%;
        min-height: auto;
        justify-content: center;
        padding-bottom: 16px;
        padding-top: 8px;

        a {
          outline: none;
          border-radius: 8px;
          padding: 12px 24px;
          @include fontButtonSm() ;
          color: $neutralText;
          background-color: $neutralSurface;

          transition: all 300ms;

          &:hover {
            background: rgba(255, 255, 255, 0.6);
          }

          &:active {
            background: rgba(255, 255, 255, 0.75);
          }
        }
      }
    }
  }

  .demand-item-backdrop {
    padding-top: 50%;
    border-radius: 0;
  }

  .demand-item-info {
    width: auto;
    width: 52%;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: flex-end;

    @media (max-width: 420px) {
      width: 47%;
    }
  }

  .demand-item-col {
    > .demand-item {
      height: 100%;
      display: flex;
      flex-direction: column;

      > .demand-item-content {
        flex-grow: 1;
      }
    }
  }



}
