.onboarding {
  margin-left: 15px;
  margin-right: 15px;
  border-radius: 15px;
  // overflow: hidden;

  &-wrapper {
    position: relative;
    padding: 25px;

    @include media-breakpoint-down(sm) {
      padding: 20px;
    }
  }

  &-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-tan;
  }

  .form {
    &-wrapper {
      width: 100%;
    }

    &-footer {
      display: flex;
      margin-top: 30px;
      align-items: center;

      @include media-breakpoint-down(sm) {
        display: block;
        width: 100%;
        text-align: center;
        margin: 40px 0;
      }
    }

    &-group {
      .form-control {
        color: $color-green-1;
        font-family: $font-header-regular;

        &::placeholder,
        &::-webkit-input-placeholder {
          text-transform: capitalize;
          color: $color-gray-7;
        }
      }
    }
  }

  &-plan {
    @include media-breakpoint-down(sm) {
      display: none;
    }
  }

  &-checkout {
    .form-control {
      font-size: 20px;

      @include media-breakpoint-down(md) {
        font-size: 18px;
      }

      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }

    .intl-payment-wrap {
      margin-top: 15px;
    }
  }

  .title-wrapper {
    margin-bottom: 20px;

    h3 {
      margin-bottom: 1.5rem;

      @include media-breakpoint-down(lg) {
        font-size: 35px;
      }
    }

    h1 {
      margin-bottom: 1.5rem;
      font-family: protogrotesk-extralight,sans-serif;
      font-weight: 300;
      font-size: 48px;
      color: #404040;
      line-height: 110%;
      font-style: normal;
      font-weight: 400;
      @include media-breakpoint-down(lg) {
        font-size: 35px;
      }
    }

    p {
      margin-bottom: 0;
      font-size: 18px;
      color: $color-black-1;

      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }

    ul {
      li {
        display: block;
        color: $color-black-1;
        font-size: 18px;

        @include media-breakpoint-down(md) {
          font-size: 14px;
        }

        &:before {
          content: "";
          display: inline-block;
        }
      }
    }
  }

  .img-wrapper {
    border-radius: 15px;
    overflow: hidden;

    @include media-breakpoint-down(sm) {
      display: none;
    }
  }

  .btn {
    &-group {
      display: block;
    }

    font-family: $font-header-regular;
    font-size: 22px;
    line-height: 110%;
    color: $color-white;
    background: $bubble-bg-pink-dark;

    &:focus {
      outline: none;
      box-shadow: unset;
    }

    &:not(:disabled):active {
      background-image: unset;
      background: $bubble-bg-pink-dark;
      color: $color-white-1;
    }

    @include media-breakpoint-down(lg) {
      font-size: 16px;
    }

    @include media-breakpoint-down(sm) {
      font-size: 14px;
    }

    &-circle {
      @include initBubbleSize(150px, 150px);

      @include media-breakpoint-down(lg) {
        @include initSize(100px, 100px);
      }

      @include media-breakpoint-down(sm) {
        @include initSize(90px, 90px);
      }
    }
  }
}
