.timer {
  display: flex;
  width: auto;
  height: 25px;
  color: #fffedf;
  position: relative;

  @include media-breakpoint-down(md) {
    margin-top: 2px;
  }

  &--clock {
    width: 100%;
    position: relative;
    overflow: hidden;

    .clock-display-grp {
      width: auto;
      position: relative;
      top: -8px;
      float: left;

      &.minutes-group {
        & + .clock-separator {
          p {
            margin-right: 5px;
          }
        }
      }

      .number-grp {
        width: auto;
        float: left;
        overflow: hidden;

        .number-grp-wrp {
          width: 100%;
          position: relative;

          .num {
            width: 100%;
            position: relative;
            text-align: center;

            p {
              width: auto;
              margin: 0;
              display: block;
              font-size: 28px;

              @include media-breakpoint-down(md) {
                font-size: 22px;
              }
            }
          }
        }
      }
    }

    .clock-separator {
      width: auto;
      float: left;
      position: relative;
      top: -8px;

      p {
        width: auto;
        margin: 0;
        display: block;
        font-size: 28px;

        @include media-breakpoint-down(md) {
          font-size: 22px;
        }
      }
    }
  }
}
