@import "./redesignedTypography";


// Media breakpoints
$xs: 360px;
$sm: 590px;
$md: 767px;
$lg: 1167px;
$xl: 1440px;
$xxl: 1920px;

// Fonts
$inter: "Inter", sans-serif;
$poppins: "Poppins", sans-serif;

// Colors

// Common 
$black: #000000;


// Neutral
$neutralText: #394531;
$neutralSurface: #ffffff;
$neutralLight: #b4b6b3;
$neutralMiddle: #666c63;
$neutralBorder: #e3e5e3;
$neutralSilver: #E8E8E8;

// Primary
$primary: #ff7a00;
$primaryAccented: #d96800;
$primarySurface: #ffdebf;
$primaryLight: #ff9c40;

// Secondary
$secondary: #cfddc7;
$secondaryLight: #e1eadc;
$secondaryAccented: #b9c5b2;
$secondarySurface: #f3f7f1;

// Tetriary
$tetriary: #B3B0A7;
$tetriaryLight: #DFDBD6;
$tetriarySurface: #f5f1ed;

// Negative
$negative: #d82d4c;
$negativeHover: #e46070;
$negativeAccented: #bb0137;
// $negativeSurface: #feedee;
$negativeSurface: #FDDADC;

// Positive
$positiveAccented: #019F44;

// Hover
$hovered: 0px 2px 2px rgba(57, 69, 49, 0.15), 0px 4px 12px rgba(0, 0, 0, 0.2);

// Shadows
// Header shadow
$headerShadow: 0px 4px 4px rgba(57, 69, 49, 0.1);;


.redesigned-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  footer,
  header,
  .wrapper {
    max-width: 100%;
    margin: 0;
  }

  a,
  div,
  p,
  span,
  label,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  ol,
  ul {
    margin: 0;
    word-break: break-word;
  }

  a,
  div,
  p,
  span,
  label,
  ol,
  ul,
  button {
    font-family: $inter;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: $poppins;
  }

  .wrapper {
    flex-grow: 1;
  }

  div,
  p,
  ul,
  ol {
    &::-webkit-scrollbar {
      width: 8px;
    }
  }

  div,
  p,
  ul,
  ol {
    &::-webkit-scrollbar-track {
      background: $secondarySurface;
      border-radius: 8px;
    }
  }

  div,
  p,
  ul,
  ol {
    &::-webkit-scrollbar-thumb {
      background: $secondary;
      border-radius: 8px;
    }
  }

  a.common {
    // color: $neutralSurface;
    // letter-spacing: 1px;
    // text-transform: uppercase;
    // font-weight: 600;
    // font-size: 12px;
    // line-height: 16px;
    position: relative;
    display: block;
    overflow: hidden;
    text-decoration: none;
    width: fit-content;
    transition: all 300ms;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $primary;
      opacity: 0;
      transform: translate3d(-100%, 0, 0);
      transition: opacity 500ms, transform 500ms;
    }

    &:hover {
      &:after {
        transform: translate3d(0, 0, 0);
        opacity: 1;
      }
    }

    &:focus {
      color: $primaryAccented;

      &:after {
        background-color: $primaryAccented;
      }
    }
  }

  a.nav-item {
    background: $secondary;
    padding: 24px 32px;
    font-weight: 600;
    font-size: 20px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    color: $neutralMiddle;
    text-decoration: none;
    transition: all 300ms;

    &:hover {
      color: $neutralText;
    }

    &:focus {
      color: $primaryAccented;
    }
  }

  // a.social-link {
  //   padding: 8px;
  //   background: $neutralSurface;
  //   border-radius: 50%;
  //   display: block;
  //   transition: all 300ms;

  //   &:hover {
  //     background: $secondaryLight;
  //   }

  //   &:focus {
  //     background: $secondaryAccented;
  //   }
  // }

  .subTitle1 {
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
    color: $neutralText;
    padding: 16px 0;
  }

  .btn-negative,
  .btn-transparent,
  .btn-primary {
    padding: 16px 24px;
    border-radius: 8px;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    text-align: center;
    display: block;
    text-decoration: none;
    transition: all 0.3s;
  }

  .btn-negative {
    background: $negative;
    color: $neutralSurface;

    &:hover {
      background: $negativeHover;
    }

    &:focus {
      background: $negativeAccented;
    }
  }

  .btn-transparent {
    background: transparent;
    color: $neutralText;

    &:hover {
      color: $primary;
    }

    &:focus {
      color: $primaryAccented;
    }
  }

  .btn-primary {
    background: $primary;
    color: $neutralSurface;

    &:hover {
      background: $primaryLight;
    }

    &:focus {
      background: $primaryAccented;
    }
  }

  .button-rectangle-outline-md {
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 24px;

    border: 1px solid $neutralText;
    border-radius: 8px;
    @include fontButtonMd();
    word-break: keep-all;
    
    color: $neutralText;
    text-decoration: none;
    transition: all 300ms;

    &:hover {
      background: rgba(185, 197, 178, 0.25);
    }

    &:active {
      background: transparent;
      border: 2px solid $neutralText;  
      padding: 15px 23px;
      transition: unset;
    }
  }

  .button-rectangle-colored-md {
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 24px;

    border: none;
    border-radius: 8px;
    @include fontButtonMd();
    word-break: keep-all;

    color: $neutralText;
    background: $secondary;
    text-decoration: none;
    transition: all 300ms;

    &:hover {
      background: $secondaryLight;
    }

    &:active {
      background: $secondaryAccented;
      transition: unset;
    }
  }


  .button-rectangle-colored-primary-md {
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 24px;

    border: none;
    border-radius: 8px;
    @include fontButtonMd();
    word-break: keep-all;

    color: $neutralSurface;
    background: $primary;
    text-decoration: none;
    transition: all 300ms;

    &:hover {
      background: $primaryLight;
    }

    &:active {
      background: $primaryAccented;
      transition: unset;
    }
  }

  .button-rectangle-clear-md {
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 24px;

    border: none;
    border-radius: 8px;
    @include fontButtonMd();
    word-break: keep-all;

    color: $neutralText;
    background: transparent;
    text-decoration: none;
    transition: all 300ms;

    &:hover {
      color: $primary;
    }

    &:active {
      color: $primaryAccented;
    }
  }
 

  .wrapper {
    .btn-negative-transparent {
      padding: 8px 18px;
      border: 1px solid $negative;
      border-radius: 8px;
      font-weight: 500;
      font-size: 18px;
      line-height: 20px;
      text-align: center;
      color: $negative;
      background: transparent;
      transition: all 300ms;

      &:hover {
        background: $negativeSurface;
      }

      &:focus {
        border: 1px solid $negativeAccented;
        color: $negativeAccented;
      }
    }
  }

  h2 {
    display: flex;
    align-items: center;

    font-weight: 500;
    font-size: 72px;
    line-height: 80px;
    letter-spacing: -1px;

    color: $neutralText;

    @media (max-width: $lg) {
      font-size: 48px;
      line-height: 56px;
    }
  }

  h6 {
    display: flex;
    align-items: center;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;

    letter-spacing: 1.5px;
    text-transform: uppercase;

    color: $neutralMiddle;

  }
}
