.video-block {
  &-wrapper {
    position: relative;
  }

  video {
    width: 100%;
    height: calc(100vh * 2 / 3);
    object-fit: cover;
  }

  .header-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 600px;
    width: 100%;
    margin-bottom: 0;
    transform: translate(-50%, -50%);
    text-align: center;
    color: $color-white;

    h3 {
      margin-bottom: 30px;
    }
  }

  @include media-breakpoint-down(sm) {
    .header-wrapper {
      max-width: 300px;
    }
  }
}

.logo-block {
  &-wrapper {
    position: relative;
    padding-bottom: 50px;

    @include media-breakpoint-down(sm) {
      padding-bottom: 0px;
    }
  }

  .slick-track {
    display: flex;
    align-items: center;
  }

  .img-today {
    height: 78px;
  }

  .img-usa-today {
    height: 35px;
  }

  .logo-list {
    display: flex;
    align-items: center;
  }

  .logo-item {
    padding: 0 20px;
    text-align: center;
    outline: none;

    &-img {
      display: flex;
      min-height: 78px;

      img {
        margin: auto auto 0;
      }
    }

    .description-wrapper {
      margin-top: 20px;
      color: $color-gray-4;
      font-style: italic;
    }
  }

  @include media-breakpoint-down(md) {
    &.custom-control-slick {
      .slick-arrow {
        top: 50%;
        transform: translateY(-50%);
      }

      .slick-prev {
        left: 0;
      }

      .slick-next {
        right: 0;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .logo-item {
      padding: 0;
    }
  }
}

.benefits-block {
  &-wrapper {
    width: auto;
    max-width: 820px;
    margin: auto;
    text-align: center;
  }

  .benefits-list {
    padding: 0 50px;
  }

  .description-wrapper {
    margin-top: 30px;
  }

  .slick-track {
    display: flex;
    align-items: center;
  }

  .slick-slide {
    transition: all 0.5s;

    &:not(.slick-current) {
      opacity: 0;
      transform: scaleY(0.5);
    }
  }

  .slick-arrow {
    display: inline-block;
    top: 50%;
    width: auto;
    height: auto;
    padding: 15px;
    border: solid $border-green-1;
    border-width: 0 2px 2px 0;

    &.slick-disabled {
      border-color: $border-grey-7;
      pointer-events: none;
    }

    &:before {
      display: none;
    }
  }

  .slick-prev {
    left: 7px;
    transform: translateY(-50%) rotate(135deg);
  }

  .slick-next {
    right: 7px;
    transform: translateY(-50%) rotate(-45deg);
  }

  @include media-breakpoint-down(sm) {
    .benefits-list {
      padding: 0 30px;
    }

    .description-wrapper {
      margin-top: 15px;

      p {
        font-size: 18px;
      }
    }

    .slick-arrow {
      padding: 7px;
    }
  }
}

.style-block {
  .slick-arrow {
    position: absolute;
    z-index: 1;
  }

  .slick-next {
    right: 0;
  }

  &-wrapper {
    position: relative;
    padding-bottom: 50px;
  }

  &-header {
    max-width: 950px;
    width: 100%;
    margin: auto;
    padding: 0 15px;
    text-align: center;
  }

  img {
    height: 150px;
    margin: auto;
  }

  .style-list {
    margin-top: 50px;
  }

  .style-item {
    text-align: center;

    .header-wrapper {
      margin-top: 30px;

      h3 {
        font-size: 28px;
        text-transform: uppercase;
      }
    }
  }

  .description-wrapper {
    p {
      font-size: 16px;
    }

    span {
      display: inline-flex;
      vertical-align: bottom;
      align-items: center;

      &:before {
        content: "\A";
        display: inline-flex;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: #000;
        margin: 0 7px;
      }
    }
  }

  .slick-slider {
    margin-bottom: 1rem;
  }

  .slick-dots button {
    width: 15px;
    height: 15px;
  }

  @include media-breakpoint-down(sm) {
    &.center-control-slick {
      .slick-prev,
      .slick-next {
        margin: 0;
      }
    }

    &-wrapper {
      padding-bottom: 0;
    }

    .style-item {
      .header-wrapper {
        h3 {
          font-size: 20px;
        }
      }
    }
  }
}

.testimonial-block {
  .slick-arrow {
    position: absolute;
    z-index: 2;
  }

  .slick-next {
    right: 0;
  }

  .header-wrapper {
    max-width: 950px;
    width: 100%;
    margin: auto;
    padding: 0 15px;
    text-align: center;
  }

  &-wrapper {
    position: relative;
    padding-bottom: 50px;
  }

  &-list {
    margin-top: 50px;
  }

  &-item {
    text-align: center;
  }

  .description-wrapper {
    font-style: italic;
  }

  &-author,
  &-title {
    color: $color-green-3;
    font-family: $font-header-regular;
  }

  &-author {
    display: flex;
    margin-top: 15px;
    margin-bottom: 0;
    align-items: center;
    justify-content: center;
    font-size: 28px;

    &:before {
      content: "";
      display: block;
      width: 20px;
      height: 3px;
      margin-right: 15px;
      background: $bg-green-2;
    }
  }

  .slick-slider {
    margin-bottom: 1rem;
  }

  .slick-dots button {
    width: 15px;
    height: 15px;
  }

  @include media-breakpoint-down(sm) {
    &.center-control-slick {
      .slick-prev,
      .slick-next {
        margin: 0;
      }
    }

    &-list {
      margin-top: 30px;
    }

    &-author {
      font-size: 18px;
      white-space: nowrap;
    }

    &-title {
      font-size: 12px;
    }

    .description-wrapper {
      p {
        font-size: 18px;
      }
    }
  }
}

.card-block {
  .card-item {
    &-col {
      display: flex;
      align-items: center;
    }

    &-img {
      border-radius: 15px;
      overflow: hidden;
    }
  }

  .header-wrapper {
    h3 {
      font-size: 38px;
    }
  }

  .description-wrapper {
    margin-top: 5%;
  }

  @include media-breakpoint-down(md) {
    .header-wrapper {
      h3 {
        font-size: 30px;
      }
    }

    .description-wrapper {
      p {
        font-size: 18px;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .card-item-wrapper {
      padding: 30px 15px 0;
    }

    .header-wrapper {
      h3 {
        font-size: 26px;
      }
    }
  }
}

.padding-section {
  padding-bottom: 100px;

  @include media-breakpoint-down(md) {
    padding-bottom: 50px;
  }
}
