.vertical-line {
  position: absolute;
  height: 100%;
  width: 1px;
  left: calc(65/1440*100%);
  background: $bg-grey;
  z-index: -10;

  @include media-breakpoint-down(sm) {
    display: none;
  }
}

.wrapper-vertical-line {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  left: calc(-65/1440*100%);
  z-index: 200;
  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;

  &.rotate-line {
    visibility: visible;
    opacity: 1;

    .content-vertical-line {
      @include transform(rotate(-90deg));
      transform-origin: 0 0;
    }
  }

  @include media-breakpoint-down(sm) {
    display: none;
  }

  .content-vertical-line {
    .text-vertical-line {
      margin: 0;
      padding: 7px 30px;
      color: $color-white;
      @extend %heading-font;
      font-size: 16px;
      text-align: center;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      border-radius: 19.5px;
    }
  }
}

@include media-breakpoint-down(xs) {
  .vertical-line {
    left: calc(25/414*100%);
  }

  .wrapper-vertical-line {
    left: calc(-25/1440*100% + 4px);

    .content-vertical-line {
      .bg-content {
        padding: 4px 20px;
      }
    }
  }
}

.grid-space-small {
  .row {
    .wrapper-vertical-line {
      left: calc(-70/1440*100%);
    }
  }
}
