    background: #f18d7e;
    border-radius: 15px;
    transition: all 0.5s; }
  .icon-plus:before {
    left: 50%;
    top: 4px;
    bottom: 4px;
    width: 4px;
    transform: translateX(-50%) rotate(-90deg); }
  .icon-plus:after {
    top: 50%;
    left: 4px;
    right: 4px;
    height: 4px;
    transform: translateY(-50%) rotate(-90deg); }
  @media (max-width: 767.98px) {
    .icon-plus {
      padding: 18px; } }
  .icon-plus--gray:before, .icon-plus--gray:after {
    content: "";
    position: absolute;
    background: #92958b;
    border-radius: 15px;
    transition: all 0.5s; }

.icon-close {
