.dailyco-player-page {
  .w8-record-stop {
    margin-right: 600px;
    margin-top: 17px;
    float: right;
  }

  .dot-pulse:after {
    content: ' . . .';
    animation: dots 2s steps(5, end) infinite;
  }

  @keyframes dots {
    0%, 20% {
      color: rgba(0, 0, 0, 0);
      text-shadow: .25em 0 0 rgba(0, 0, 0, 0),
      .5em 0 0 rgba(0, 0, 0, 0);
    }
    40% {
      color: white;
      text-shadow: .25em 0 0 rgba(0, 0, 0, 0),
      .5em 0 0 rgba(0, 0, 0, 0);
    }
    60% {
      text-shadow: .25em 0 0 white,
      .5em 0 0 rgba(0, 0, 0, 0);
    }
    80%, 100% {
      text-shadow: .25em 0 0 white,
      .5em 0 0 white;
    }
  }

  .show-custom-users-list-box {
    height: 200px;
    width: 400px;
    border: 1px black solid;
    position: absolute;
    top: 58px;
    z-index: 999999;
    float: right;
    left: 234px;
    background: white;
    display: none;
  }

  .show-custom-users-list-ul {
    padding: 10px;
    margin-top: 5px;
  }

  .close-custom-users-list-ul {
    float: right;
    padding-right: 9px;
    font-size: 17px;
    color: black;
    font-family: monospace;
  }

  .show-custom-users-list-ul li {
    border-bottom: 1px solid #8080804f;
    padding-bottom: 5px;
    padding-top: 5px;
  }

  .show-custom-users-list-box .action-btns {
    float: right;
  }

  .show-custom-users-list-ul li a {
    color: #5a7176;
    margin-right: 5px;
  }

  .removed-from-playlist-popup-text {
    color: #fff;
    font-size: 16px;
    line-height: 20px;
    margin-right: 32px;
  }


  .removed-from-playlist-popup {
    padding: 20px 32px;
    background-color: rgba(20, 78, 90, .9);
    z-index: 10001;
    position: fixed;
    bottom: 24px;
    left: 72px;
    display: none;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }

  .control-box a {
    color: midnightblue;
    margin-right: 40px;
  }

}
