.dailyco-player-page {
  .instructor-ctrl-block {
    display: flex;
    font-family: protogrotesk-regular, sans-serif;
  }

  .ctrl-left-block {
    background-color: #f8eeda;
    width: 19%;
    margin-right: 2px;
    border-radius: 20px 0 0 20px;
    padding: 4px;
    float: left;
    min-width: 238px;
    display: none !important;
  }

  .ctrl-left-block-topbar-text {
    float: left;
  }

  .ctrl-left-block-topbar {
    font-size: 13px;
    margin-top: 6px;
    margin-bottom: 16px;
    margin-left: auto;
    margin-right: auto;
    width: 183px;
  }

  .ctrl-left-block-topbar-incons {
    position: relative;
    bottom: 3px;
    left: 7px;
  }

  .ctrl-topbar-icon {
    float: left;
    display: none;
  }

  .ctrl-topbar-icon-muted {
  }

  .ctrl-topbar-videodisabled {
  }

  .ctrl-left-block-btns {
    margin: auto;
    margin-top: 39px;
    width: 220px;
    position: relative;
    top: 5px;
    display: flex;
  }

  .ctrl-rec-btn {
    cursor: pointer;
    color: white;
    cursor: not-allowed;
  }

  .rec-btn-left {
    border-radius: 30px 0 0 30px;
    margin-right: 2px;
    height: 44px;
    padding: 11px 20px;
    display: block;
    font-weight: bold;
    margin-bottom: 30px;
    margin-right: 2px;
    white-space: nowrap;
  }

  .rec-btn-right {
    border-radius: 0 40px 40px 0;
    height: 44px;
    display: block;
    width: 66px;
  }

  .rec-svg {
    width: 33px;
    height: 42px;
    margin-left: 10px;
  }

  .top-bar-svg {
    width: 18px;
    margin-right: 2px;
  }

  .ctrl-mid-block {
    float: left;
    background-color: #f8eeda;
    margin-right: 2px;
    width: 70%;
    border-radius: 20px 0 0 20px;
  }

  .ctrl-btn-msg {
    cursor: not-allowed !important;
  }

  .ctrl-mid-block-topbar {
    margin-top: 10px;
    text-align: center;
    color: gray;
    font-size: 14px;
  }

  .ctrl-mid-block-btns {
    margin: auto;
    width: 250px;
    margin-top: 11px;
  }

  .ctrl-mid-block-btns-titles {
    margin: auto;
    ont-size: 13px;
    margin-top: 6px;
    margin-bottom: 16px;
    margin-left: auto;
    margin-right: auto;
    width: 183px;
    width: 250px;
    margin-top: 2px;
    position: relative;
    right: 5px;
  }

  .btn-title-msg {
    float: left;
    font-size: 14px;
    margin-right: 49px;
  }

  .ctrl-btn-mute {
    margin-left: 53px;
  }

  .ctrl-mute-all-dellay {
    margin-left: 53px !important;
  }

  .btn-title-mute {
    float: left;
    font-size: 14px;
    margin-right: 10px;
    margin-left: 63px;
  }

  .btn-title-blockvideo {
    float: left;
    font-size: 14px;
    position: relative;
    left: 32px;
  }

  .ctrl-btn {
    border-radius: 50px;
    border: 2px solid black;
    color: black !important;
    font-size: 13px;
    width: 48px;
    font-weight: normal;
    display: block;
    height: 48px;
    padding: 10px 0px 10px 10px;
    float: left;
    margin: 0 50px 0 0;
    cursor: pointer;
  }

  .ctrl-btn-blockvid {
    margin-right: 0px;
  }

  .ctrl-right-block {
    float: left;
    background-color: #f8eeda;
    float: left;
    width: auto;
    background-color: #f8eeda;
    border-radius: 0 20px 20px 0;
    flex-grow: 1;
    min-width: 105px;
  }

  .ctrl-right-block-topbar {
    margin-top: 10px;
    text-align: center;
    color: gray;
    font-size: 14px;
  }

  .ctrl-right-block-btns {
    margin-top: 10px;
    display: flex;
  }

  .ctrl-btn-kick {
    border: 2px solid red;
    color: red !important;
    margin: auto;
  }

  .ctrl-right-block-btns-titles {
    margin: auto;
    margin-top: 2px;
    position: relative;
    right: 5px;
  }

  .btn-title-kick {
    font-size: 14px;
    color: red;
    width: 20px;
    margin: auto;
  }

  .status-block {
    color: gray;
    margin-left: 3px;
  }

  .rec-green {
    background-color: #1dab9d;
  }

  .dropdown-menu {
    display: none;
    position: absolute;
    transform: translate3d(0px, 60px, 0px);
    top: 0px;
    left: 0px;
    will-change: transform;
    overflow-y: clip;
  }

  .dropdown-item {
    cursor: pointer;
  }

  .removed-from-playlist-popup {
    display: none;
    background-color: #f8eeda;
    position: fixed;
    bottom: 50px;
    left: 50px;
    border-radius: 20px;
  }

  .removed-from-playlist-popup-text {
    padding: 25px;
    display: block;
    font-family: protogrotesk-regular, sans-serif;
    padding-right: 28px;
    color: gray;
  }

  .row-c-title {
    background-color: #f8eeda;
    border-radius: 20px;
    text-align: center;
  }

  .c-title {
    padding: 25px;
    margin: auto;
    font-family: protogrotesk-regular, sans-serif;
  }

  .disalow-btn {
    color: white;
    background-color: #e58b7e;
    border-radius: 40px;
  }

  .btn-group {
    position: relative;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    vertical-align: middle;
  }

  .btn-circle {
    width: 150px !important;
    height: 150px !important;
    border-radius: 50%;
    position: relative;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
  }

  .btn {
    font-family: protogrotesk-regular, sans-serif;
    font-size: 22px;
    line-height: 110%;
    color: #fff;
    background: #e58b7e;
  }

  .zoom-effect {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
    -webkit-transition: -webkit-transform 250ms ease-in-out;
    transition: -webkit-transform 250ms ease-in-out;
    -o-transition: -o-transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out,
    -o-transform 250ms ease-in-out;
    -webkit-font-smoothing: subpixel-antialiased;
    -webkit-transform-origin: center center;
    -ms-transform-origin: center center;
    -o-transform-origin: center center;
    transform-origin: center center;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }

  #banned-block {
    margin-top: 40px;
  }

  .zoom-effect:hover {
    color: white;
  }

  .form-footer {
    margin-left: 40px;
  }

  .ban-mid-box {
    margin-top: 50px;
    border: 2px solid #f18d7e;
    padding: 23px 28px 23px 28px;
    border-radius: 11px;
    font-weight: bold;
    font-family: AkkuratLLWeb-Regular, sans-serif;
    font-style: normal;
    font-size: 19px;
    font-color: #373d3c !important;
  }

  .ban-mid-box {
    margin-bottom: 7px;
  }

  .gr-txt {
    color: #49818d;
    font-size: 30px;
  }

  .rotate-180 {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
    position: relative;
    bottom: 2px;
  }

  .ctrl-expand-arrow {
    margin-left: 5px;
    cursor: pointer;
  }

  .ctrl-expand-block {
    display: none;
    background-color: #f8eeda;
    border-radius: 20px;
    color: #f18d7e;
    text-align: center;
    padding: 20px;
    margin-bottom: 14px;
    text-transform: uppercase;
    cursor: pointer;
  }

  @media (max-width: 1199.98px) {
    #ask_for_other_instructor .form-footer,
    #banned-block .form-footer {
      margin-left: 11px;
    }

    #ask_for_other_instructor .onboarding .btn,
    #banned-block .onboarding .btn {
      font-size: 22px;
    }
  }

  @media (max-width: 767.98px) {
    #ask_for_other_instructor .title-wrapper,
    #banned-block .title-wrapper {
      text-align: center;
      padding: 0 25% 0 25%;
    }

    #ask_for_other_instructor .title-wrapper p,
    #banned-block .title-wrapper p {
      font-size: 16px !important;
    }

    .ban-mid-box {
      text-align: center;
      margin: 20px 10% 10px 10%;
    }
  }

  @media (max-width: 1055px) {
  }

  @media (max-width: 650px) {
    .expanding-block {
      display: none;
    }

    .ctrl-expand-block {
      display: block;
    }

    .instructor-ctrl-block {
      display: block;
    }

    .ctrl-left-block {
      height: 115px;
      width: 100%;
      float: none;
      border-radius: 20px 20px 0 0;
      display: none !important;
    }

    .ctrl-mid-block {
      width: 100%;
      height: 116px;
      float: none;
      border-radius: 20px 20px 0 0;
    }

    .ctrl-right-block {
      width: 100%;
      float: none;
      border-radius: 0 0 20px 20px;
    }

    .ctrl-mid-block-topbar {
      margin-top: 2px;
    }

    .ctrl-right-block-topbar {
      padding-top: 10px;
      margin-top: 2px;
    }

    .ctrl-right-block-btns-titles {
      padding-bottom: 10px;
    }
  }

  [type="button"],
  [type="reset"],
  [type="submit"],
  button {
    -webkit-appearance: button;
  }

  #ask_for_other_instructor {
    margin-top: 60px;
  }

  #ask_for_other_instructor .account-plan-select-item p {
    white-space: nowrap;
  }

  #ask_for_other_instructor .account-plan-select-item-wrapper {
    position: relative;
    padding-left: 58px;
    align-items: center;
  }

  #ask_for_other_instructor .account-plan-select-item-wrapper::before {
    content: "";
    position: absolute;
    left: 14px;
    top: 18px;
    width: 34px;
    height: 34px;
    border: 1px solid #8d9690;
    border-radius: 100%;
    background: #fff;
  }

  #ask_for_other_instructor
  .account-plan-select-item
  input[type="radio"]:checked
  ~ .account-plan-select-item-wrapper::after {
    content: "";
    width: 18px;
    height: 18px;
    background: #f18d7e;
    position: absolute;
    top: 26px;
    left: 22px;
    border-radius: 100%;
    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
  }

  #ask_for_other_instructor .title-wrapper {
    margin-bottom: 84px;
  }

  @media (max-width: 575.98px) {
    .account-plan-select-item-info {
      padding: 0;
    }
  }

  @media (max-width: 1280px) {
    #ask_for_other_instructor .title-wrapper {
      margin-bottom: 48px;
    }
  }

  @media (max-width: 1070px) {
    #ask_for_other_instructor .title-wrapper {
      margin-bottom: 32px;
    }
  }
}
