.bubble-wrap {
  position: absolute;
  right: calc(5% + 30px);
  bottom: 8%;
  display: inline-block;

  .fuzzy-bubble {
    @include initFuzzyBubble($width: 150px, $height: 150px);
    filter: blur(3px);

    &.solid-color {
      background: $bg-pink;
      filter: none;
    }

    @include media-breakpoint-down(md) {
      @include initFuzzyBubble(120px, 120px);
    }
  }

  .description-bubble {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
  }

  .description-bubble__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
  }

  .description-bubble__text {
    border-radius: 50%;
    overflow: hidden;

    .text-in-bubble {
      transition: all 0.5s ease;
    }

    &:hover {
      .text-in-bubble {
        @include media-breakpoint-up(lg) {
          transform: scale(1.05);
        }
      }
    }

    .text-in-bubble {
      font-size: 28px;

      @include media-breakpoint-down(md) {
        font-size: 22px;
      }
    }
  }

  .text-in-bubble {
    display: flex;
    position: relative;
    margin-bottom: 0;
    align-items: center;
    text-align: center;
    align-self: center;
    color: $color-white;
  }

  .time-wrapper {
    display: flex;
    width: 100%;
    justify-content: center;

    &::before, &::after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      height: 20px;
      z-index: 10;
    }

    .time-part-wrapper {
      display: inline-block;
      position: relative;
      padding: 0 15px;

      &::after {
        display: block;
        width: 32px;
        height: 230px;
        position: absolute;
        top: 2px;
        right: -15px;
        color: $color-white;
        font-size: 28px;
        line-height: 0.9;
      }

      &:not(:last-child)::after {
        content: "m";
      }

      &:last-child::after {
        content: "s";
      }
    }

    .time-part {
      width: 20px;
      text-align: center;
      height: 40px;
      display: inline-block;
      overflow: hidden;
      margin-left: -5px;
      box-sizing: border-box;

      .digit-wrapper {
        animation-timing-function: $easing;

        .digit {
          display: inline-block;
          font-size: 28px;
          color: $color-white-1;
          height: $digit-height;
          line-height: 110%;
        }
      }

      &.minutes {
        &.tens .digit-wrapper {
          animation-name: minutes-tens;
          animation-duration: $one-second * 10 * 6 * 10 * 6;
          animation-iteration-count: 1;
        }

        &.ones .digit-wrapper {
          animation-name: minutes-ones;
          animation-duration: $one-second * 10 * 6 * 10;
          animation-iteration-count: 6;
        }
      }

      &.seconds {
        &.tens .digit-wrapper {
          animation-name: seconds-tens;
          animation-duration: $one-second * 10 * 6;
          animation-iteration-count: 60;
        }

        &.ones .digit-wrapper {
          animation-name: seconds-ones;
          animation-duration: $one-second * 10;
          animation-iteration-count: 360;
        }
      }
    }
  }
}

@keyframes minutes-tens {
  @include animateCountDown(6);
}

@keyframes minutes-ones {
  @include animateCountDown(10);
}

@keyframes seconds-tens {
  @include animateCountDown(6);
}

@keyframes seconds-ones {
  @include animateCountDown(10);
}

.bubble {
  &-list {
    position: relative;
    display: flex;
    width: 30%;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
  }

  &-item {
    position: relative;
    margin-bottom: 5px;
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, .1);

    &__wrapper {
      position: relative;
    }
  }

  &-circle {
    @include initBubbleSize(60px, 60px);
  }

  &-content {
    position: absolute;
    top: 50%;
    left: 50%;
    @include transform(translate(-50%, -50%));
  }
}
