.yah-carousel-widget {
  padding-top: 2em;
  padding-bottom: 2em;

  &.featured-carousel {
    background-color: $bg-grey;
  }

  .carousel-widget-wrapper {
    position: relative;
  }

  .demand-item {
    &-backdrop {
      padding-top: 50%;

      .demand-item-backdrop__wrapper {
        padding: 15px 0 15px 15px;

        @include media-breakpoint-down(md) {
          padding: 15px;
        }
      }
    }

    &-description {
      .demand-item-content {
        .demand-item-bubble-list {
          min-height: 57px;
        }
      }
    }
  }

  @include media-breakpoint-up(sm) {
    .demand-item {
      &-info {
        &__avatar {
          @include initSize(60px, 60px);
        }

        &__detail {
          h5 {
            font-size: 20px;
          }
        }
      }

      &-content {
        margin-top: -20px;
        padding: 0 15px;
      }

      &-bubble {
        &-item {
          &__wrapper {
            @include initSize(40px, 40px);
          }
        }

        &-content {
          h5 {
            font-size: 20px;
          }
        }
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .demand-item {
      margin-bottom: 0;

      &-info {
        &__detail {
          .context-2 {
            margin-bottom: 0;
          }
        }
      }

      &-backdrop {
        padding-top: 40%;
      }
    }
  }

  .slick-dotted {
    &.slick-slider {
      @include media-breakpoint-up(xs) {
        margin-bottom: 1em;
      }
    }
  }
}
