.redesigned-wrapper {
    // FOOTER
    footer {

      .footer-bottom {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        
        background-color: $tetriarySurface;
        
        padding: 0px 32px;
  
        .footer-bottom_links {
          display: flex;
          align-items: center;
          justify-content: space-between;
          gap: 40px; 
          padding: 14px 0;
  
          a.common {
            padding: 8px 0;
            @include fontText2Md();
            color: $neutralMiddle;
            text-transform: unset
          }
        }
  
  
        .footer-bottom__social-links {
          display: flex;
          gap: 8px;

          padding: 12px 16px;
  
          a.social-link {
            img {
              width: 24px;
              height: 24px;
            }
            padding: 8px;
          }
  
        }
  
        .footer-bottom__text {
          @include fontText2Md();
          color: $tetriary;
        }

        @media (max-width: $lg) {
          flex-wrap: wrap;
          padding: 0;
          background-color: $neutralSurface;

          .footer-bottom_links {
            gap: 32px;
            order: 2;
            padding: 6px 16px;
          }
    
    
          .footer-bottom__social-links {
            order: 1;
            width: 100%;
            justify-content: space-between;
            
            background-color: $tetriarySurface;
            padding: 12px 16px;   
          }
    
          .footer-bottom__text {
            display: flex;
            order: 3;
            padding: 14px 16px;
          }
        }

        @media (max-width: $md) {

          .footer-bottom_links {
            gap: unset;
            width: 100%;
            flex-wrap: wrap;
            order: 2;
            padding: 6px 16px;

            & a.common:nth-child(3) {
              display: flex;
              justify-content: center;
              width: 100%;
            }
          }
        
          .footer-bottom__social-links {
            order: 1;
  
          }
    
          .footer-bottom__text {
            width: 100%;
            align-items: center;
            justify-content: center;
            order: 3;
            padding: 8px 16px;
          }
        }
      }
    }
}
