.redesigned-wrapper {

  .live-schedule-block, .live-schedule-table {

    div {
      word-break: unset;
    }

    padding: 0px;
    background: $tetriarySurface;


    // FILTERS SECTIONS
    .filter-panel {
      margin: 0 auto;
      max-width: $xl;

      .live-schedule-filter-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 16px;

        padding: 0 16px;

        @media (max-width: $lg) {
          padding: 0;
        }

        .search-wrapper {
          width: 100%;
          padding: 48px 16px 16px 0;

          @media (max-width: $lg) {
            padding: 0;
          }

          #search_tagsinput {
            padding: 0px;
          }

          #search_tag {
            margin: 0;

            padding: 16px 40px 16px 12px;

            background: $neutralSurface;
            border: 1px solid $neutralLight;
            border-radius: 4px;
            display: flex;
            align-items: center;

            @include fontText1Md();

            color: $neutralText;

            &:focus {
              border: 2px solid $primary;
              padding: 15px 40px 15px 11px;

            }

            &::placeholder {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              opacity: 0.5;
            }

          }


          form {
            margin: 0;

            .form-icon {
              position: absolute;
              top: 50%;
              left: unset;
              right: 12px;
              display: flex;
              
              padding: 0px;
  
              width: 24px;
              height: 24px;
  
              border: none;
  
              svg {
                path {
                  fill: $neutralLight;
                }
              }
            }        
          }

        }

        @media (max-width: $lg) {
          flex-direction: column;
          align-items: flex-start;
          padding: 24px 16px 16px;
          gap: 8px;
        }

        .filter-panel-bg {
          max-width: max-content;
        }

      }
    }

    // Filter section
    .filter-demand-list__wrapper {
      display: flex;

      padding: 48px 0 16px;
      justify-content: space-between;
      flex-wrap: nowrap;
      gap: 16px;

      @media (max-width: $lg) {
        padding: 0;
        align-self: stretch;
      }

      @media (max-width: $sm) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      }
    }

    .filter-demand-item {
      display: flex;

      padding: 12px 16px;
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      text-align: center;
      color: $neutralText;
      min-width: fit-content;
      width: fit-content;

      @media (max-width: $lg) {
        font-size: 22px;
        padding: 12px 0;
      }

      @media (max-width: $sm) {

        // Level dropdown
        &:nth-child(2) {
          width: fit-content;
          justify-content: flex-end;
        }

        // Teacher dropdown
        &:nth-child(3) {
          width: 100%;
          justify-content: center;
        }
      }

      &:last-child {
        .dropdown-menu {
          right: -16px;
        }
      }

      .dropdown-toggle {
        svg {
          &:last-child {
            transform: rotate(0deg);
            transition: all 0.5s;
          }
        }

        span {
          color: $neutralText;
          @include fontSubtitle2Md();

          @media (max-width: $lg) {
            @include fontSubtitle2Sm();
          }
        }
      }
    }

    .dropdown-menu.show {
      .dropdown-toggle {
        svg {
          &:last-child {
            transform: rotate(180deg);
          }
        }

        span {
          color: $primary;
        }

        svg {
          path {
            fill: $primary;
          }
        }
      }
    }

    .dropdown-toggle {
      display: flex;
      transition: all 300ms;
      align-items: center;

      &:hover {
        svg {
          &:first-child {
            path {
              fill: $primary;
            }
          }
        }
      }

      > span {
        padding: 0 8px;
        display: inline-block;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        color: $neutralText;
        font-family: $poppins;
      }
    }

    .dropdown-menu {
      background: $neutralSurface;
      border-radius: 16px;
      overflow-y: auto;
      box-shadow: 0px 2px 2px rgba(57, 69, 49, 0.15),
        0px 4px 12px rgba(0, 0, 0, 0.2);
      
      max-width: calc(100vw - 28px);

      .dropdown-item {
        padding: 18px 24px;
        color: $neutralText;
        cursor: pointer;

        @include fontSubtitle2Md();

        @media (max-width: $lg) {
          @include fontSubtitle2Sm();
        }

        &:hover,
        &:focus {
          background: $secondarySurface;
        }

        &::before {
          content: none;
        }
      }

      .dropdown-item.active {
        color: $primary;
      }
    }

    .filter-result {
      margin-bottom: 0;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      letter-spacing: 1.5px;
      text-transform: uppercase;
      color: $neutralMiddle;
      font-family: $poppins;
    }

    #search_tagsinput {
      border: none;

      @include fontSubtitle2Md();
    }


    // tags section
    .additional-tags-box {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      flex-wrap: wrap;
      padding: 24px 32px;
      gap: 16px;
      
      width: 100%;

      @media (max-width: $lg) {
        padding: 24px 16px;
      }

      .tag {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 0px 8px;

        background: $secondary;
        border-radius: 32px;

        
        word-break: unset;
        white-space: nowrap;
        
        span {
          padding: 8px 4px;

          color: $neutralText;
          @include fontOverlineMd();

          @media (max-width: $lg) {
            @include fontOverlineSm();

          }
        }

        img {
          width: 16px;
          height: 16px;
        }
      
        &:hover {
          cursor: pointer;
          background: $secondaryLight;
          transition: all 300ms;
        }

      }

      .btn-negative-transparent {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 8px 18px;
        gap: 10px;

        color: $negative;
        background: transparent;
        border: 1px solid $negative;
        border-radius: 8px;
        
        text-transform: none;
        transition: all 300ms;
        
        @include fontButtonMd();

        &:hover {
          background: $negativeSurface;
        }
  
        &:focus {
          background: transparent;
          border: 1px solid $negativeAccented;
          color: $negativeAccented;
        }

        @media (max-width: $md) {
          justify-content: center;
        }
      }
    }

    // calendar section (date wrapper)
    .filter-date-wrapper {
      margin: 0 auto;

      .container-fluid {
        
        padding: 0;

        // "days of week" sections
        .row.filter-wrapper {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: flex-start;
          
          padding: 16px 32px;

          border-width: 1px 0px;
          border-style: solid;
          border-color: $tetriaryLight;

          @media (max-width: $lg) {
            padding: 16px;
          }

          .filter-item {
            padding: 0;

            .filter-content {
              display: flex;
              align-items: center;
              
              width: 56px;
              height: 56px;

              border: 1px solid $neutralText;
              
              
              & h5 {
                color: $neutralText;
                @include fontHeader5Md();

                @media (max-width: $md) {
                  @include fontHeader5Sm();
                }
              }
              
              &:hover {
                transition: all 0.3s;
                background: rgba(185, 197, 178, 0.25);
              }

              &:active {
                border: 2px solid #394531;
              }

              @media (max-width: $md) {
                width: 40px;
                height: 40px;
              }

            }

            .filter-description {
              padding-top: 8px;
              text-align: center;
              
              p {
                color: $neutralText;
                @include fontOverlineMd();
               
              }
            }

            &.active {
              .filter-content {
                border: 1px solid $secondary;
                background: $secondary;
              }


            }
          }

        }

        // buttons "next day", "prev day", "show week"
        .row.filter-control {

          padding: 10px 32px;

          & > span {
            padding: 12px 16px;

            text-align: center;
            color: $neutralText;
            text-transform: unset;

            @include fontButtonMd();
          }

          @media (max-width: $lg) {
            padding: 10px 16px;
          }

          @media (max-width: $md) {
            padding: 0;
          }
        }
      }

    }


    // LIVE SCHEDULE SECTION

    .live-schedule-wrapper {
      max-width: $xl;
      margin: 0 auto;

      // to prevent bug with words break
      span, a {
        word-break: initial;
      }

      .container-fluid {
        padding: 32px 32px 64px;

        @media (max-width: $lg) {
          padding: 32px 16px 64px;
        }
      }


      #live-content-wrap {
        padding: 0;
        overflow: visible;

        .live-content-item__level {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          padding: 4px 12px;
          margin: 16px 0;

          width: fit-content;

          background: $secondary;
          border-radius: 16px;
          
          color: $neutralText;
          @include fontOverlineMd();
        }

        // table row
        .live-content-item {

          display: flex;
          flex-direction: row;
          align-items: flex-start;

          padding: 0 0 24px 0;
          gap: 8px;

          & :nth-child(1) { 
            order: 2;
          }
          & :nth-child(2) { 
            order: 3;
          }
          & :nth-child(3) { 
            order: 1;
          }
          & :nth-child(4) { 
            order: 4;
          }
          & :nth-child(5) { 
            order: 5;
          }

          .live-content-item__info {

            @media (max-width: $md) {
              width: 65%;
              justify-content: flex-end;
            }
            
            .live-content-item__description {

              max-width: min-content;
             
              margin-left: 0;

              @media (max-width: $lg) {
                max-width: 200px;
              }

              @media (max-width: $md) {
                max-width: 100%;
              }

              h5 {
                @include fontSubtitle2Md();
                color: $neutralText;

                @media (max-width: $lg) {
                  @include fontSubtitle2Sm();
                }
              }

              span.substitute {
                display: flex;

                margin-left: 28px; 
                @include fontText1Md();
                color: $neutralMiddle;

                white-space: nowrap;
                overflow: hidden;

                text-decoration: line-through;
                text-decoration-color: $negative;
                text-decoration-thickness: 2px;

                @media (max-width: $lg) {
                  @include fontText1Sm();
                }
                
              }

              div.context-1.live-content-item__description-row {
                display: flex;
                padding: 0;
                margin: 0;

                // avatar image
                .live-content__avatar {
                  border-radius: 4px;
                  width: 20px;
                  height: 20px;
                  min-width: fit-content;
                  min-height: unset;
                  order: 1;
                  margin: auto 0;
                }
  
                // teacher name - Melanie Salvatore-August
                .live-content-item__teacher-name {
                  margin: 0 16px 0 8px;
                  max-width: 180px;

                  @include fontText1Md();
                  color: $neutralMiddle;

                  white-space: nowrap;
                  overflow: hidden;

                  order: 2;

                  @media (max-width: $lg) {
                    @include fontText1Sm();
                  }
                  
                  @media (max-width: $md) {
                    width: 50%;
                  }

                }

                // 60 min - lesson time length
                div.context-1.live-content-item__lesson-length {
                  display: flex;
                  align-items: center;
                  width: max-content;

                  padding: 0;
                  margin: 0;
                  order: 3;

                  // img {
                  //   width: 20px;
                  //   height: 20px;
                  //   margin-right: 8px;
                  //   margin-top: 2px;
                  // }

                  span {
                    white-space: nowrap;
                    color: $neutralMiddle;                 

                    @include fontText1Md();
  
                    @media (max-width: $lg) {
                      @include fontText1Sm();
                    }
  
                  }
                }
              }
            }
          }


          .live-content-item__content {
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: column;
            position: unset;

            @media (max-width: $lg) {
              width: calc(40% - 80px);
            }

            @media (max-width: $md) {
              width: 100%;
            }

            p.description-paragraph span {

              color: $neutralText;

              @include fontText1Md();

              @media (max-width: $lg) {
                @include fontText1Sm();
              }
            }

            &.opened-description {
              span.inner {
                span:nth-child(1) {
                  display: none;
                }
               
                span:nth-child(2) {
                  display: block;
                }
              }
            }

            &:not(.opened-description){
              span.inner {
                span:nth-child(1) {
                  display: block;
                }
               
                span:nth-child(2) {
                  display: none;
                }
              }
            }
          

            button.view-more {
              position: unset;
              background: transparent;
              right: 0;
              left: unset;
              transform: unset;

              display: flex;
              justify-content: flex-end;
              padding: 0;

              span {
                margin: 0;
                width: unset;
                height: unset;
                background: transparent;

                @include fontButtonMd();
                color: $neutralText;
              }

            }


          }


          .live-content-item__schedule {
            cursor: auto; 
            padding: 0;
            width: 100px;
            
            .live-content-item__schedule-time {
              margin: 0 0 8px;
              order: 2;

              @include fontSubtitle2Md();
              color: $neutralText;

              @media (max-width: $lg) {
                @include fontSubtitle2Sm();
              }

              span {
                @include fontSubtitle2Md();
                color: $neutralText;
  
                @media (max-width: $lg) {
                  @include fontSubtitle2Sm();
                } 
              }
            }

            // labels "ends in, today, tomorrow", etc
            .context-3 {
              order: 3;

              @include fontText1Md();
              color: $neutralMiddle;
              text-transform: none;

              @media (max-width: $lg) {
                @include fontText1Sm();
              }
            }

          }


          // last column with buttons Join, Add to calendar etc
          .live-content-item__description-watch {
            animation: none;
            transform: none;
            background: transparent;
            
            width: 176px;
            height: auto;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            padding: 0;
            margin-top: 6px;
            
            border: 1px solid $neutralSurface;
            border-radius: 8px;

            @media (max-width: $lg) {
              width: 160px;
            }

            @media (max-width: $md) {
              width: 100%;
            }

            button.atcb-link {
              padding: 0;
            }

            &:not(.coming-soon) {
              a {
                background: $neutralSurface;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;

                padding: 16px 0;

                @include fontButtonMd();
                color: $neutralText;
                text-transform: none;
                transition: all 0.3s;

                span.button-circle {
                  width: 16px;
                  height: 16px;
                  margin: 0px 8px;
                  background: radial-gradient(50% 50% at 50% 50%, #FF7A00 42.19%, rgba(255, 122, 0, 0) 100%);
                }

                span.button-title {
                  display: flex;
                  align-items: center;
                  padding-right: 16px;

                  @include fontButtonMd()
                }

                &:hover {
                  background: rgba(255, 255, 255, 0.6);
                }
                
                &:active {
                  background: rgba(255, 255, 255, 0.75);
                  transition: none;
                }


              }
            }

            &.coming-soon {
              gap: 10px;
              
              border: none;
              background: transparent;

              overflow: visible;

              transition: all 0.3s;
              
              & > a:first-child {
                display: none;
              }
              
              .live-content-item__schedule {
                margin: 0;
                padding: 0;
                width: 100%;
                
                .addtocalendar {
                  width: 100%;
                  
                  button.atcb-link {
                    display: flex;
                    justify-content: center;
                    width: 100%;

                    border: 1px solid $neutralText;

                    padding: 15px 0;

                    @include fontButtonMd();
                    color: $neutralText;
                    text-transform: none;

                    &:hover {
                      background: rgba(185, 197, 178, 0.25);
                    }  

                    &:active {
                      background: transparent;        
                      border: 2px solid $neutralText;

                      padding: 14px 0;

                      transition: none;
                    }
                  }

                  ul.atcb-list {
                    right: 0;
                    left: unset;
                    width: 328px; 
                    padding: 0;
                    max-width: fit-content;
                    
                    .atcb-item {
                      padding: 16px 24px;
                      margin: 0;
                      white-space: nowrap;
                      
                      color: $neutralText;

                      a {
                        text-transform: unset;
                        @include fontSubtitle2Md();
                      }

                      .atcb-checkmark {
                        display: none;
                      }

                      & .atcb-checkmark + .atcb-item-link {
                        color: $primary;
                      }
                    }
                  }
    
                  &.added button.atcb-link {
                    border: 1px solid $positiveAccented;
                    color: $positiveAccented;

                    div.atcb-checkmark {
                      display: none;
                    }

                    &:active {
                      background: transparent;        
                      border: 2px solid $positiveAccented;

                      padding: 14px 0;

                      transition: none;
                    }
                  }
                }
              }
           }
          }
        }
      }
    }

  }
}
