.notification-comp {
  position: relative;

  .wrapper-ml {
    @include media-breakpoint-down(sm) {
      margin: 0;
    }
  }

  .container-fluid {
    position: relative;
  }

  .form-wrapper {
    padding: 30px 0;

    @include media-breakpoint-down(md) {
      padding: 30px 0 0;
    }

    & ~ .form-wrapper {
      border-top: 1px solid $border-grey-2;
    }

    &:not(:last-child) {
      .form-group:last-child {
        padding-bottom: 0;
        
        .form-group-wrap {
          border: 0;
        }
      }
    }
  }

  .title-wrapper {
    padding-top: 30px;
    color: $color-black-2;

    @include media-breakpoint-down(sm) {
      padding: 0;
    }

    h5 {
      padding-top: 30px;
      margin: 0;
      color: inherit;

      @include media-breakpoint-down(md) {
        padding-top: 0;
        font-size: 24px;
      }
      
      @include media-breakpoint-down(sm) {
        padding-top: 0;
        font-size: 18px;
      }
    }
  }

  .form-group {
    padding: 30px 0;
    margin-bottom: 0;

    @include media-breakpoint-down(md) {
      padding: 0;
    }

    &-wrap {
      padding: 30px 0;
      border-bottom: 1px solid $border-grey-2;

      @include media-breakpoint-up(lg) {
        display: flex;
        align-items: center;
      }

      @include media-breakpoint-down(sm) {
        padding: 20px 0;
      }
    }

    .form-control {
      padding: 0;
      margin: 0;
      border: 0;
      color: $color-gray-5;

      @include media-breakpoint-down(sm) {
        font-size: 18px;
      }
    }

    .btn-inline {
      margin-left: 10px;
      white-space: nowrap;
      transform: unset;

      @include media-breakpoint-up(lg) {
        position: relative;
      }

      @include media-breakpoint-down(md) {
        top: auto;
        bottom: 5px;
        right: 0;
      }

      @include media-breakpoint-down(sm) {
        font-size: 10px;
      }
    }
  }
}