.redesigned-wrapper {
  .journey {

    padding: 0px;
    background: $tetriarySurface;

    .wrapper-ml {
      max-width: $xl;
      margin: 0 auto;
    }

    // Banner section 
    .journey-series__banner {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-grow: 1;

      width: 100%;
      height: 758px;
      max-width: $xxl;
      margin: 0 auto 56px;

      background-image: url('../images/banner-bg-image2.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;

      @media (max-width: $md) {
        height: 640px;
      }

      @media (max-width: $lg) {
        margin: 0 auto 40px;
      }

      .banner-wrapper {        
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        max-width: $xl;
        margin: 0 auto;
        padding: 72px 32px 0px;

        .banner-title {
          @include fontHeader1Md();
          color: $neutralSurface;
          /* YW/text shadow */
          text-shadow: 0px 0px 64px rgba(57, 69, 49, 0.5);
          text-align: center;

          @media (max-width: $lg) {
            @include fontHeader1Sm();
          }
        }

        .banner-subtitle {
          @include fontHeader5Md();
          margin-top: 16px;
          color: $neutralSurface;
          /* YW/text shadow */
          text-shadow: 0px 0px 64px rgba(57, 69, 49, 0.5);
          text-align: center;

          @media (max-width: $lg) {
            @include fontHeader5Sm();
          }
        }

        .banner-button {
          margin-top: 48px;
          padding: 16px 48px;
        }
      }
    }

    .card-bubble-item {
      padding: 8px;

      img {
        border-radius: 8px;
      }

      &:hover {
        img {
          box-shadow: $hovered;
          transition: all 0.3s;
        }
      }

    }

    .container-fluid {
      padding: 0px 8px 56px;

      @media (max-width: $lg) {
        padding: 0px 8px 32px;

      }

      // header section
      .row.journey-series__header {
        padding: 0 8px;

        // journey series title
        & > div.col-12:first-child {
          padding: 64px 0 16px;

          @media (max-width: $lg) {
            padding: 48px 0 8px;
          }
        }

        // both headers
        & > div.col-12 {
          padding: 0 0 8px;

          @media (max-width: $lg) {
            padding: 0 0 4px;
          }

          .wrapper-heading {
            padding: 0px;

            h6 {
              color: $neutralMiddle;
              @include fontHeader6Md();

              @media (max-width: $lg) {
                @include fontHeader6Sm();
              }
            }
          
            h2 {
              color: $neutralText;
              @include fontHeader2Md();

              @media (max-width: $lg) {
                @include fontHeader2Sm();
              }
            }
          }
        }
      }


      // cards section
      .row.journey-series__cards > div.col-md-6 {
        padding-left: 0px;
        padding-right: 0px;
      }
    }

  }
}
