.journey {
  position: relative;
  padding-top: 20px;

  .wrapper-heading {
    margin-top: 7%;
  }

  .wrapper-ml {
    margin-left: 0;
  }

  &-title {
    &:hover {
      color: $color-black-2;
    }
  }

  &-content {
    display: flex;
    margin-top: 50px;

    @include media-breakpoint-down(md) {
      display: block;
    }

    @include media-breakpoint-down(sm) {
      margin-top: 30px;
    }
  }

  &-heading {
    width: 50%;
    padding-left: 25px;
    padding-right: 40px;

    @include media-breakpoint-down(md) {
      width: 100%;
      padding: 0 25px;
    }

    @include media-breakpoint-down(sm) {
      padding: 0 15px;
    }

    h2 {
      font-size: 48px;
      font-family: $font-header-regular;
      color: $color-black-1;

      @include media-breakpoint-down(sm) {
        font-size: 24px;
      }
    }

    h5 {
      color: $color-gray-1;
    }

    p {
      color: $color-black-1;
    }
  }

  &-description {
    width: 50%;
    padding-left: 40px;
    padding-right: 25px;
    color: $color-black-1;

    @include media-breakpoint-down(md) {
      width: 100%;
      padding: 0 25px;
    }

    @include media-breakpoint-down(sm) {
      padding: 0 15px;
    }

    p {
      margin-top: 45px;
      margin-bottom: 0;
      font-size: 18px;
      color: inherit;

      @include media-breakpoint-down(sm) {
        font-size: 14px;
        margin-top: 20px;
      }

      &:first-child {
        margin-top: 0;
        font-size: 38px;
        font-family: $font-header-regular;
        line-height: 44px;

        @include media-breakpoint-down(sm) {
          font-size: 24px;
          line-height: 26px;
        }
      }
    }
  }
}

.js-item {
  &-backdrop {
    position: relative;
    height: 0;
    width: 100%;
    padding-top: 350px;
    overflow: hidden;
    z-index: 0;
    @include media-breakpoint-down(sm) {
      padding-top: 40%;
    }

    img {
      position: absolute;
      bottom: 0;
      height: auto;
      width: 100%;
      margin: auto;
    }
  }

  &-subtitle {
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  &-leg-desc {
    margin-bottom: 1em;
    text-align: center;
  }

  &-day-number {
    background: transparent url(../images/icon-check-0.png) 91% 50% no-repeat;
    border: 1px solid $border-grey-3;
    color: $color-black-1;
    cursor: pointer;
    display: inline-block;
    font-size: 0.75rem;
    min-height: 18px;
    padding: 4px 35px 4px 10px;
    margin: 0 auto 20px;
    position: relative;
    text-align: center;
    border-radius: 3px;
    text-transform: uppercase;

    &:hover {
      border-color: $border-grey-4;
    }

    &.watched {
      background-image: url(../images/icon-check-1.png);
      border-color: $border-green-1;

      &:hover {
        border-color: $border-green-1;
      }
    }
  }
}
