.comment {
  margin-top: 70px;
  margin-bottom: 30px;

  @include media-breakpoint-down(sm) {
    margin-top: 30px;
  }

  .form-control {
    padding-right: 15px;
    border: 0;
    width: calc(100% - 35px);

    &::placeholder,
    &::-webkit-input-placeholder {
      color: $color-pink-dark;
    }
  }

  .btn-inline {
    font-size: 10px;
    opacity: 0;
    visibility: hidden;
    transition: all ease-in-out 0.3s;

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }

  &-input {
    border-bottom: 1px solid $border-grey-2;
  }

  &-post {
    padding-left: 25px;
    padding-right: 25px;
    color: $color-black-1;

    @include media-breakpoint-down(lg) {
      padding-right: 10px;
    }

    @include media-breakpoint-down(sm) {
      padding-left: 0;
      padding-right: 0;
      font-size: 12px;
    }

    h5 {
      margin-bottom: 0;
      font-size: 38px;
      color: inherit;

      @include media-breakpoint-down(lg) {
        font-size: 30px;
      }

      @include media-breakpoint-down(sm) {
        font-size: 24px;
      }
    }

    p:first-child {
      margin-top: 6px;
    }

    p {
      margin-top: 35px;
      margin-bottom: 0;
      font-size: 18px;

      @include media-breakpoint-down(sm) {
        margin-top: 25px;
        font-size: 14px;
      }
    }

    a {
      color: $color-pink-dark;
    }
  }

  &-block {
    padding-left: 25px;
    padding-right: 25px;

    @include media-breakpoint-down(lg) {
      padding-left: 10px;
    }

    @include media-breakpoint-down(sm) {
      margin-top: 30px;
      padding-left: 0;
      padding-right: 0;
    }

    &-detail {
      & >.comment-item:first-child {
        > .comment-item-wrapper {
          border: 0;
        }
      }
    }
  }

  &-item {
    display: none;
    position: relative;
    padding-right: 80px;

    @include media-breakpoint-down(lg) {
      padding-right: 40px;
    }

    @include media-breakpoint-down(sm) {
      padding-right: 0;
    }

    p {
      margin-bottom: 0;
      color: inherit;
      font-size: inherit;
      font-family: inherit;
    }

    &-reply {
      padding-left: 60px;
      padding-right: 0;

      @include media-breakpoint-down(lg) {
        padding-left: 40px;
      }

      @include media-breakpoint-down(sm) {
        top: auto;
        padding-left: 30px;
      }
    }

    &-wrapper {
      padding: 30px 0;
      border-top: 1px solid $border-grey-2;
      word-break: break-word;

      @include media-breakpoint-down(sm) {
        position: relative;
        padding: 15px 0;
      }
    }

    &-content {
      margin-bottom: 20px;
      font-size: 18px;

      @include media-breakpoint-down(sm) {
        margin-bottom: 5px;
        font-size: 14px;
      }
    }

    &-name {
      font-size: 14px;

      @include media-breakpoint-down(sm) {
        font-size: 13px;
      }
    }

    &-date {
      font-size: 14px;
      color: $color-gray-2;

      @include media-breakpoint-down(sm) {
        font-size: 13px;
      }
    }
  }

  &-total {
    margin-top: 30px;
    text-align: right;
    text-transform: uppercase;
    font-size: 18px;
    font-family: $font-context-regular;
    color: $color-gray;

    @include media-breakpoint-down(sm) {
      margin-top: 15px;
      font-size: 14px;
    }

    a {
      color: inherit;
      font-size: inherit;
      font-family: inherit;
    }
  }

  &-required-logged-in {
    font-size: 28px;
    font-family: $font-context-regular;

    @include media-breakpoint-down(md) {
      font-size: 24px;
    }

    @include media-breakpoint-down(sm) {
      font-size: 20px;
    }

    a {
      color: $color-pink-dark;
    }
  }

  .btn-reply {
    position: absolute;
    top: 30px;
    right: 0;
    color: $color-pink-dark;
    font-size: 10px;
    font-family: $font-context-regular;
    text-transform: uppercase;

    @include media-breakpoint-down(sm) {
      top: auto;
      bottom: 15px;
    }
  }
}
