.link-carousel {
  position: relative;
  padding-top: 20px;

  .yoga-at-home:not(.logged-in) & {
    @include media-breakpoint-up(lg) {
      padding-top: 50px;
    }
  }

  .container-fluid {
    @include media-breakpoint-up(lg) {
      width: 85%;
    }
  }

  .wrapper-heading {
    margin-top: 7%;
  }

  &-bg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
  }

  &-item {
    display: block;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
    overflow: hidden;

    &__img {
      overflow: hidden;
      border-radius: 15px;
      background: linear-gradient(0deg, #FCF3E3, #FCF3E3);
    }

    img {
      width: 100%;
      height: 100%;
    }

    &__content {
      position: absolute;
      width: auto;
      top: 60%;
      left: 30px;
    }

    h4 {
      margin-bottom: 0;
      color: $color-white;
    }
  }

  &-bubble {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 1;
  }

  .bubble-content {
    p {
      margin-bottom: 0;
      color: $color-black-2;
    }
  }
}

@include media-breakpoint-down(md) {
  .link-carousel {
    &-item {
      &__content {
        top: 75%;
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .link-carousel {
    &-item {
      &__content {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        h4 {
          font-size: 40px;
        }
      }
    }

    &__slider {
      overflow: hidden;
    }
  }

  .bubble-circle {
    width: 34.33px;
    height: 34.33px;
  }
}
