.testimonial {
  &-wrapper {
    margin: 0 84px;

    p {
      font-family: $font-header-regular;
      font-weight: 300;
    }

    .slick-list {
      padding:0 20% 0 0 !important;
    }

    .col-6 {
      padding: 0;

      &:nth-child(2n) {
        padding-left: 26px;
      }

      &:nth-child(2n+1) {
        padding-right: 26px;
      }
    }
  }

  &-item {
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  }

  &-copy {
    font-size: 38px;
    line-height: 116%;
    color: $color-gray-4;
    padding: 24px 65px 60px 3px;
  }

  &-info {
    h5 {
      color: $color-black-1;
    }

    &__name {
      font-size: 18px;
      line-height: 110%;
      color: $color-black-1;
    }

    .context-2  {
      font-family: $font-context-regular;
      font-weight: normal;
      color: $color-orange;
      margin-bottom: 20px;
    }
  }
}

@include media-breakpoint-down(md) {
  .testimonial {
    &-wrapper {
      margin: 0 54px;
    }

    &-copy {
      font-size: 30px;
    }
  }
}

@include media-breakpoint-down(sm) {
  .testimonial {
    &-wrapper {
      margin: 0 32px;
    }

    &-copy {
      padding: 0 0 60px 0;
      font-size: 21px;
    }

    &-info {
      h5 {
        font-size: 18px;
      }

      &__name {
        font-size: 13px;
        margin-bottom: 10px;
      }

      .context-2 {
        font-size: 13px;
      }
    }
  }
}
